/**
 * @typedef {Object} MouseEvent
 * @property {boolean} altKey - indicates if the alt key was pressed
 * @property {number} deltaY - vertical scroll amount
 * @property {number} x - horizontal coordinate of event
 * @property {number} y - vertical coordinate of event
 */

/**
 * @typedef {object} KeyEvent
 * @property {boolean} altKey - indicates if the alt key was pressed
 * @property {string} key - returns the value of a key pressed by the user
 */

import PropTypes from 'prop-types';

import React, {Component} from 'react';
import {observer} from 'mobx-react';

import SpriteFigure from '../Figure/SpriteFigure';

import classes from './Magnifier.scss';

import {
  MAGNIFIER_ARROW_WIDTH,
  MAGNIFIER_DEFAULT_WIDTH,
  MAGNIFIER_DEFAULT_SCALE,
  MAGNIFIER_DEFAULT_ZOOM,
  SIDEBAR_SECTIONS_DISTANCE,
  MOSAIC_CORRECTION_WIDTH,
  MOSAIC_CORRECTION_HEIGHT,
  FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP,
} from '@epo/web-common';

import {getCrop} from 'Figures.Helper';

/**
 * Component that create magnifier and magnifier glass
 */
@observer
class Magnifier extends Component {
  constructor (props) {
    super(props);
    this.state = {
      // initial zoom (can be updates on alt and mouse wheel)
      scrollZoom: 1,
      // indicates that image was not loaded before
      index: -1,
      // if magnifier is visible
      visible: false,
      width: props.width,
      height: props.height,
      // arrow (styling) that shows which image zoomed
      arrow: {
        // position of magnifier in relation selected figure
        type: 'top',
        left: 0,
        top: 0,
      },
      // element shows which part of figure will be zoomed
      glass: {
        left: 0,
        top: 0,
        width: 0,
        height: 0,
        // required if magnifier not on the right size
        marginLeft: 0,
      },
      // magnifier size
      root: {
        top: 0,
        left: 0,
      },
      zoomed: {
        left: 0,
        top: 0,
      },
    };
    this.magnifierScale = MAGNIFIER_DEFAULT_SCALE;
  }

  /**
   * Method shows magnifier
   * @public
   */
  hide = () => {
    // only if it was mounted
    if (this.refs.root !== undefined) {
      this._createDefaultGlass();
      this.refs.root.style.display = 'none';
    }
  };

  /**
   * Method hides magnifier
   * @public
   */
  show = () => {
    if (this.refs.root !== undefined) {
      this.refs.root.style.display = 'block';
    }
  };

  /**
   * Method created default magnifier glass
   * @private
   */
  _createDefaultGlass = () => {
    let {glass} = this.state;
    glass.left = 0;
    glass.top = 0;
    glass.height = 0;
    glass.width = 0;
    this.setState({
      glass,
    });
  };

  /**
   * Method calculates magnifier size and position (only root part)
   * @param {MouseEvent} evt - mouse event
   * @param {number} index - figure index in mosaic mode
   * @param figureWidth - maximum figure element width
   * @param figureHeight - maximum figure element height
   * @private
   */
  _calculateMagnifierPosition = (evt, index, figureWidth, figureHeight) => {
    this.magnifierScale = MAGNIFIER_DEFAULT_WIDTH / figureWidth;

    // Check if there is enough space in container to insert magnifier
    // it needs when we have 2 columns in a row
    if ((figureWidth + MAGNIFIER_ARROW_WIDTH * 2) >= this.props.container.width) {
      this.magnifierScale = (figureWidth - MAGNIFIER_ARROW_WIDTH) / ((this.props.container.width));
    } else if ((figureWidth + MAGNIFIER_ARROW_WIDTH * 2) > (this.props.container.width / 2)) {
      this.magnifierScale = (figureWidth - MAGNIFIER_ARROW_WIDTH) / ((this.props.container.width / 2));
    }

    this.setState({
      index,
      width: figureWidth,
      height: figureHeight,
    }, () => {
      let mv = this.refs.root.parentNode.parentNode;
      let section = mv.querySelector('section[data-id="' + this.state.index + '"]');
      if (!section) return;
      let sectionBCR = mv.getBoundingClientRect();
      let mvBCR = section.getBoundingClientRect();
      let top = mvBCR.top - sectionBCR.top + mv.scrollTop;
      let left = mvBCR.left - sectionBCR.left;
      let offsetLeft = left + figureWidth;
      let {arrow, zoomed} = this.state;

      arrow.left = 0;
      arrow.top = figureHeight / 2 - MAGNIFIER_ARROW_WIDTH;
      zoomed.top = 0;
      if ((this.props.container.width + 30) < (offsetLeft + this.props.store.mode * 4 + figureWidth * this.magnifierScale)) {
        if (left < figureWidth * this.magnifierScale) {
          if (top - mv.scrollTop > figureHeight + MAGNIFIER_ARROW_WIDTH) {
            arrow.type = 'bottom';
            arrow.left = -(figureWidth / 2 + MAGNIFIER_ARROW_WIDTH);
            arrow.top = -MAGNIFIER_ARROW_WIDTH;
            zoomed.left = -((figureWidth - MAGNIFIER_ARROW_WIDTH * 3 - SIDEBAR_SECTIONS_DISTANCE) * this.magnifierScale);
            zoomed.top = -(figureHeight * this.magnifierScale + MAGNIFIER_ARROW_WIDTH * 2);
            offsetLeft += MAGNIFIER_ARROW_WIDTH;
          } else {
            arrow.type = 'top';
            arrow.left = -(figureWidth / 2 + MAGNIFIER_ARROW_WIDTH);
            arrow.top = figureHeight - MAGNIFIER_ARROW_WIDTH + 4;
            zoomed.left = -((figureWidth - SIDEBAR_SECTIONS_DISTANCE));
            zoomed.top = figureHeight + SIDEBAR_SECTIONS_DISTANCE;
            offsetLeft += MAGNIFIER_ARROW_WIDTH;
          }
        } else {
          arrow.left = -(figureWidth + MAGNIFIER_ARROW_WIDTH * 2);
          arrow.type = 'right';
          zoomed.left = -(figureWidth * this.magnifierScale + figureWidth + MAGNIFIER_ARROW_WIDTH * 2 + SIDEBAR_SECTIONS_DISTANCE);
          zoomed.top = this.getBottomOverFlow(sectionBCR, top - mv.scrollTop);
          offsetLeft += MAGNIFIER_ARROW_WIDTH;
        }
      } else {
        arrow.type = 'left';
        arrow.left = -MAGNIFIER_ARROW_WIDTH;
        zoomed.left = 0;
        zoomed.top = this.getBottomOverFlow(sectionBCR, top - mv.scrollTop);
        offsetLeft += MAGNIFIER_ARROW_WIDTH;
      }
      this.setState({
        zoomed,
        root: {
          top,
          left: offsetLeft,
        },
        arrow,
      });
    });
  };

  /**
   * Method returns if passed element is not in the visible part and return how far it is
   * @param {DOMRect} selection - selected element DOMRect
   * @param {number} top - scroll from top
   * @return {number} - 0 if full element in visible part
   */
  getBottomOverFlow = (selection, top) => {
    const bottom = selection.height - (top + (this.state.height - MOSAIC_CORRECTION_HEIGHT) * this.magnifierScale) - 30;
    if (bottom < 0) {
      return bottom;
    }
    return 0;
  };

  /**
   * Method calculate magnifier glass size and and position
   * @param {MouseEvent} evt - mouse event
   * @param figureWidth - maximum figure element width
   * @private
   */
  _calculateGlass = (evt, figureWidth) => {
    let {x, y} = evt;
    let {width, height} = this.state;
    let glass = {};

    glass.width = (width - MOSAIC_CORRECTION_WIDTH) / (MAGNIFIER_DEFAULT_ZOOM * this.state.scrollZoom);
    glass.height = (height - MOSAIC_CORRECTION_HEIGHT) / (MAGNIFIER_DEFAULT_ZOOM * this.state.scrollZoom);
    glass.left = x - glass.width / 2;
    glass.top = y - glass.height / 2;
    glass.marginLeft = -(figureWidth + MAGNIFIER_ARROW_WIDTH);

    // check if figure is mounted
    if (this.refs.figure) {
      //update figure position
      this.refs.figure.updateBackgroundTranslate(
        (-x + (width - MOSAIC_CORRECTION_WIDTH) / 2) * (MAGNIFIER_DEFAULT_ZOOM * this.state.scrollZoom) * this.magnifierScale,
        (-y + (height - MOSAIC_CORRECTION_HEIGHT) / 2) * (MAGNIFIER_DEFAULT_ZOOM * this.state.scrollZoom) * this.magnifierScale,
      );
    }
    this.setState({
      glass,
    });
  };

  /**
   *
   * @param {MouseEvent} evt - mouse event
   * @param index
   * @param figureWidth
   * @param figureHeight
   * @public
   */
  mouseMove = (evt, index, figureWidth, figureHeight) => {
    // recalculate position of magnifier
    if (this.state.index !== index) {
      this._calculateMagnifierPosition(evt, index, figureWidth, figureHeight);
    }
    // Apply glass if index is not empty
    if (index !== -1) {
      this._calculateGlass(evt, figureWidth);
    }
  };

  /**
   * Method handles mouse wheel scrolling
   * @param {MouseEvent} evt - mouse event
   * @private
   */
  _onWheelHandler = (evt) => {
    if (evt.shiftKey === true) {
      evt.preventDefault();
      let scrollZoom;
      if (evt.deltaY > 0) {
        scrollZoom = this.state.scrollZoom - 0.1;
      } else {
        scrollZoom = this.state.scrollZoom + 0.1;
      }
      // disable zoom less than 1
      if (scrollZoom < 1) scrollZoom = 1;
      this.setState({
        scrollZoom,
      });
    }
  };

  /**
   * Method handles pressing keyboard keys
   * @param {KeyEvent} evt - key down event
   * @private
   */
  onKeyPressHandler = (evt) => {
    if (evt.altKey === true && evt.key === '5') {
      this.setState({
        scrollZoom: 1,
      });
    }
  };

  componentDidMount () {
    // Apply listeners for mouse wheel and key down (will be used to track alt key)
    document.addEventListener('wheel', this._onWheelHandler);
    document.addEventListener('keydown', this.onKeyPressHandler);
  }

  componentWillUnmount () {
    // Remove listeners on after removing component
    document.removeEventListener('wheel', this._onWheelHandler);
    document.removeEventListener('keydown', this.onKeyPressHandler);
  }

  render () {
    let {markers, store: {publication: {figures}}, finIn, isBNS} = this.props;
    let {width, height, index, arrow, zoomed, glass, root} = this.state;
    let figure = figures[index];

    // if figure is missed remove component output
    if (figure === undefined) return null;

    let arrowClassName = classes[arrow.type];
    let glassStyles = {...glass};
    let rootStyles = {...root};
    let arrowStyles = {...arrow};
    width = (width - MOSAIC_CORRECTION_WIDTH) * this.magnifierScale;
    height = (height - MOSAIC_CORRECTION_HEIGHT) * this.magnifierScale;

    return (
      <div
        style={rootStyles}
      >
        <div
          ref='root'
          className={classes.root}
          style={rootStyles}
        >
          <div
            ref='glass'
            className={classes.glass}
            style={glassStyles}
          />
          <div
            ref='zoomed'
            style={{
              width,
              height,
              marginLeft: `${zoomed.left}px`,
              marginTop: `${zoomed.top}px`,
            }}
            className={classes.zoomed}
          >
            <div
              className={classes.zoomedContainer}
              style={{
                marginTop: `${zoomed.top}px`,
              }}
            >
              <SpriteFigure
                ref='figure'
                figure={figure}
                zoom={MAGNIFIER_DEFAULT_ZOOM * this.state.scrollZoom * FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP}
                width={width}
                height={height}
                markers={markers}
                isStatic={Boolean(true)}
                hideToolTip={Boolean(true)}
                crop={Boolean(getCrop(isBNS))}
                finIn={finIn}
                isSprite={Boolean(false)}
                index={0}
                angle={figure.mosaicAngle}
              />
            </div>
            <span
              className={`${classes.arrow} ${arrowClassName}`}
              ref='arrow'
              style={arrowStyles}
            />
          </div>
        </div>
      </div>
    );
  }
}

Magnifier.propTypes = {
  height: PropTypes.number,
  markers: PropTypes.array,
  store: PropTypes.object,
  container: PropTypes.shape({
    height: PropTypes.number,
    width: PropTypes.number,
  }),
  parent: PropTypes.object, // should be dom element
  isBNS: PropTypes.object,
  width: PropTypes.number,
  finIn: PropTypes.bool,
};

export default Magnifier;
