import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer} from 'mobx-react';
import {hotkeys} from '@epo/web-common';
import {onAnyPanel} from './FiguresShortcuts.Helper';
import BindAll from 'lodash-decorators/bindAll';

@observer
@BindAll()
class FiguresShortcuts extends Component {
  /**
   * Method sets previous figure as active (or open previous document if there no more figures)
   * @param {boolean} openNext - indicate should method opens previous document if there no more figures
   * @private
   */
  @onAnyPanel
  _openPreviousFigure (openNext = true) {
    let {store: {prevActiveFigure, modeWidth, full, mode}, openNextDocument, funnelSettings: {funnel, markers}, persistency} = this.props;
    let crossFamilyBoundary = persistency.get({type: 'FNB', module: 'familyBoundarySetting'});
    // let hasPrev = prevActiveFigure(jumpMarker, funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : modeWidth, true); // Disabled in favour of P0693-1201
    let hasPrev = prevActiveFigure('*', funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : (modeWidth * mode), true);
    // if (hasPrev === false && openNext === true && jumpMarker === '*') { // Disabled in favour of P0693-1201
    if (hasPrev === false && crossFamilyBoundary && openNext === true) {
      openNextDocument(false);
    }
  };

  /**
   * Method sets next figure as active (or open next document if there no more figures)
   * @param {boolean} openNext - indicate should method opens next document if there no more figures
   * @private
   */
  @onAnyPanel
  _openNextFigure (openNext = true) {
    let {store: {nextActiveFigure, modeWidth, full, mode}, openNextDocument, funnelSettings: {funnel, markers}, persistency} = this.props;
    let crossFamilyBoundary = persistency.get({type: 'FNB', module: 'familyBoundarySetting'});
    // let hasNext = nextActiveFigure(jumpMarker, funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : modeWidth); // Disabled in favour of P0693-1201
    let hasNext = nextActiveFigure('*', funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : (modeWidth * mode));
    // if (hasNext === false && openNext === true && jumpMarker === '*') { // Disabled in favour of P0693-1201
    if (hasNext === false && crossFamilyBoundary && openNext === true) {
      openNextDocument(true);
    }
  };

  /**
   * Method sets previous figure (visually line) as active
   * @private
   */
  @onAnyPanel
  _openPreviousMosaicPage () {
    this._openPreviousFigure(false);
  };

  /**
   * Method sets next figure (visually line) as active
   * @private
   */
  @onAnyPanel
  _openNextMosaicPage () {
    this._openNextFigure(false);
  };

  /**
   * Method sets first figure as active in document
   * @private
   */
  @onAnyPanel
  _openFirstFigure () {
    let {store: {firstActiveFigure}, funnelSettings: {jumpMarker, funnel, markers}, isPanelActive} = this.props;
    if (isPanelActive) {
      firstActiveFigure(jumpMarker, funnel === true ? markers : {});
    }

  };

  /**
   * Method sets last figure as active in document
   * @private
   */
  @onAnyPanel
  _openLastFigure () {
    let {store: {lastActiveFigure}, funnelSettings: {jumpMarker, funnel, markers}, isPanelActive} = this.props;
    if (isPanelActive) {
      lastActiveFigure(jumpMarker, funnel === true ? markers : {});
    }
  };

  /**
   * Method sets annotations legend as active or inactive
   * @private
   */
  @onAnyPanel
  _toggleAnnotationsVisibility () {
    let {store, store: {legend}} = this.props;
    store.setLegend(!legend);
  };

  /**
   * Method rotates active figure in anticlockwise direction
   * @private
   */
  @onAnyPanel
  _rotateFigureAnticlockwise () {
    this.props.store.updateActiveFigureAngle(-90);
  };

  /**
   * Method rotates active figure in clockwise direction
   * @private
   */
  @onAnyPanel
  _rotateFigureClockwise () {
    this.props.store.updateActiveFigureAngle(90);
  };

  /**
   * Method zoom-in active figure
   * @private
   */
  @onAnyPanel
  _figureZoomIn () {
    this.props.store.updateActiveFigureZoom('in');
  };

  /**
   * Method zoom-out active figure
   * @private
   */
  @onAnyPanel
  _figureZoomOut () {
    this.props.store.updateActiveFigureZoom('out');
  };

  /**
   * Method resets zoom for active figure
   * @private
   */
  @onAnyPanel
  _figureZoomMax () {
    this.props.store.updateActiveFigureZoom('reset');
  };

  /**
   * Method decrease mosaic mode by 1
   * @private
   */
  @onAnyPanel
  _figuresMosaicBackward () {
    let {store: {mode, modeStep, modeMaxValue, setMode}} = this.props;
    let value = mode + modeStep;
    if (value > modeMaxValue) value = modeMaxValue;
    setMode(value);
  };

  /**
   * Method increase mosaic mode by 1
   * @private
   */
  @onAnyPanel
  _figuresMosaicForward () {
    let {store: {mode, modeStep, modeMinValue, setMode}} = this.props;
    let value = mode - modeStep;
    if (value < modeMinValue) value = modeMinValue;
    setMode(value);
  };

  /**
   * Method activates or deactivate auto flipping
   * @private
   */
  @onAnyPanel
  _pauseResumeAutoFlip () {
    let {store: {autoFlipping, updateAutoFlipping, updateAutoFlippingContinue, updateDenominator, denominatorMinValue}} = this.props;
    let value = !autoFlipping;
    if (value === false) updateAutoFlippingContinue(false);
    else updateDenominator(denominatorMinValue);
    updateAutoFlipping(value);
  };

  /**
   * Method increase auto flipping speed
   * @private
   */
  @onAnyPanel
  _increaseAutoFlipSpeed () {
    let {store: {updateSpeed, speed, speedMaxValue, speedStep}} = this.props;
    let value = speed + speedStep;
    if (value > speedMaxValue) value = speedMaxValue;
    updateSpeed(value);
  };

  /**
   * Method decrease auto flipping speed
   * @private
   */
  @onAnyPanel
  _decreaseAutoFlipSpeed () {
    let {store: {updateSpeed, speed, speedMinValue, speedStep}} = this.props;
    let value = speed - speedStep;
    if (value < speedMinValue) value = speedMinValue;
    updateSpeed(value);
  };

  /**
   * Method increase auto flipping speed by 2
   * @private
   */
  @onAnyPanel
  _increaseAutoFlipMultiplier () {
    let {store: {updateDenominator, denominator, denominatorMaxValue, denominatorMinValue, autoFlipping, updateAutoFlipping}} = this.props;
    let value = denominator * 2;
    if (value > denominatorMaxValue) value = denominatorMinValue;
    updateDenominator(value);
    if (autoFlipping === false) {
      updateAutoFlipping(true);
    }
  };

  /**
   * Method activates or deactivate magnifier lens
   * @private
   */
  @onAnyPanel
  _toggleMagnifier () {
    let {store} = this.props;
    store.magnifier = !store.magnifier;
  };

  /**
   * Method register figures shortcuts
   * @returns {array}
   */
  renderGlobalViewShortcuts () {
    let {
      store: {mode}
    } = this.props;

    let keys = [];

    keys.push({
      name: 'PREV_FIGURE',
      callback: this._openPreviousFigure
    });
    keys.push({
      name: 'NEXT_FIGURE',
      callback: this._openNextFigure
    });

    // should apply only for active panel
    keys.push({
      name: 'FIRST_FIGURE',
      callback: this._openFirstFigure
    });
    keys.push({
      name: 'LAST_FIGURE',
      callback: this._openLastFigure
    });
    keys.push({
      name: 'FIGURES_MOSAIC_BACKWARD',
      callback: this._figuresMosaicBackward
    });
    keys.push({
      name: 'FIGURES_MOSAIC_FORWARD',
      callback: this._figuresMosaicForward
    });
    keys.push({
      name: 'PAUSE_RESUME_AUTOFLIP',
      callback: this._pauseResumeAutoFlip
    });
    keys.push({
      name: 'INCREASE_AUTOFLIP_SPEED',
      callback: this._increaseAutoFlipSpeed
    });
    keys.push({
      name: 'DECREASE_AUTOFLIP_SPEED',
      callback: this._decreaseAutoFlipSpeed
    });
    keys.push({
      name: 'INCREASE_AUTOFLIP_MULTIPLIER',
      callback: this._increaseAutoFlipMultiplier
    });
    // shortcuts only for single view
    if (mode === 1) {
      keys.push({
        name: 'TOGGLE_ANNOTATIONS_VISIBILITY',
        callback: this._toggleAnnotationsVisibility
      });
      keys.push({
        name: 'ROTATE_FIGURE_ANTICLOCKWISE',
        callback: this._rotateFigureAnticlockwise
      });
      keys.push({
        name: 'ROTATE_FIGURE_CLOCKWISE',
        callback: this._rotateFigureClockwise
      });
      keys.push({
        name: 'FIGURE_ZOOM_IN',
        callback: this._figureZoomIn
      });
      keys.push({
        name: 'FIGURE_ZOOM_OUT',
        callback: this._figureZoomOut
      });
      keys.push({
        name: 'FIGURE_ZOOM_MAX',
        callback: this._figureZoomMax
      });
    } else {
      // shortcuts only for mosaic mode view
      keys.push({
        name: 'TOGGLE_LENS',
        callback: this._toggleMagnifier
      });
      keys.push({
        name: 'MOSAIC_PREVIOUS_PAGE',
        callback: this._openPreviousMosaicPage
      });
      keys.push({
        name: 'MOSAIC_NEXT_PAGE',
        callback: this._openNextMosaicPage
      });
    }

    return hotkeys(keys);
  };

  render () {
    return (
      <div>
        {this.renderGlobalViewShortcuts()}
      </div>
    );
  }
}

FiguresShortcuts.propTypes = {
  store: PropTypes.object,
  funnelSettings: PropTypes.object.isRequired,
  isPanelActive: PropTypes.bool.isRequired,
  openNextDocument: PropTypes.func,
  persistency: PropTypes.object
};

export default FiguresShortcuts;
