import PropTypes from 'prop-types';
import React from 'react';
import classes from './FigureViewCounter.scss';

const FigureViewCounter = ({active, current, total}) => {
  return <div
    className={`${classes.root} ${active ? classes.active : ''}`}
  >{`${current}/${total}`}</div>;
};

export default FigureViewCounter;

FigureViewCounter.propTypes = {
  active: PropTypes.bool,
  current: PropTypes.number,
  total: PropTypes.number
};
