import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';

class FigureMarker extends PureComponent {
  render () {
    let {title, index, ratio, marker, angle, height, type, handleDeleteMarker, deleteMarker, x, y, paddingLeft, paddingTop} = this.props;
    let link = `#${this.props.prefix}-marker-m${marker}`;
    let scale = 1;
    if (height <= 143) scale = 0.3;
    else if (height <= 286) scale = 0.6;

    if (type === 'user') link += '-user';
    if (deleteMarker === true) link += '-deletion';

    return (
      <use
        xlinkHref={link}
        data-title={title}
        key={`${index}-${x}-${y}`}
        x={x / scale}
        y={y / scale}
        data-ratio={ratio}
        data-marker={marker}
        data-paddingleft={paddingLeft}
        data-paddingtop={paddingTop}
        style={{cursor: 'move'}}
        // transform={`rotate(${(angle * -1)} ${x} ${y}) scale(${scale})`}
        transform={`translate(${x} ${y}) rotate(${(angle * -1)}) translate(${(x * -1)} ${(y * -1)}) scale(${scale})`}
        onMouseDown={handleDeleteMarker}
      />
    );
  }
}

FigureMarker.propTypes = {
  angle: PropTypes.number.isRequired,
  deleteMarker: PropTypes.bool.isRequired,
  height: PropTypes.number.isRequired,
  handleDeleteMarker: PropTypes.func.isRequired,
  index: PropTypes.number.isRequired,
  marker: PropTypes.string,
  prefix: PropTypes.string,
  ratio: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  paddingLeft: PropTypes.number,
  paddingTop: PropTypes.number,
  x: PropTypes.number.isRequired,
  y: PropTypes.number.isRequired
};

export default FigureMarker;
