/**
 * Helper class that wraps a given callback method with a proxy function in order
 * not to give a direct method reference on the react component. Later all this callbacks (= object references)
 * can be cleared at any moment (i.e. on unmount phase).
 */
class CallbackProxy {
  /**
   * Registry of methods that are used as callbacks
   * @type {Map<String, Array<Function>>} - uuid / array of callbacks
   */
  map = new Map();

  /**
   * Wraps a callback with a proxy function and register in a map
   * @param {String} uuid - id that identifies an instance of react component
   * @param {Function} callback
   * @returns Function proxy
   */
  getCallbackProxy = (uuid, callback) => {
    if (!this.map.has(uuid)) {
      this.map.set(uuid, []);
    }

    const i = this.map.get(uuid).length;
    const proxy = (arg) => {
      const callbacks = this.map.get(uuid);
      if (callbacks && callbacks[i]) {
        callbacks[i](arg);
        callbacks[i] = null;
      }
    };

    this.map.get(uuid).push(callback);

    return proxy;
  };

  /**
   * Clears all callbacks registered for a class instance with a given uuid
   * @param {String} uuid
   */
  removeAllCallbacks = (uuid) => {
    if (this.map.has(uuid)) {
      this.map.delete(uuid);
    }
  }
}

export default new CallbackProxy();
