import {observable} from 'mobx';
import unique from 'lodash.uniq';

/**
 * Figure Annotation
 *
 * @class Annotation
 */
export default class AnnotationModel {
  height;
  key;
  @observable text = '';
  @observable markers = [];
  width;
  @observable x;
  @observable y;
  @observable cx;
  @observable cy;
  cropped;

  /**
   * Create an annotation
   *
   * @param {string} key - annotation key
   * @param {string} text - annotation text
   * @param {number} x - annotation x coordinate
   * @param {number} y - annotation y coordinate
   * @param {number} width - annotation width
   * @param {number} height - annotation height
   * @param {boolean} manual - added manually by user
   */
  constructor (key, text, x, y, width, height, manual = false) {
    this.key = key;
    this.text = text;
    this.markers = [];
    this.x = x;
    this.y = y;
    this.cx = x + width / 2;
    this.cy = y + height / 2;
    this.width = width;
    this.height = height;
    this.manual = manual;
    this.cropped = false;
  }

  parseMarkersFromText = (str) => {
    let markers = [];
    let parseMarkers = str.match(/class='m(.)?'/ig);
    if (parseMarkers !== null) {
      markers = parseMarkers.map((text) => text.replace(/class='m(.)'/g, '$1'));
    }
    return unique(markers);
  };

  /**
   * Update Text on Annotation.
   *
   * @memberOf Annotation
   */
  updateText = (refs, markers) => {
    if (refs[this.key] !== undefined) {
      this.text = refs[this.key];
      this.markers = this.parseMarkersFromText(this.text + '');
      this.text = this.text.replace(/class='m(.)'/g, (str, key) => {
        return key + ' style="background-color: ' + (markers['m' + key].isNotUsedInHighlight ? '' : markers['m' + key]['bgcolor']) + '; color: ' + (markers['m' + key].isNotUsedInHighlight ? '' : markers['m' + key]['color']) + '"';
      });
    }
    return this.markers;
  };
}
