import PropTypes from 'prop-types';
import React, { Component } from 'react';
import classes from './FigureSearchToolBar.scss';

class FigureSearchToolBar extends Component {
  triggerSearch = (evt) => {
    this.props.triggerSearchRun();
  }

  triggerClose = () => {
    this.props.triggerSearchClose();
  }

  render () {
    const {x, y, width, height} = this.props;
    let top = y + height;
    let left = x + width - 166;
    let w = 8;

    if (x === 0 || y === 0 || width === 0 || height === 0) {
      return null;
    }

    return (<g>
      <g ref='searchToolBar'>
        <rect onClick={this.triggerSearch}
          className={classes.figureSearchButton}
          width={126}
          height={24}
          x={left + 5}
          y={top + 0}
        />
        <rect onClick={this.triggerClose}
          className={classes.figureSearchCloseButton}
          width={24}
          height={24}
          x={left + 126}
          y={top + 0}
        />
        <text
          className={classes.figureSearchButtonText}
          x={left + 126}
          y={top + 20}
          fill='black'
          textAnchor='end'
          dominantBaseline='ideographic'
          height={20}
          fontSize={16}
        >Figure search</text>
        <path
          className={classes.figureSearchClose}
          d={`
            M ${left + 132},${top + 6} L ${left + 144},${top + 18}
            M ${left + 144},${top + 6}  L ${left + 132},${top + 18}
          `}
        />
      </g>
      <g ref='searchResize'>
        <rect
          ref='tl'
          className={classes.resizableTL}
          x={x - w / 2} y={y - w / 2} width={w} height={w}
        />
        <rect
          ref='tr'
          className={classes.resizableTR}
          x={x + width - w / 2} y={y - w / 2} width={w} height={w}
        />
        <rect
          ref='bl'
          className={classes.resizableBL}
          x={x - w / 2} y={y + height - w / 2} width={w} height={w}
        />
        <rect
          ref='br'
          className={classes.resizableBR}
          x={x + width - w / 2} y={y + height - w / 2} width={w} height={w}
        />
      </g>
    </g>);
  }
}

FigureSearchToolBar.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  x: PropTypes.number,
  y: PropTypes.number,
  triggerSearchRun: PropTypes.func,
  triggerSearchClose: PropTypes.func
};

export default FigureSearchToolBar;
