import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer} from 'mobx-react';

import SpriteFigure from '../Figure/SpriteFigure';
import FigureLegend from '../FigureLegend';
import FigureViewCounter from '../FigureViewCounter';

import classes from './FigureView.scss';
import {getCrop} from 'Figures.Helper';

const controlPanelHeight = 34;

@observer
class FigureView extends Component {
  constructor (props) {
    super(props);
    this.state = {
      tooltip: {
        x: null,
        y: null,
        text: undefined
      }
    };
  }

  handleCloseLegend = () => {
    this.props.store.setLegend(false);
  };

  handleCloseSelection = () => {
    this.props.store.updateSelection(false);
  };

  onToolTip = (_x, _y, _text) => {
    let {
      tooltip: {
        x, y, text
      }
    } = this.state;
    if (x === _x || y === _y || text === _text) return;

    this.setState({
      tooltip: {
        _x, _y, _text
      }
    });
  };

  get activeMarkers () {
    let {markers, funnelSettings: {markers: funnelMarkers}} = this.props;
    return markers.filter(marker => funnelMarkers[marker.name] === true);
  }

  render () {
    let {
      store: {
        publication: {
          figures
        },
        activeFigure,
        autoFlipping,
        legend,
        panel,
        selection,
        insert,
        deleteMarker
      },
      isBNS,
      width,
      height
    } = this.props;
    if (activeFigure === -1) {
      return (<div className={classes.noMatches}>Sorry, there are no figures that match your selected filters</div>);
    }

    if (figures.length === 0 || figures[activeFigure] === undefined) {
      return null;
    }

    let activeMarkers = this.activeMarkers;
    height -= controlPanelHeight;

    return (
      <div
        className={classes.root}
        ref='root'
        style={{width, height}}
      >
        {legend === true
          ? <FigureLegend
            closeLegend={this.handleCloseLegend}
            figure={figures[activeFigure]}
            height={height}
            markersByColorUnfiltered={this.props.markersByColorUnfiltered}
            onFindSimilar={this.props.onFindSimilar}
            addSelectedTextToMarkerHandler={this.props.addSelectedTextToMarkerHandler}
          />
          : null
        }
        <SpriteFigure
          figure={figures[activeFigure]}
          height={height}
          searchSelection={selection}
          closeSelection={this.handleCloseSelection}
          insert={insert}
          deleteMarker={deleteMarker}
          width={width}
          markers={activeMarkers}
          isStatic={Boolean(false)}
          onToolTip={this.onToolTip}
          crop={Boolean(getCrop(isBNS))}
          prefix={panel}
          isSprite={Boolean(false)}
          index={0}
          angle={figures[activeFigure].angle}
          figureZoom={figures[activeFigure].zoom}
          annotationsLength={figures[activeFigure].annLength}
        />
        <FigureViewCounter
          active={autoFlipping}
          current={activeFigure + 1}
          total={figures.filter(el => !el.filtered).length}
        />
      </div>
    );
  }
}

FigureView.propTypes = {
  isBNS: PropTypes.object,
  store: PropTypes.object,
  width: PropTypes.number,
  height: PropTypes.number,
  markers: PropTypes.array,
  markersByColorUnfiltered: PropTypes.object,
  funnelSettings: PropTypes.object,
  onFindSimilar: PropTypes.func,
  addSelectedTextToMarkerHandler: PropTypes.func
};

export default FigureView;
