import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer} from 'mobx-react';

import FigureMarker from '../FigureMarker';

import classes from './FigureAnnotations.scss';

@observer
class FigureAnnotations extends Component {
  handleDeleteMarker = (marker, evt) => {
    if (marker.type === 'user') {
      this.props.stamps.splice(marker.index, 1);
    } else {
      evt.target.style.display = 'none';
    }
  };

  _renderAnnotations () {
    let desc = '';
    let annotationsList = [];
    let markersList = [];
    let {figureMaxHeight, annotations, height, markers, active, hideToolTip, stamps, cropped} = this.props;
    let markersPadding = 2 / (height / figureMaxHeight);
    let visibleMarkers = {};
    for (let marker of markers) {
      if (marker.status === 0) {
        visibleMarkers[marker.name] = true;
      }
    }

    annotations.forEach((ann, i) => {
      annotationsList.push(<rect
        className={classes.boxes}
        height={ann.height}
        key={i}
        data-title={ann.key}
        width={ann.width}
        x={ann.x}
        y={ann.y}
      />);
      desc += `M${ann.x + cropped.x},${ann.y + cropped.y}h${ann.width}v${ann.height}h${ann.width * -1}Z`;
      if (ann.markers.length > 0) {
        ann.markers.forEach((marker, markerIndex) => {
          if (visibleMarkers[marker] === undefined) return;
          let paddingLeft = (markerIndex) * markersPadding;
          let paddingTop = (markerIndex) * markersPadding;
          let x = (ann.cx + paddingLeft) * 1 + cropped.x;
          let y = (ann.cy + paddingTop) * 1 + cropped.y;
          markersList.push({
            visible: true,
            type: 'system',
            marker,
            title: ann.key,
            x,
            y,
            ratio: this.props.ratio,
            angle: this.props.angle,
            height,
            paddingLeft,
            paddingTop
          });
        });
      }
    });

    stamps.forEach((marker, index) => {
      markersList.push({
        index,
        visible: true,
        type: 'user',
        marker: marker.name,
        title: '',
        x: marker.x,
        y: marker.y,
        ratio: this.props.ratio,
        angle: this.props.angle,
        height
      });
    });

    let {angle, ratio} = this.props;

    let annotationHandlers = {};
    if (hideToolTip !== true) {
      annotationHandlers.onMouseOver = (evt) => {
        this.props.handleMouseOver(evt.target);
      };
      annotationHandlers.onMouseOut = () => {
        this.props.handleMouseOut();
      };
    }

    return (
      <g
        className={classes.root}
        transform={`scale(${this.props.ratio})`}
        {...annotationHandlers}
      >
        {
          active === true && hideToolTip !== true
            ? annotationsList.map(annotation => annotation)
            : <path d={desc} fill='blue' fillOpacity='0.25' className={classes.boxes} />
        }
        {
          markersList.filter(marker => marker.visible).map((marker, index) => {
            return (
              <FigureMarker
                key={index}
                angle={angle}
                deleteMarker={this.props.deleteMarker}
                handleDeleteMarker={(evt) => {
                  if (this.props.deleteMarker === true) {
                    this.handleDeleteMarker(marker, evt);
                  }
                }}
                height={marker.height}
                index={index}
                marker={marker.marker}
                ratio={ratio}
                title={marker.title}
                type={marker.type}
                prefix={this.props.prefix}
                paddingLeft={marker.paddingLeft}
                paddingTop={marker.paddingTop}
                x={marker.x}
                y={marker.y}
              />
            );
          })
        }
      </g>
    );
  }

  render () {
    return this._renderAnnotations();
  }
}

FigureAnnotations.propTypes = {
  length: PropTypes.number,
  angle: PropTypes.number,
  active: PropTypes.bool,
  deleteMarker: PropTypes.bool,
  ratio: PropTypes.number,
  markers: PropTypes.array,
  annotations: PropTypes.oneOfType([
    PropTypes.object, // mobx array
    PropTypes.array // array
  ]),
  height: PropTypes.number,
  hideToolTip: PropTypes.bool,
  prefix: PropTypes.string,
  figureMaxHeight: PropTypes.number,
  handleMouseOver: PropTypes.func,
  handleMouseOut: PropTypes.func,
  stamps: PropTypes.oneOfType([
    PropTypes.object, // mobx array
    PropTypes.array // array
  ]),
  cropped: PropTypes.shape({
    x: PropTypes.number,
    y: PropTypes.number
  })
};

FigureAnnotations.defaultProps = {
  annotations: [],
  stamps: [],
  cropped: {
    x: 0,
    y: 0
  }
};

export default FigureAnnotations;
