import PropTypes from 'prop-types';
import React, { Component } from 'react';
import Rnd from 'react-rnd';
import {Table, Glyphicon} from 'react-bootstrap';
import {observer} from 'mobx-react';
import classes from './FigureLegend.scss';
import { SelectionMenu } from '@epo/web-common';

@observer
class FigureLegend extends Component {
  state = {
    activeClass: ''
  };

  get annotations () {
    return this.props.figure.annotations.map((annotation) => {
      return {
        key: annotation.key,
        text: '<td><b>' + annotation.key + '</b></td><td>' + annotation.text.replace(new RegExp('^' + annotation.key, 'i'), '') + '</td>'
      };
    }).sort((a, b) => {
      let c = parseInt(a.key, 10);
      let d = parseInt(b.key, 10);
      return c > d ? 1 : c < d ? -1 : a.key > b.key ? 1 : a.key < b.key ? -1 : 0;
    })
      .map(annotation => annotation.text)
      .filter((annotation, index, self) => {
        return self.indexOf(annotation) === index;
      });
  }

  boxHandler = (status) => {
    return () => {
      let {root} = this.refs;
      if (root !== undefined) {
        if (status === 'start') {
          root.style.width = '100%';
          root.style.height = '100%';
          root.style.pointerEvents = 'all';
        } else {
          root.style.width = 'auto';
          root.style.height = 'auto';
          root.style.pointerEvents = 'none';
        }
      }
    };
  };

  render () {
    return (
      <div
        className={classes.root}
        ref='root'
      >
        <Rnd
          default={{
            x: 0,
            y: this.props.height - 285,
            width: 260,
            height: 255
          }}
          minWidth={150}
          minHeight={155}
          onResizeStart={this.boxHandler('start')}
          onResizeStop={this.boxHandler('stop')}
          onDragStart={this.boxHandler('start')}
          onDragStop={this.boxHandler('stop')}
          dragHandlerClassName={'.handle'}
        >
          <div className='panel panel-default no-cursor'
            onWheel={(event) => {
              event.stopPropagation();
            }}
          >
            <div className='panel-heading handle'>
              <h4 className='panel-title'>Legend</h4>
              <Glyphicon
                onClick={this.props.closeLegend}
                glyph='remove'
                className='pull-right clickable'
              />
            </div>
            <SelectionMenu
              menuConfig={{hideUnderlineMenu: false}}
              markersByColorUnfiltered={this.props.markersByColorUnfiltered}
              onFindSimilar={(text) => {
                this.props.onFindSimilar(text);
              }}
              addSelectedTextToMarkerHandler={this.props.addSelectedTextToMarkerHandler}
              legend={Boolean(true)}
            >
              <div className='panel-body-wrapper'>
                <div className='panel-body'>
                  <Table>
                    <tbody>{
                      this.annotations.map((annotation, i) => {
                        return (<tr
                          key={i}
                          className={classes.legendRow}
                          dangerouslySetInnerHTML={{__html: annotation}}
                        />);
                      })
                    }</tbody>
                  </Table>
                </div>
                <span className='react-resizable-handle' />
              </div>
            </SelectionMenu>
          </div>
        </Rnd>
      </div>
    );
  }
}

FigureLegend.propTypes = {
  closeLegend: PropTypes.func,
  figure: PropTypes.object,
  height: PropTypes.number,
  markersByColorUnfiltered: PropTypes.object,
  onFindSimilar: PropTypes.func,
  addSelectedTextToMarkerHandler: PropTypes.func
};

export default FigureLegend;
