import {observable, transaction} from 'mobx';
import * as figuresAPI from 'api/FiguresAPI';

import {
  PublicationsStore,
  FigureStoreItem
} from './models';
import FiguresCacheStore from './FiguresCacheStore';

/**
 * Figures Store
 *
 * @export
 * @class FiguresStore
 */
class FiguresStore {
  @observable stores = {};
  @observable publications = {};
  @observable globalSpeed = 1000;

  /**
   * Creates an instance of FiguresStore.
   * @param {Object} params - parameters
   * @memberOf FiguresStore
   */
  constructor (params) {
    const { api } = params;
    this.publications = new PublicationsStore(api);
    this.cache = new FiguresCacheStore(undefined, api);
  }

  /**
   * Replace initial persistency with another instance
   * usually, properly initialised instance
   *
   * @param {Persistency} persistency
   */
  addPersistency (persistency) {
    this.persistency = persistency;
    const globalSettings = persistency.get({
      type: 'FNB',
      module: 'figures_settings'
    });
    if (globalSettings) {
      this.globalSpeed = globalSettings.speed || 1000;
    }
  }

  /**
   * Crete Figures stores by viewer type and panel name
   *
   * @param {number} sessionId - session id
   * @param {string} type - viewer type
   * @param {string} panel - panel name
   *
   * @memberOf FiguresStore
   */
  createStore (sessionId, type, panel) {
    if (sessionId === undefined || type === undefined || panel === undefined) {
      throw new Error('sessionId, Type and Panel required');
    } else if (typeof sessionId !== 'number' || typeof type !== 'string' || typeof panel !== 'string') {
      throw new Error('Type and Panel should be string, sessionId should be number');
    }

    if (this.stores[sessionId] === undefined) {
      this.stores[sessionId] = {};
    }

    if (this.stores[sessionId][type] === undefined) {
      this.stores[sessionId][type] = {};
    }
    if (this.stores[sessionId][type][panel] === undefined) {
      this.stores[sessionId][type][panel] = new FigureStoreItem(type, panel, this.persistency, this.updateGlobalSpeed);
    }
  }

  updateGlobalSpeed = (speed) => {
    this.globalSpeed = speed;
    this.persistency.set({
      type: 'FNB',
      module: 'figures_settings',
      data: { speed: speed }
    });
  }

  /**
   * Set Publication Id and link to publication
   *
   * @param {Number} sessionId - session id
   * @param {String} type - viewer type
   * @param {String} panel - panel name
   * @param {Publication} publication - publication
   * @param {String} publication.familyId - family id
   * @param {String} publication.publicationId - publication id
   * @param {String} publication.publicationNumber - publication number
   * @param {String} publication.publicationIdShort - publication id short version
   * @param {String} publication.documentType - document type (application or publication)
   * @param {Array} publication.figures - publication figures
   * @param {Function} publication.onCreate - call back on create publication (wrapped with a proxy function)
   *
   * @memberOf FiguresStore
   */
  setPublication (sessionId, type, panel, publication) {
    if (sessionId === undefined || type === undefined || panel === undefined || publication === undefined) {
      throw new Error('Type, Panel and Publication required');
    } else if (typeof sessionId !== 'number' || typeof type !== 'string' || typeof panel !== 'string' || typeof publication !== 'object') {
      throw new Error('Type, Panel and Publication should be string');
    }

    const publicationId = this.publications.publications[publication.publicationId];
    if (publicationId === undefined) {
      this.publications.createPublication({
        familyId: publication.familyId,
        publicationId: publication.publicationId,
        publicationNumber: publication.publicationNumber,
        publicationIdShort: publication.publicationIdShort,
        documentType: publication.documentType,
        figures: publication.figures,
        onCreate: publication.onCreate,
        viewerType: type
      });
    }

    // Check if store was create before
    this.createStore(sessionId, type, panel);
    let store = this.stores[sessionId][type][panel];
    transaction(() => {
      store.publicationId = publication.publicationId;
      store.publication = this.publications.getById(this.stores[sessionId][type][panel]['publicationId']);
      store.resetStore();
    });
  }

  /**
   * Get Active Publication link by Publication ID
   *
   * @param {number} sessionId - session id
   * @param {string} type - viewer type
   * @param {string} panel - panel name
   *
   * @returns {Publication|*} - Publication
   *
   * @memberOf FiguresStore
   */
  getActivePublication (sessionId, type, panel) {
    return this.publications.getById(this.stores[sessionId][type][panel]['publicationId']);
  }
}

const figuresStore = new FiguresStore({api: figuresAPI});

export {
  figuresStore as default,
  FiguresStore
};
