import {
  CROPPING_ATTRIBUTE,
  FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP,
  CONTROL_PANEL_HEIGHT,
  SIDEBAR_THUMBS_BORDERS,
  SIDEBAR_WIDTH,
  PATENT,
  NON_PATENT,
  UNPUBLISHED,
  paths
} from '@epo/web-common';

/**
 * Find lowest possible mutliplier for figure
 *
 * @export
 * @param {number} width - current figure width
 * @param {number} height - current figure height
 * @param {number} maxWidth - maximum figure width
 * @param {number} MaxHeight - maximum figure width
 * @param {number} [multiplier=2] - multiplier for figure
 * @returns {number} multiplier
 */
export const multiplyBy = function (width, height, maxWidth, MaxHeight, multiplier = 2) {
  let multiplierX;
  let multiplierY;

  if (width * multiplier > maxWidth) multiplierX = maxWidth;
  else multiplierX = width * multiplier;

  if (height * multiplier > MaxHeight) multiplierY = MaxHeight;
  else multiplierY = height * multiplier;

  return multiplierX < multiplierY ? parseInt(multiplierX, 10) : multiplierY;
};

/**
 * Round number for precision
 *
 * @export
 * @param {number} number - number to round
 * @param {number} precision - how many 0 after dot
 * @returns {number}
 */
export const round = function (number, precision) {
  let factor = Math.pow(10, precision);
  let tempNumber = number * factor;
  let roundedTempNumber = Math.round(tempNumber);
  return roundedTempNumber / factor;
};

export const getTransform = function (str) {
  return Object.assign(...str.match(/(\w+)\(([^,)]+),?([^)]+)?\)/gi).map((el) => {
    let prop = el.match(/[\w.-]+/g);
    return [prop.shift(), prop];
  }).map(el => ({[el[0]]: el[1]})));
};

export const updateAttributes = function (node, attr) {
  if (typeof node.setAttribute !== 'function') return;
  for (let i in attr) {
    if (attr.hasOwnProperty(i) === true) {
      node.setAttribute(i, attr[i]);
    }
  }
};

export const isContainClass = function (node, str) {
  let className = node.getAttribute('class');
  return !(className === null || className.indexOf(str) === -1);
};

export const imagesPreload = function (list) {
  let images = [...list];
  let loadedImagesInProgress = 0;
  let loadedImagesLimit = 6;

  const loadImage = (image) => {
    if (image === undefined) {
      return;
    }
    loadedImagesInProgress += 1;
    let img = new Image();
    img.src = image;
    img.onabort = onLoadAbort;
    img.onerror = onLoadError;
    img.onload = onLoadSuccess;
    loadNextImage();
  };
  const loadNextImage = () => {
    if (loadedImagesInProgress <= loadedImagesLimit) {
      loadImage(images.shift());
    }
  };
  const onLoadFinished = () => {
    loadedImagesInProgress -= 1;
  };
  const onLoadAbort = () => {
    onLoadFinished();
    loadNextImage();
  };
  const onLoadError = () => {
    onLoadFinished();
    loadNextImage();
  };
  const onLoadSuccess = () => {
    onLoadFinished();
    loadNextImage();
  };

  loadNextImage();
};

export const getDefaultDimensions = function (publicationId) {
  let width;
  let height;
  let countryCode = publicationId ? publicationId.slice(0, 2) : undefined;

  switch (countryCode) {
    case 'JP':
      width = 1653;
      height = 2338;
      break;
    case 'US':
      width = 2560;
      height = 3300;
      break;
    case 'EP':
      width = 2592;
      height = 3508;
      break;
    default:
      width = 2479;
      height = 3508;
  }

  return {width, height};
};

/**
 * Parses facsimieId and pageId from figure model
 * @param figure - a figure model
 * @returns {}an array}
 */
export const getFacsimileAttributes = function (figure) {
  const { source } = figure;
  if (source.indexOf('sprite') === -1) {
    const isXP = source.indexOf('XP') !== -1;
    const shortIndex = 2;
    const url = /^http/.test(source) ? new URL(isXP ? source.replace(/&/, '?') : source) : null;
    const splitSource = (!url ? source : url.pathname.replace(/\/$/, '')).split('/');
    const fullIndex = splitSource.length - 1;
    const page = !url ? splitSource[shortIndex].trim() : splitSource[fullIndex].trim();
    const facsimileId = !url ? splitSource[shortIndex - 1].trim() : splitSource[fullIndex - (!isXP ? 1 : 4)].trim();
    return [facsimileId, page];
  } else {
    return [figure.facsimileId, figure.sheet];
  }
};

/**
 * Builds unique sprite request key
 * @param index - a start position
 * @param maxLen - max number of images in a sprite
 * @param source - sprite url
 * @param attributes - an array with sprite attributes
 * @param rotation - sprite rotation
 * @returns {string}
 */
export const getImageKey = function (index, maxLen, source, attributes, rotation) {
  const url = new URL(source);
  const { pathname, searchParams } = url;
  ['width', 'height'].forEach(param => searchParams.delete(param));
  let key = `${pathname}${searchParams.toString()}_${rotation}_`;
  if (attributes.length > 0) {
    if (index >= attributes.length) {
      console.warn('FiguresHelper.getImageKey.warning', 'Image index is greater than image array length. Images could not be cached in FiguresCacheStore correctly!');
      index = attributes.length - 1;
    }
    const figures = getFiguresInterval(index, maxLen, attributes);
    figures.forEach((attr, index) => {
      if (index === 0) {
        key += attr.join('_');
      } else {
        key += '_' + attr[1];
      }
    });
  } else {
    console.warn('FiguresHelper.getImageKey.warning', 'Attributes are empty. Images could not be cached in FiguresCacheStore correctly!');
  }
  return key;
};

/**
 * Bilds sprites attributes into a payload string
 * @param index - start index in an array
 * @param maxLen - max number of images in a sprite
 * @param attributes - an array with sprite attributes
 * @returns {string}
 */
export const getSpriteBody = function (index, maxLen, attributes) {
  let body = '';
  const figures = getFiguresInterval(index, maxLen, attributes);
  figures.forEach((attr) => {
    body += attr.join('~');
    body += '\n';
  });
  return body;
};

/**
 * Returns a slice of an array
 * @param index - start index of the array
 * @param maxLen - max number of images in a sprite
 * @param attributes - an array of sprite attributes
 * @returns {*}
 */
export const getFiguresInterval = function (index, maxLen, attributes) {
  if (attributes.length <= index) {
    return [];
  }
  let start = ~~(index / maxLen);
  start *= maxLen;
  let end = start + maxLen - 1;
  end = (attributes.length - 1) < end ? attributes.length - 1 : end;

  return attributes.slice(start, end + 1);
};

/**
 * Returns sprite zoom
 * @param figureZoom - figure zoom
 * @param spriteZoom - sprite zoom
 * @param isStatic - image is static
 * @param isSprite - image is from sprite
 * @returns {number}
 */
export const getSpriteZoom = function (isStatic, isSprite, figureZoom = 1, spriteZoom = 1) {
  return spriteZoom * (isStatic === true ? (isSprite === true ? 1 : 1 / FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP) : figureZoom);
};

/**
 * Returns sprite width
 * @param width - sprite width
 * @param crop - cropping is applied
 * @param zoom - zoom
 * @returns {number}
 */
export const getSpriteWidth = function (width, crop, zoom) {
  return Math.round(width * zoom * (crop === false ? 1 : FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP));
};

/**
 * Returns sprite height
 * @param height - sprite height
 * @param crop - cropping is applied
 * @param zoom - zoom
 * @returns {number}
 */
export const getSpriteHeight = function (height, crop, zoom) {
  return Math.round(height * zoom * (crop === false ? 1 : FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP));
};

/**
 * Returns max zoom for a figure
 * @param maxSize - max size of a figure
 * @param size - size of a figure
 * @param crop - cropping is applied
 * @returns {number}
 */
export const getMaxZoom = function (maxSize, size, crop) {
  return maxSize / (size * (crop === false ? 1 : FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP));
};

/**
 * Returns max zoom for a sprite
 * @param maxZoom - max zoom of a figure
 * @param spriteZoom - sprite zoom
 * @param isStatic - a figure is static
 * @param isSprite - a figure is from a sprite
 * @returns {number}
 */
export const getMaxFigureZoom = function (maxZoom, spriteZoom, isStatic, isSprite) {
  if (isStatic === true && isSprite === true) return 1;
  if (isStatic === true && isSprite === false) return 1 / FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP;
  return maxZoom / spriteZoom;
};

/**
 * Returns a sprite url extended with width and height params
 * @param width - sprite width
 * @param height - sprite height
 * @param crop - cropping is applied
 * @param angle - figure angle
 * @param source - sprite url
 * @returns {*}
 */
export const extendSpriteSource = function (width, height, crop, angle, source) {
  source += (source.indexOf('?') === -1 ? '?' : '&') + 'height=' + height + '&width=' + width;

  if (crop === false && source.indexOf('crop=-1') !== -1) {
    source = source.replace(/&crop=(-?\d+)/g, '');
  }
  return source;
};

/**
 * Returns sprite url
 * @param source - facsimile url
 * @returns {*}
 */
const getSpriteSource = function (source) {
  if (source.indexOf('sprite') !== -1) return source;

  let applicationNumber;
  const PUB_NR = 'publicationNumber=';
  const SEPARATOR = '/';
  const PARAM = '&';
  const isXP = source.indexOf('XP') !== -1;
  const url = new URL(isXP ? source.replace(/&/, '?') : source);
  let sourceArr = url.pathname.replace(/\/$/, '').split(SEPARATOR);
  let rid;

  let docType = isXP ? NON_PATENT : (source.indexOf(UNPUBLISHED) !== -1) ? UNPUBLISHED : PATENT;
  if (docType === UNPUBLISHED) {
    const typeIndex = sourceArr.length - 5;
    applicationNumber = sourceArr[typeIndex + 1].trim();
    rid = [sourceArr[typeIndex].trim(), applicationNumber].join(SEPARATOR);
  } else {
    rid = sourceArr[sourceArr.length - 4].trim();
  }

  let spriteSource = paths.getSpriteFacsimile.replace('%docType', docType).replace('%rid', rid);

  sourceArr = source.split(PARAM);
  const publicationNumber = sourceArr.find(item => item.startsWith(PUB_NR));
  if (publicationNumber !== undefined && publicationNumber.length > PUB_NR.length) {
    spriteSource += PARAM + publicationNumber;
  }
  return spriteSource;
};

/**
 * Returns an item (figure) size for figures in mosaic mode
 * @param mode - figures mode
 * @param figures - an array of figures
 * @param height - height of a container
 * @param width - width of a container
 * @param scrollbarWidth - a scroll bar width
 * @returns {{itemWidth: number, itemHeight: number, fit: number}}
 */
export const getItemSize = function (mode, figures, height, width, scrollbarWidth) {
  let innerWidth = width - scrollbarWidth;
  const imageAspectRatio = 1.43;
  let itemWidth = 100;
  let itemHeight = itemWidth * imageAspectRatio;
  let fit = 1;

  if (mode !== 1) {
    let checkPrevMode = true; // should check previous mode
    let notFit = false; // indicate when it's not possible to fit all images on 1 page
    let steppedBack = false; // indicate that we make step back to fit images on 1 page
    while (checkPrevMode === true) {
      itemHeight = (height - 4) / (mode < figures.length ? mode : figures.length);
      itemWidth = (itemHeight) / (imageAspectRatio);
      fit = Math.floor((width - 20) / itemWidth);
      fit = (fit > figures.length) ? figures.length : fit;
      fit = (fit === 0 && figures.length === 1) ? 1 : fit;
      itemWidth = (innerWidth - 4) / fit;
      /**
       * If we have less images than we can display on the page we are trying to go step back.
       * We check is it possible to put all images on 1 page if mode will be -1 from last mode.
       * (but not less than 2, we can't switch to mode 1)
       * If we can we check can we do the same again.
       * If we can't display images on steeped back mode we return previous mode (mode + 1)
       */
      // When we stepped back but found out that it's not possible to put all images on single page we return previous mode.
      if (notFit) {
        checkPrevMode = false;
        // Check if can display all images on a single page and mode is not 1
      } else if (mode * fit >= figures.length && mode > 1) {
        // Indicate that we are trying to use smaller mode
        steppedBack = true;
        // We are trying to decrease mode and check if we can fit all images in that mode
        mode = mode - 1;
      } else {
        // If we changed mode before
        if (steppedBack === true) {
          // We can't put all images on smaller mode so we return to previous mode
          mode = mode + 1;
          notFit = true;
          // if didn't change mode before (it can happens when you have 1 image or when you have more images then you can add to single page)
        } else {
          checkPrevMode = false;
        }
      }
    }
  }
  return {itemWidth, itemHeight, fit};
};

/**
 * Returns mosaic view size
 * @param mode - figures mode
 * @param isBNS - BNS view is activated
 * @param height - height of a container
 * @param width - width of a container
 * @param documentWithError - is document loaded with an error
 * @returns {{mosaicViewWidth: number, mosaicViewHeight: number}}
 */
export const getMosaicViewSize = function (mode, isBNS, height, width, documentWithError = false) {
  return {
    mosaicViewWidth: mode === 1 ? (SIDEBAR_WIDTH - SIDEBAR_THUMBS_BORDERS * 2) : width,
    mosaicViewHeight: height - CONTROL_PANEL_HEIGHT + ((isBNS !== undefined && isBNS.active || (documentWithError && !isBNS.active)) ? 16 : 0)
  };
};

/**
 * Builds and sets values regarding sprites to model of each figures from an array
 * @param figures - an array of figure models
 */
export const setSprite = function (figures) {
  let sprite;
  let source;

  figures.forEach(figure => {
    if (figure !== undefined) {
      if (sprite === undefined) {
        sprite = collectSprite(figures);
      }
      figure.sprite = sprite;
    }
    if (figure === undefined) return;
    if (source === undefined) {
      source = getSpriteSource(figures[0].source);
    }
    figure.spriteSource = source;

    if (figure.facsimileId === undefined) {
      const [facsimileId, page] = getFacsimileAttributes(figure);
      figure.facsimileId = facsimileId;
      figure.sheet = page;
    }
  });
};

/**
 * Collects figures attributes facsimileId and pageId as sprite attributes
 * @param figures - an array of figure models
 * @returns {Array}
 */
export const collectSprite = function (figures) {
  let sprite = [];
  if (figures.length) {
    //collect facsimiles
    let processed = [];
    figures.forEach((figure) => {
      let item = {...figure};
      let attr = !item.facsimileId ? getFacsimileAttributes(item) : [figure.facsimileId, figure.sheet];
      if (attr) {
        const attrKey = attr.join('');
        if (processed.find(key => key === attrKey) === undefined) {
          sprite.push(attr);
          processed.push(attrKey);
        }
      }
    });
  }
  return sprite;
};

/**
 * Returns whether cropping should be applied
 * @param isBNS - BNS view is activated
 * @returns {boolean}
 */
export const getCrop = function (isBNS) {
  return CROPPING_ATTRIBUTE === -1 && (isBNS === undefined || isBNS.active !== true);
};

/**
 * Returns whether cropping should be applied with taking mode in consideration
 * @param isBNS - BNS view is activated
 * @param mode - figures view mode
 * @returns {*|boolean}
 */
export const getModeCrop = function (isBNS, mode) {
  return getCrop(isBNS) && mode !== 1;
};

/**
 * Calculates side bar width
 * @param root - element root
 * @param mode - figures view mode
 * @returns {number}
 */
export const calculateSideBar = (root, mode) => {
  if (root !== undefined && root !== null && mode !== 1) {
    return root.offsetWidth - root.clientWidth;
  } else {
    return 0;
  }
};

/**
 * Returns indication (true or false) whether figures in a requested interval are straight and rotated
 * @param index - a start index in an array from which figures to be extracted
 * @param limit - a number of figures to be extracted from a figure array
 * @param figures - an array of figures
 * @returns {{direct: boolean, rotate: boolean}}
 */
export const getRotation = function (index, limit, figures) {
  let direct = false;
  let rotate = false;
  const attributes = getFiguresInterval(index, limit, figures[0].sprite);
  if (attributes.length > 0) {
    attributes.forEach((attr) => {
      let figure = figures.find(figure => figure.facsimileId === attr[0] && figure.sheet === attr[1]);
      if (figure) {
        rotate = rotate || figure.mosaicAngle % 180;
        direct = direct || (figure.mosaicAngle % 180) === 0;
      }
    });
  }
  return {direct, rotate};
};
