/**
 * Interface specifies the standard properties used by DOMRect to define a rectangle
 * @external DOMRectReadOnly
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/API/DOMRectReadOnly}
 */

import PropTypes from 'prop-types';

import React, { Component } from 'react';
import classNames from 'classnames/bind';
import find from 'lodash.find';
import {observer, inject} from 'mobx-react';

import {coordinatesOnAngle} from './Figure.Helper';
import FigureSearchToolBar from './FigureSearchToolBar/FigureSearchToolBar';
import FigureToolTip from '../FigureToolTip';
import FigureAnnotations from './FigureAnnotations';
import MarkersSVG from 'components/MarkersSVG/MarkersSVG';

import {
  levels,
  Spinner,
  CROP_BORDERS,
  MARKER_WIDTH,
  MARKER_HEIGHT,
  FIGURES_ZOOM_SELECTION_LIMIT,
  FIGURE_DEFAULT_ZOOM,
  FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP
} from '@epo/web-common';

import classes from './Figure.scss';

let cx = classNames.bind(classes);

@inject((stores) => ({
  notify: stores.notify,
  figureSearch: stores.figureSearch
}))
@observer
class Figure extends Component {
  static defaultProps = {
    onMousePosition: function () {
    },
    skipOrientation: false,
    deleteMarker: false,
    zoom: FIGURE_DEFAULT_ZOOM
  };

  state = {
    active: false,
    zoom: 1,
    markers: [],
    moveSearch: false,
    loaded: false,
    error: false,
    search: {
      x: 0,
      y: 0,
      width: 0,
      height: 0
    }
  };

  image = {
    width: -1,
    height: -1
  };

  mouseDownPoint = {
    x: 0,
    y: 0
  };

  background = {
    x: 0,
    y: 0
  };

  translate = {
    x: 0,
    y: 0
  };

  rotate = {
    x: 0,
    y: 0
  };

  elements = [];

  markers = [];

  zoomScale = 1;
  translateScale = 1;

  componentDidUpdate (prevProps) {
    if (prevProps.searchSelection !== this.props.searchSelection) {
      if (this.props.searchSelection === false) {
        this.disableSearchBox();
        this.clearSelection();
      }
    }
    if (typeof prevProps.selection === 'object' && prevProps.selection.x !== undefined) {
      if (prevProps.selection.x !== this.props.selection.x) {
        this.createSelectionBox();
      }
    }
  }

  UNSAFE_componentWillReceiveProps (nextProps) {
    if (nextProps.loadState.loaded === true && nextProps.loadState.error === false) {
      if (nextProps.url === this.props.url && nextProps.figureZoom !== this.props.figureZoom) {
        this.translateScale = this.props.urlZoom / nextProps.figureZoom;
        this.zoomScale = 1 / this.translateScale;
      } else if ((this.props.url === '' && nextProps.url !== this.props.url && nextProps.figureZoom === this.props.figureZoom) ||
        (nextProps.url !== this.props.url && nextProps.figureZoom === this.props.figureZoom && this.props.urlZoom !== nextProps.urlZoom)) {
        this.translateScale = nextProps.urlZoom / nextProps.figureZoom;
        this.zoomScale = 1 / this.translateScale;
      } else if (nextProps.url !== this.props.url) {
        this.zoomScale = 1;
        this.translateScale = 1;
      }
    } else {
      this.setState({
        loaded: nextProps.loadState.loaded,
        error: nextProps.loadState.error
      });
    }

    if (typeof nextProps.figure === 'object' && typeof nextProps.figure.source === 'string') {
      if (nextProps.figure.source !== this.props.figure.source) {
        if (this.props.searchSelection === true) {
          this.disableSearchBox();
          this.clearSelection();
        }
        this.setState({
          loaded: false,
          error: false
        });
      }
    }
  }

  componentDidMount () {
    this.createSelectionBox();
  }

  /**
   * Method returns cursor position
   * @param e
   * @returns {{x: number, y: number}}
   * @private
   */
  _cursorPoint (e) {
    return {
      x: e.clientX - this.point.left,
      y: e.clientY - this.point.top
    };
  }

  /**
   * Method returns maximal width and height of the figure based when crop applied
   * @returns {{maxHeight: Number, maxWidth: Number}}
   */
  get maxWidthAndHeight () {
    let {
      figure: {
        maxOriginalHeight,
        maxOriginalWidth,
        maxHeight: figHeight,
        maxWidth: figWidth
      },
      crop,
      width,
      height
    } = this.props;
    if (crop === false && maxOriginalHeight !== undefined && maxOriginalWidth !== undefined) {
      figHeight = maxOriginalHeight;
      figWidth = maxOriginalWidth;
    }
    return {
      maxHeight: figHeight !== undefined ? figHeight : height,
      maxWidth: figWidth !== undefined ? figWidth : width
    };
  }

  /**
   * Method returns figure cropping when crop applied to component
   * @returns {{x: number, y: number}}
   */
  get croppingValues () {
    let {figure: {cropped}} = this.props;
    let x = 0;
    let y = 0;
    if (this.props.crop === false && cropped && cropped.fetched && cropped.fetched === 1) {
      x = cropped.x;
      y = cropped.y;
    }
    return {
      x, y
    };
  }

  /**
   * Method returns aspect ratio for figure
   * @returns {number}
   */
  get aspectRatio () {
    let {maxWidth, maxHeight} = this.maxWidthAndHeight;
    return maxWidth / maxHeight;
  }

  /**
   * Method returns the size of an element and its position relative to the viewport
   * @returns {DOMRectReadOnly}
   */
  get point () {
    return this.refs.root.getBoundingClientRect();
  }

  get zoom () {
    let fitIn = 1;
    let {
      figure: {
        zoom: figureZoom
      },
      crop,
      height,
      isStatic,
      finIn,
      width,
      zoom,
      isSprite
    } = this.props;
    let {maxWidth, maxHeight} = this.maxWidthAndHeight;

    if (isStatic === false || finIn === true || isSprite === true) {
      if (this.angle % 180) {
        fitIn = 1 / (maxWidth / maxHeight);
        fitIn = fitIn * fitIn;
        let isFitedHeight = (height / width) / (maxWidth / maxHeight);
        if (isFitedHeight > 1) {
          fitIn /= isFitedHeight;
        }
      } else {
        let isFitedHeight = (width / height) / (maxWidth / maxHeight);
        if (isFitedHeight < 1) {
          fitIn *= isFitedHeight;
        }
      }
    }
    return (isStatic === true ? 1 : figureZoom) * (crop === true && zoom === FIGURE_DEFAULT_ZOOM ? FIGURE_DEFAULT_ZOOM_WITH_AUTO_CROP : zoom) * (this.angle % 180 ? this.aspectRatio : 1) * fitIn * CROP_BORDERS;
  }

  /**
   * Method return source to file with height based on zoom
   * @returns {string} - url to file
   */
  get source () {
    let {figure: {source}, height} = this.props;
    let {maxHeight} = this.maxWidthAndHeight;

    height = Math.round(height * this.zoom);
    if (height > maxHeight) {
      height = maxHeight;
    }
    source += (source.indexOf('?') === -1 ? '?' : '&') + 'height=' + height;

    if (this.props.crop === false && source.indexOf('crop=-1') !== -1) {
      source = source.replace(/&crop=(-?\d+)/g, '');
    }

    if (source.indexOf('content-type') === -1) {
      source += (source.indexOf('?') === -1 ? '?' : '&') + 'content-type=image/png';
    }

    return source;
  }

  /**
   * Method returns figure dimensions
   * @returns {{ratio: number, width: number, height: number, x: (number|*), y: (number|*)}}
   */
  imageDimensions = (factor = 1) => {
    let {maxWidth, maxHeight} = this.maxWidthAndHeight;
    let ratio = this.props.height / maxHeight;
    let height = Math.round(maxHeight * ratio * factor);
    let width = Math.floor(maxWidth * ratio * factor);
    let x;
    let y;

    x = (this.props.width - width) / 2;
    y = (this.props.height - height) / 2;

    /*
      Previously ration was limited by 1,
      because it was not possible to have screen more than images,
      with auto cropping it's possible.
     */
    return {
      ratio,
      width,
      height,
      x,
      y
    };
  }

  /**
   * Method returns angle of rotation if not blocked by skipOrientation prop
   * @returns {number} - angle of rotation
   */
  get angle () {
    let {figure: {angle, mosaicAngle}, skipOrientation, isSprite, isStatic} = this.props;
    return skipOrientation === true ? 0 : !isSprite && !isStatic ? angle : mosaicAngle;
  }

  setAttributes = (node, attrs) => {
    if (typeof node.setAttribute === 'function') {
      for (let i in attrs) {
        if (attrs.hasOwnProperty(i) === true) {
          node.setAttribute(i, attrs[i]);
        }
      }
    }
  };

  /**
   * Method handle moving image in plane
   * @param type {string} - action type
   * @param evt {MouseEvent} - user mouse action
   */
  backgroundTranslateHandler = (type, evt) => {
    let cursor = this._cursorPoint(evt);

    if (type === 'start') {
      this.mouseDownPoint = cursor;
      this.hideAnnotationToolTip(true);
    } else if (type === 'stop') {
      this.props.figure.offset.x += cursor.x - this.mouseDownPoint.x;
      this.props.figure.offset.y += cursor.y - this.mouseDownPoint.y;
      this.background.x = this.props.figure.offset.x;
      this.background.y = this.props.figure.offset.y;
    } else if (type === 'process') {
      let x = this.props.figure.offset.x + cursor.x - this.mouseDownPoint.x;
      let y = this.props.figure.offset.y + cursor.y - this.mouseDownPoint.y;
      this.background.x = this.props.figure.offset.x;
      this.background.y = this.props.figure.offset.y;
      this.updateBackgroundTranslate(x, y);
    }
  };

  /**
   * Methdo update position on figure in plane
   * @param x {number} - x coordinate
   * @param y {number} - y coordinate
   */
  updateBackgroundTranslate = (x, y) => {
    // this.props.figure.offset.x = x;
    // this.props.figure.offset.y = y;
    this.refs.background.setAttribute('transform', `translate(${x} ${y})`);
    this.refs.imgBackground.style.transform = `translate(${x}px, ${y}px)`;
  };

  _calculateRotate = () => {
    let {height, width} = this.image;
    this.rotate.x = Math.round(width * this.zoom / 2);
    this.rotate.y = Math.round(height * this.zoom / 2);
    return `rotate(${this.angle} ${this.rotate.x * this.zoomScale} ${this.rotate.y * this.zoomScale})`;
  };

  _calculateTranslate = (factor = 1) => {
    let {width, height} = this.imageDimensions(factor);
    let x = (this.props.width - width * this.zoom) / 2;
    let y = (this.props.height - height * this.zoom) / 2;

    this.translate = {x, y};

    return {x, y};
  };

  setActionType = (action) => {
    this.action = action;
  };

  handleContextMenu = (evt) => {
    evt.preventDefault();
  };

  handleMouseLeave = (evt) => {
    // special behaviour when mouse leave figure on selection search.
    if (this.action === 'selection') {
      this.createSelection('aborted', evt);
    } else if (this.action === 'move-search') {
      this.moveSearchBox();
    }
    this.setState({
      active: false
    });
    this.moveElement('aborted', evt);
    this.setActionType();
  };

  handleMouseEnter = () => {
    this.setState({
      active: true
    });
  };

  calculateRotatedPosition = (position) => {
    let {width, height} = this.props;
    let x = (position.x);
    let y = (position.y);

    if (this.angle === 0 || this.angle === 360) {
      x = position.x - (this.translate.x + this.props.figure.offset.x);
      y = position.y - (this.translate.y + this.props.figure.offset.y);
    } else if (this.angle === 90) {
      x = position.y - (this.translate.y + this.props.figure.offset.y) - (this.rotate.y - this.rotate.x);
      y = width - position.x - this.translate.x + this.props.figure.offset.x + (this.rotate.y - this.rotate.x);
    } else if (this.angle === 180) {
      x = width - position.x - this.translate.x + this.props.figure.offset.x;
      y = height - position.y - this.translate.y + this.props.figure.offset.y;
    } else if (this.angle === 270) {
      x = height - position.y - this.translate.y + this.props.figure.offset.y - (this.rotate.y - this.rotate.x);
      y = position.x - this.translate.x - this.props.figure.offset.x + (this.rotate.y - this.rotate.x);
    }

    x /= this.image.ratio * this.zoom;
    y /= this.image.ratio * this.zoom;

    return {x, y};
  };

  insertMarker = (evt, source) => {
    let {figure: {stamps}} = this.props;
    let position = this._cursorPoint(evt);
    let {x, y} = this.calculateRotatedPosition(position);
    stamps.push({
      name: this.props.insert,
      source: source,
      x,
      y
    });
  };

  handleMouseDown = (evt) => {
    evt.stopPropagation();

    if (evt.button === 2) {
      this.setActionType('translate');
      this.backgroundTranslateHandler('start', evt);
    } else if (evt.button === 0) {
      let {nodeName} = evt.target;
      if (this.props.insert !== false && nodeName !== 'use') {
        let {figure: {source}} = this.props;
        this.insertMarker(evt, source);
      } else if (nodeName === 'svg') {
        if ((this.props.searchSelection === true && this.state.search.width === 0) || this.props.searchSelection === false) {
          this.disableSearchBox();
          this.setActionType('selection');
          this.createSelection('start', evt);
        }
      } else if (nodeName === 'use') {
        this.setActionType('move');
        this.hideAnnotationToolTip();
        this.moveElement('start', evt);
      } else if (nodeName === 'rect') {
        let className = evt.target.getAttribute('class');
        if (className !== null) {
          if (className.indexOf('selectionSearch') !== -1) {
            this.setActionType('move-search');
            this.moveElement('start', evt);
            this.disableSearchBox();
          } else if (className.indexOf('resizable') !== -1) {
            this.resizeElement(className, evt);
          }
        }
      }
    }
  };

  handleMouseMove = (evt) => {
    evt.preventDefault();
    evt.stopPropagation();
    if (this.props.trackMouse === true) {
      this.props.onMousePosition(this._cursorPoint(evt));
    }
    switch (this.action) {
      case 'translate':
        this.backgroundTranslateHandler('process', evt);
        break;
      case 'selection':
        this.createSelection('process', evt);
        break;
      case 'move':
      case 'move-search':
        this.moveElement('process', evt);
        break;
      default:
        break;
    }
  };

  handleMouseUp = (evt) => {
    evt.preventDefault();
    evt.stopPropagation();

    switch (this.action) {
      case 'translate':
        this.backgroundTranslateHandler('stop', evt);
        break;
      case 'selection':
        this.createSelection('stop', evt);
        break;
      case 'move':
        this.moveElement('stop', evt);
        if (evt.target.nodeName === 'use') {
          this.handleAnnotationOver(evt.target, true);
        }
        break;
      case 'move-search':
        this.moveElement('stop', evt);
        this.moveSearchBox();
        break;
      default:
        break;
    }

    this.setActionType();
  };

  resizeElement = (className, evt) => {
    this.setActionType('selection');
    let startX = parseFloat(this.refs.selectorBox.getAttribute('x'));
    let startY = parseFloat(this.refs.selectorBox.getAttribute('y'));
    let width = parseFloat(this.refs.selectorBox.getAttribute('width'));
    let height = parseFloat(this.refs.selectorBox.getAttribute('height'));
    let x = startX;
    let y = startY;

    let rootCursor = 'nw-resize';
    if (className.indexOf('resizableTL') !== -1) {
      x = startX + width;
      y = startY + height;
    } else if (className.indexOf('resizableTR') !== -1) {
      y = startY + height;
      rootCursor = 'ne-resize';
    } else if (className.indexOf('resizableBL') !== -1) {
      x = startX + width;
      rootCursor = 'ne-resize';
    }

    this.refs.root.style.cursor = rootCursor;

    this.mouseDownPoint = {
      x: x,
      y: y
    };
    this.disableSearchBox();
  };

  handleAnnotationOver = (target, force = false) => {
    if (this.action !== undefined) return;
    let {figure: {annotations}} = this.props;
    let annotation;

    if (target.nodeName === 'use') {
      let {height, width} = target.getBoundingClientRect();
      let x = parseFloat(target.getAttribute('x'));
      let y = parseFloat(target.getAttribute('y'));
      let paddingLeft = parseFloat(target.getAttribute('data-paddingleft'));
      let paddingTop = parseFloat(target.getAttribute('data-paddingtop'));
      width = MARKER_WIDTH;
      height = MARKER_HEIGHT;

      x -= width / 2;
      y -= height / 2;

      x -= paddingLeft;
      y -= paddingTop;

      annotation = find(annotations, (el) => {
        return (
          el.x < x + width && el.x + el.width > x && el.y < y + height && el.y + el.height > y
        );
      });
    } else {
      annotation = find(annotations, {key: target.getAttribute('data-title')});
    }
    //Keep for potencial rollback to not overlaying tooltips
    let {left, top, width, height: targetHeight} = target.getBoundingClientRect();
    // let {top: rootTop, left: rootLeft} = this.refs.root.getBoundingClientRect();
    let x = left + width / 2;
    // let y = this.props.height - (top - rootTop) + 45;
    let y = top - 15;
    this.refs.tooltip.show(x, y, annotation, this.props.height, targetHeight);
  };

  /**
   * Method call hide annotation tooltip
   * @param force {boolean} - force hide
   */
  hideAnnotationToolTip = (force = false) => {
    this.refs.tooltip.hide(force);
  };

  /**
   * Method hides search box
   *
   * @return {void}
   */
  disableSearchBox = () => {
    this.setState({
      search: {
        x: 0, y: 0, width: 0, height: 0
      }
    });
  };

  /**
   * Method moves search rectangle
   *
   * @return {void}
   */
  moveSearchBox = () => {
    let x = parseFloat(this.refs.selectorBox.getAttribute('x'));
    let y = parseFloat(this.refs.selectorBox.getAttribute('y'));
    let width = parseFloat(this.refs.selectorBox.getAttribute('width'));
    let height = parseFloat(this.refs.selectorBox.getAttribute('height'));
    this.setState({
      search: {
        x, y, width, height
      }
    });
  };

  moveElement = (type, evt) => {
    let cursor = this._cursorPoint(evt);

    if (type === 'start') {
      this.refs.scale.style.pointerEvents = 'none';
      this.mouseDownPoint = cursor;
      this.elements.push(evt.target);
      this.setAttributes(evt.target, {
        'data-x': evt.target.getAttribute('x'),
        'data-y': evt.target.getAttribute('y')
      });
    } else if (type === 'process') {
      let left = (cursor.x - this.mouseDownPoint.x);
      let top = (cursor.y - this.mouseDownPoint.y);

      this.elements.forEach((el) => {
        let ratio = parseFloat(el.getAttribute('data-ratio'));

        if (ratio) {
          left = left / ratio / this.zoom;
          top = top / ratio / this.zoom;
        } else {
          let x = ~~el.getAttribute('x');
          let y = ~~el.getAttribute('y');
          let width = ~~el.getAttribute('width');
          let height = ~~el.getAttribute('height');

          if (x + left < 1) left = -x;
          else if ((left + x + width) > this.props.width) left = this.props.width - width - x;

          if (y + top < 1) top = -y;
          else if ((top + y + height) > this.props.height) top = this.props.height - height - y;
        }

        if (this.state.moveSearch === false) {
          (({x: left, y: top} = coordinatesOnAngle(left, top, this.angle)));
        }

        el.style.transform = `translate(${left}px,${top}px)`;
        if (this.state.moveSearch === true) {
          el.setAttribute('transform', `translate(${left} ${top})`);
        }
      });
    } else if (type === 'stop' || type === 'aborted') {
      this.refs.scale.style.pointerEvents = 'all';
      this.elements.forEach((el) => {
        let translate = el.style.transform;
        if (translate) {
          let [, x, y] = translate.match(/([\d.-]+).*?([\d.-]+)/);
          x = ~~x + (~~el.getAttribute('x'));
          y = ~~y + (~~el.getAttribute('y'));

          if (this.action === 'move-search' && type === 'aborted') {
            let height = ~~el.getAttribute('height');
            if (this.props.height - 30 < height + y) {
              y = this.props.height - height - 30;
            }
          }

          this.setAttributes(el, {
            x, y
          });
          if (this.state.moveSearch === true) {
            el.style.transform = '';
            el.setAttribute('transform', '');
          } else {
            el.style.transform = '';
            el.setAttribute('transform', `translate(${x} ${y}) rotate(${(this.angle * -1)}) translate(${(x * -1)} ${(y * -1)})`);
          }
        }
      });
      if (this.action === 'move-search' && type === 'aborted') {
        this.moveSearchBox();
      }
      this.elements = [];
    }
  };

  /**
   * Method creates selection rectangle
   * @return {void}
   */
  createSelectionBox = () => {
    let {selection} = this.props;
    if (selection) {
      let {maxWidth, maxHeight} = this.maxWidthAndHeight;
      let ratioX = this.props.width / maxWidth;
      let ratioY = this.props.height / maxHeight;
      let {x, y, width, height} = selection;
      x = x * ratioX;
      y = y * ratioY;
      width = width * ratioX;
      height = height * ratioY;
      let {selectorBox} = this.refs;
      this.setAttributes(selectorBox, {width, height, x, y});
    }
  };

  /**
   * Method calculates selection box before create it
   * @param type {string} - action type
   * @param evt {MouseEvent} - mouse event
   */
  createSelection = (type, evt) => {
    let cursor = this._cursorPoint(evt);
    let {selectorBox} = this.refs;
    let {figure} = this.props;
    let width = 0;
    let height = 0;
    let x = 0;
    let y = 0;

    if (type === 'start') {
      this.mouseDownPoint = cursor;
      (({x, y} = cursor));
      this.refs.root.style.cursor = 'crosshair';
    } else if (type === 'stop' || type === 'process' || type === 'aborted') {
      (({x, y} = this.mouseDownPoint));
      width = cursor.x - this.mouseDownPoint.x;
      height = cursor.y - this.mouseDownPoint.y;
      if (width < 0) {
        x += width;
        width *= -1;
      }
      if (height < 0) {
        y += height;
        height *= -1;
      }
    }

    this.setAttributes(selectorBox, {width, height, x, y});

    if (type === 'stop' || type === 'aborted') {
      this.refs.root.style.cursor = 'default';
      if (this.props.searchSelection === true) {
        // If selection was aborted
        if ((y + height) >= this.props.height) {
          height = height + (this.props.height - (height + y) - 30);
          this.setAttributes(selectorBox, {height});
        }
        if ((x + width) >= this.props.width) {
          width = width + (this.props.width - (width + x) - 10);
          this.setAttributes(selectorBox, {width});
        }

        this.setState({
          search: {
            x, y, width, height
          }
        });
      } else {
        if (width > FIGURES_ZOOM_SELECTION_LIMIT.x && height > FIGURES_ZOOM_SELECTION_LIMIT.y) {
          let figureCenter = {
            x: this.props.width / 2,
            y: this.props.height / 2
          };
          let selectionCenter = {
            x: x + width / 2,
            y: y + height / 2
          };
          let zoomX = this.props.width / width;
          let zoomY = this.props.height / height;
          let zoom = zoomX < zoomY ? zoomX : zoomY;

          if (zoom * figure.zoom > figure.zoomMax) {
            zoom = figure.zoomMax / figure.zoom;
          }

          figure.offset.x = (figure.offset.x + figureCenter.x - selectionCenter.x) * zoom;
          figure.offset.y = (figure.offset.y + figureCenter.y - selectionCenter.y) * zoom;

          figure.setZoomValue(zoom * figure.zoom);
        }
        this.clearSelection();
      }
    }
  };

  /**
   * Method hide selection
   * @return {void}
   */
  clearSelection = () => {
    let {selectorBox} = this.refs;
    this.setAttributes(selectorBox, {
      width: 0,
      height: 0,
      x: 0,
      y: 0
    });
  };

  /**
   * Method run figures search
   * @return {void}
   */
  triggerSearchRun = () => {
    let {
      state: {search},
      props: {
        figure: {
          cropped,
          offset: {
            x: offsetX,
            y: offsetY
          }
        },
        notify,
        figureSearch
      },
      translate: {
        x: translateX,
        y: translateY
      },
      image
    } = this;

    let zoom = image.ratio * this.zoom;
    let _search = {
      x: search.x,
      y: search.y,
      height: search.height,
      width: search.width,
      offsetX: offsetX,
      offsetY: offsetY
    };

    if (this.angle === 180) {
      _search.x = this.props.width - search.x - search.width;
      _search.y = this.props.height - search.y - search.height;
      _search.offsetX = offsetX * -1;
      _search.offsetY = offsetY * -1;
    } else if (this.angle === 90 || this.angle === 270) {
      let x;
      let y;
      _search.offsetX = offsetY;
      _search.offsetY = offsetX;
      if (this.angle === 90) {
        x = Number(search.y);
        y = Number(this.props.width - search.x - search.width);
        _search.offsetY *= -1;
      } else if (this.angle === 270) {
        x = Number(this.props.height - search.y - search.height);
        y = Number(search.x);
        _search.offsetX *= -1;
        // _search.offsetY *= -1;
      }
      _search.y = y;
      _search.x = x;

      translateX -= (this.props.width - this.props.height) / 2;
      translateY = 0;

      _search.width = search.height;
      _search.height = search.width;
    }

    let x1 = _search.x - _search.offsetX - translateX;
    let y1 = _search.y - _search.offsetY - translateY;

    let x2;
    let y2;

    x1 = x1 / zoom;
    y1 = y1 / zoom;

    x2 = x1 + (_search.width / zoom);
    y2 = y1 + (_search.height / zoom);

    let markers = [...this.refs.scale.querySelectorAll('use')].map(el => {
      return {
        marker: el.getAttribute('data-marker'),
        x: el.getAttribute('x') * 1 - MARKER_WIDTH / (2 * zoom),
        y: el.getAttribute('y') * 1 - MARKER_HEIGHT / (2 * zoom)
      };
    });

    markers = markers.filter(el => {
      return el.x < x2 && (el.x + MARKER_WIDTH / zoom) > x1 && el.y < y2 && (el.y + MARKER_HEIGHT / zoom) > y1;
    });

    x1 += cropped.x;
    y1 += cropped.y;
    x2 += cropped.x;
    y2 += cropped.y;

    if (markers.length === 0) {
      notify({
        message: 'Please make sure there are markers within the figure selection.',
        level: levels.warning,
        timeout: 3000
      });
    } else {
      let markerPositions = {};
      markers.forEach((marker) => {
        if (markerPositions[`m${marker.marker}`] === undefined) markerPositions[`m${marker.marker}`] = [];
        markerPositions[`m${marker.marker}`].push(
          [
            marker.x + cropped.x,
            marker.y + cropped.y
          ]
        );
      });
      let selection = {
        sx: x1,
        sy: y1,
        ex: x2,
        ey: y1,
        w: x2 - x1,
        h: y2 - y1
      };

      figureSearch({
        markerPositions,
        selection,
        imageUrl: this.props.figure.source.replace('&crop=-1', ''),
        anns: markers.map(el => {
          return {x: el.x + (cropped.x), y: el.y + (cropped.y), title: el.marker};
        }),
        figure: markerPositions,
        figureSize: {height: this.props.figure.maxOriginalHeight, width: this.props.figure.maxOriginalWidth}
      })
      .catch(error => {
        console.warn('Figure.triggerSearchRun.error', error);
        notify({
          message: error || 'Unable to launch figure search',
          level: levels.warning,
          timeout: 3000
        });
      });
    }
  };

  triggerSearchClose = () => {
    this.clearSelection();
    this.disableSearchBox();
  };

  handleSelectorBoxMouseDown = () => {
    this.setState({moveSearch: true});
  };

  handleSelectorBoxMouseUp = () => {
    this.setState({moveSearch: false});
  };

  handleResetZoom = () => {
    if (this.props.isStatic !== true) {
      this.props.figure.zoomReset();
    }
  };

  handleImageLoadingSuccess = () => {
    if (this.props.url) {
      if (typeof this.props.onLoad === 'function') {
        this.props.onLoad(this.props.figure.source);
      }
      this.setState({
        loaded: true,
        error: false
      });
    }
  };
  handleImageLoadingError = () => {
    if (this.props.loadState.loaded === true) {
      if (typeof this.props.onError === 'function') {
        this.props.onError(this.props.figure.source);
      }
      this.setState({
        loaded: true,
        error: true
      });
    }
  };

  _imageVisibility = (source) => {
    return this.state.loaded === true ? `visible` : `hidden`;
  };

  _imageLoadingError = () => {
    if (this.props.hideError === true) return null;
    return <span className={classes.loadingError}>Problem loading image</span>;
  };

  render () {
    let {height, width, figure: {zoom, active, offset, sheet}, isStatic, isThumb, searchSelection, solid, offset: position, size, loadState, url} = this.props;
    let {maxHeight} = this.maxWidthAndHeight;
    let {search} = this.state;
    this.image = this.imageDimensions(this.translateScale);
    let {x: transformX, y: transformY} = this._calculateTranslate(this.translateScale);
    let {x: annTransformX, y: annTransformY} = this._calculateTranslate(1);
    let prefix = this.props.prefix || `prefix`;
    prefix += sheet;
    let source = this.source;
    let iWidth = this.image.width * this.zoom;
    let iHeight = this.image.height * this.zoom;

    const figureElement = (
        <div
          className={classes.imgRoot}
          ref='img'
          style={{
            width,
            height,
            visibility: this._imageVisibility(source)
          }}
        >
          {loadState.error === true
            ? this._imageLoadingError()
            : <div
              className={classes.imgBackground}
              ref={`imgBackground`}
              style={{
                width: iWidth,
                height: iHeight,
                transform: `translate(${isStatic ? 0 : offset.x}px, ${isStatic ? 0 : offset.y}px)`
              }}
            >
              <div
                style={{
                  width: iWidth,
                  height: iHeight,
                  transform: `translate(${transformX}px, ${transformY}px)`
                }}
              >
                <div
                  className={classes.imgRotate}
                  style={{
                    transform: `rotate(${this.state.error === true ? 0 : this.angle}deg)`
                  }}
                >
                  <div
                    style={{
                      transform: `scale(${this.zoomScale})`,
                      width: iWidth < size.width ? size.width : iWidth,
                      height: iHeight > size.height ? size.height - 1 : iHeight,
                      overflow: `hidden`
                    }}
                  >
                    <img
                      style={{
                        left: `-${position}px`,
                        maxHeight: 'none',
                        maxWidth: 'none',
                        position: `absolute`
                      }}
                      ref='imgImg'
                      src={url}
                      alt={``}
                      className={this.state.loaded === true ? `loaded` : `loading`}
                      height={iHeight < size.maxHeight ? size.maxHeight : iHeight}
                      width={iWidth < size.maxWidth ? size.maxWidth : iWidth}
                      onLoad={this.handleImageLoadingSuccess}
                      onError={this.handleImageLoadingError}
                      onAbort={this.handleImageLoadingError}
                    />
                  </div>
                </div>
              </div>
            </div>
          }
        </div>
    );

    if (isThumb) {
      return figureElement;
    }

    return (
      <div
        className={`${cx({
          active: active === true,
          loadingErrorContainer: loadState.error === true
        })} ${this.state.error === true ? 'loadingError' : ''}`}
        onMouseLeave={this.handleMouseLeave}
        onMouseEnter={this.handleMouseEnter}
        onDoubleClick={this.handleResetZoom}
      >
        <MarkersSVG
          markers={this.props.markers}
          width={MARKER_WIDTH / (this.zoom * this.image.ratio)}
          prefix={prefix}
          solid={solid}
        />
        {this.state.loaded === false
          ? <div>
            {this.props.disableSpinner === true
              ? null : <Spinner
                loading={Boolean(true)}
                height='50px'
                width='5px'
                delay={100}
              />}
          </div>
          : null}

        {figureElement}

        <svg
          ref='root'
          className={cx({
            root: true,
            searchSelection: searchSelection === true && this.state.search.width === 0,
            deleteMarkers: this.props.deleteMarker
          })}
          width={width}
          height={height}
          viewBox={`0 0 ${width} ${height}`}
          onMouseDown={this.handleMouseDown}
          onMouseMove={this.handleMouseMove}
          onMouseUp={this.handleMouseUp}
          onContextMenu={this.handleContextMenu}
          data-zoom={zoom}
          xmlns={`http://www.w3.org/2000/svg`}
          xmlnsXlink={`http://www.w3.org/1999/xlink`}
          style={{
            visibility: this.state.loaded === false || this.state.error === true ? `hidden` : `visible`
          }}
        >
          <g ref='background' transform={`translate(${isStatic ? 0 : offset.x} ${isStatic ? 0 : offset.y})`}>
            <g ref='transform' transform={`translate(${annTransformX} ${annTransformY})`}>
              <g
                ref='rotate'
                transform={this._calculateRotate()}
                className={cx({
                  disableMarkers: (searchSelection && search.width === 0)
                })}
              >
                <g ref='image' className={cx({image: true})} />
                <g ref='scale' transform={`scale(${this.zoom})`}>
                  {this.state.error !== true ? <FigureAnnotations
                    active={this.state.active}
                    angle={this.angle}
                    annotations={this.props.figure.annotations}
                    deleteMarker={this.props.deleteMarker}
                    figureMaxHeight={maxHeight}
                    handleMouseOver={this.handleAnnotationOver}
                    handleMouseOut={this.hideAnnotationToolTip}
                    height={this.props.height}
                    hideToolTip={this.props.hideToolTip || false}
                    markers={this.props.markers}
                    ratio={this.image.ratio}
                    cropped={this.croppingValues}
                    stamps={this.props.isStatic ? [] : this.props.figure.stamps}
                    prefix={prefix}
                    scale={this.translateScale}
                    length={this.props.figure.annotations.length}
                  /> : <g />}
                </g>
              </g>
            </g>
          </g>
          <g className={classes.selection}>
            <rect
              ref='selectorBox'
              className={cx({
                selectionSearch: searchSelection === true,
                selectionZoom: searchSelection === false,
                selectionMove: this.state.moveSearch
              })}
              onMouseDown={this.handleSelectorBoxMouseDown}
              onMouseUp={this.handleSelectorBoxMouseUp}
            />
            <FigureSearchToolBar
              ref='searchToolBar'
              x={search.x}
              y={search.y}
              width={search.width}
              height={search.height}
              triggerSearchRun={this.triggerSearchRun}
              triggerSearchClose={this.triggerSearchClose}
            />
          </g>
        </svg>
        <FigureToolTip
          ref='tooltip'
        />
      </div>
    );
  }
}

Figure.propTypes = {
  figure: PropTypes.object,
  insert: PropTypes.any,
  height: PropTypes.number,
  hideToolTip: PropTypes.bool,
  markers: PropTypes.array,
  searchSelection: PropTypes.bool,
  trackMouse: PropTypes.bool,
  isStatic: PropTypes.bool,
  finIn: PropTypes.bool,
  deleteMarker: PropTypes.bool,
  skipOrientation: PropTypes.bool,
  hideError: PropTypes.bool,
  zoom: PropTypes.number,
  onMousePosition: PropTypes.func,
  width: PropTypes.number,
  prefix: PropTypes.string,
  solid: PropTypes.bool,
  crop: PropTypes.bool,
  selection: PropTypes.object,
  disableSpinner: PropTypes.bool,
  onLoad: PropTypes.func,
  onError: PropTypes.func,
  isSprite: PropTypes.bool,
  isThumb: PropTypes.bool,
  size: PropTypes.object,
  offset: PropTypes.number,
  loadState: PropTypes.object,
  url: PropTypes.string,
  figureZoom: PropTypes.number,
  urlZoom: PropTypes.number
};

export default Figure;
