import PropTypes from 'prop-types';
import React, { Component } from 'react';
import MarkerSVG from './MarkerSVG';
import classes from './MarkersSVG.scss';
import {MARKER_WIDTH, MARKER_HEIGHT} from '@epo/web-common';

class MarkersSVG extends Component {
  render () {
    let markerWidth = this.props.width || MARKER_WIDTH;
    let markerHeight = this.props.width || MARKER_HEIGHT;
    let {markers, prefix, solid} = this.props;

    return (<svg
      className={classes.root}
      xmlns={`http://www.w3.org/2000/svg`}
      xmlnsXlink={`http://www.w3.org/1999/xlink`}
    >
      <defs>
        {markers.map((marker) => {
          return (<g key={'marker-group-' + marker.name}>
            <MarkerSVG
              name={marker.name}
              prefix={prefix}
              deletion={Boolean(false)}
              userAdded={Boolean(false)}
              width={markerWidth}
              height={markerHeight}
              bgcolor={marker.bgcolor}
              color={marker.color}
              solid={solid}
            />
            <MarkerSVG
              name={marker.name}
              prefix={prefix}
              deletion={Boolean(false)}
              userAdded={Boolean(true)}
              width={markerWidth}
              height={markerHeight}
              bgcolor={marker.bgcolor}
              color={marker.color}
              solid={solid}
            />
            <MarkerSVG
              name={marker.name}
              prefix={prefix}
              deletion={Boolean(true)}
              userAdded={Boolean(false)}
              width={markerWidth}
              height={markerHeight}
              bgcolor={marker.bgcolor}
              color={marker.color}
              solid={solid}
            />
            <MarkerSVG
              name={marker.name}
              prefix={prefix}
              deletion={Boolean(true)}
              userAdded={Boolean(true)}
              width={markerWidth}
              height={markerHeight}
              bgcolor={marker.bgcolor}
              color={marker.color}
              solid={solid}
            />
          </g>);
        })}
      </defs>
    </svg>);
  }
}

export default MarkersSVG;

MarkersSVG.propTypes = {
  markers: PropTypes.array,
  width: PropTypes.number.isRequired,
  prefix: PropTypes.string,
  solid: PropTypes.bool
};

MarkersSVG.defaultProps = {
  markers: [],
  prefix: 'prefix',
  solid: false
};
