import {observable} from 'mobx';
import {Publication} from './index';

/**
 * Publications Store
 *
 * @export
 * @class PublicationsStore
 */
export default class PublicationsStoreModel {
  @observable publications = {};

  /**
   * Construct an instance of a model
   * @param {Object} api - an model's API
   */
  constructor (api) {
    this.api = api;
  }

  /**
   * Add new publication to Publications store
   * @param {Object} object - publication details
   * @param {string} object.familyId - publication family id @example 045798243
   * @param {string} object.publicationId - publication id @example 352990458
   * @param {string} object.publicationIdShort - publication Short ID @example CN202162391U
   * @param {Array} object.figures - figures array
   * @param {function} object.onCreate - callback function
   * @param {boolean} object.preLoaded - is figures pre loaded
   * @param {Array|undefined} object.annotations - annotations array
   * @param {Array|undefined} object.refs - references keys
   * @param {Array|undefined} object.refPhrases - references values
   * @param {String|undefined} object.publicationNumber - publicationNumber
   * @memberOf PublicationsStore
   */
  createPublication = ({familyId, publicationId, publicationIdShort, figures, onCreate, preLoaded, annotations, refs, refPhrases, publicationNumber, documentType, viewerType}) => {
    if (this.publications[publicationId] === undefined) {
      this.publications[publicationId] = new Publication(
        familyId,
        publicationId,
        publicationIdShort,
        figures,
        [],
        onCreate,
        preLoaded,
        annotations,
        refs,
        refPhrases,
        publicationNumber,
        documentType,
        viewerType,
        this.api
      );
    } else {
      if (typeof onCreate === 'function') {
        let {figures} = this.publications[publicationId];
        onCreate({figures: [...figures]});
      }
    }
  };

  /**
   * Get publication by id
   *
   * @param {string} publicationId
   * @returns {Publication} - return Publication
   *
   * @memberOf PublicationsStore
   */
  getById (publicationId) {
    return this.publications[publicationId];
  }

  /**
   * Remove publication by id
   *
   * @param {string} publicationId
   *
   * @memberOf PublicationsStore
   */
  remove = (publicationId) => {

  };
}
