import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer} from 'mobx-react';
import {
  Button,
  ButtonGroup,
  Glyphicon,
  MenuItem
} from 'react-bootstrap';
import FontAwesome from 'react-fontawesome';
import ReactBootstrapSlider from 'react-bootstrap-slider';
import AutoFlipSpeedSlider from '../AutoFlipSpeedSlider';

import classes from './FiguresControls.scss';

const titles = {
  deleteAnnotation: `Delete Marker Annotations`,
  insertAnnotation: `Stamp for marker `,
  searchSelection: `Selection tool`
};

@observer
class FiguresControls extends Component {
  state = {
    actionDropdown: false
  };

  componentWillUnmount () {
    this.props.store.disableInsert();
  }

  /**
   * Increase Active figure zoom.
   */
  handleZoomIn = () => {
    this.props.store.updateActiveFigureZoom('in');
  };

  /**
   * Decrease Active figure zoom.
   */
  handleZoomOut = () => {
    this.props.store.updateActiveFigureZoom('out');
  };

  /**
   * Reset Active figure zoom.
   */
  handleZoomReset = () => {
    this.props.store.updateActiveFigureZoom('reset');
  };

  /**
   * Rotate Active figure by 90 deg.
   */
  handleRotate = () => {
    this.props.store.updateActiveFigureAngle(90);
  };

  handleAutoFlipPause = () => {
    this.props.store.updateAutoFlipping(false);
    this.props.store.updateAutoFlippingContinue(false);
    this.props.store.autoFlipCounter = 0;
  };

  /**
   * Remove annotations from figure
   */
  handleRemoveAnnotation = () => {
    this.props.store.toggleDelete();
  };

  /**
   * Change Figure representative mode
   * @param event{Event} - click event
   */
  handleChangeMode = (event) => {
    this.changeMode(parseInt(event.target.value, 10));
  };

  changeMode = (value) => {
    this.props.store.setMode(value);
  };

  /**
   * Toggle Figure Search Selection
   */
  handleSelectFigureSearch = () => {
    let {store, store: {selection}} = this.props;
    store.updateSelection(!selection);
  };

  /**
   * Toggle figure Legend
   */
  handleLegend = () => {
    let {store, store: {legend}} = this.props;
    store.setLegend(!legend);
  };

  /**
   * Handle activate auto-flipper
   */
  handleAutoFlipStart = () => {
    this.props.store.updateAutoFlipping(true);
    this.props.store.startAutoFlipping(true);
  };

  updateDenominator = () => {
    let {store: {denominator, denominatorMinValue, updateDenominator, autoFlipping}} = this.props;
    if (autoFlipping !== false || denominator === denominatorMinValue) updateDenominator(denominator * 2);
    this.handleAutoFlipStart();
  };

  handleCreateMarker = (markerName) => {
    let value = false;
    let {store: {insert, enableInsert, disableInsert}} = this.props;
    if (insert !== markerName) {
      value = markerName;
      enableInsert(value);
    } else {
      disableInsert();
    }
  };

  _onSelect = (a, b, c, d) => {

  };

  handleActionsDropdown = () => {
    this.setState({
      actionDropdown: !this.state.actionDropdown
    });
  };

  render () {
    let {
      store: {
        autoFlipping,
        autoFlippingContinue,
        denominator,
        denominatorMinValue,
        insert,
        deleteMarker,
        legend,
        mode,
        full,
        selection
      },
      markers,
      getShortcutCombination
    } = this.props;
    const isMosaic = mode !== 1;
    const activeDropDown = this.state.actionDropdown === true && !isMosaic;

    return (
      <div className={classes.root} ref='root'>
        <ButtonGroup>
          <ButtonGroup className={classes.dropDownButtons}>
            <Button
              onClick={this.handleActionsDropdown}
              disabled={isMosaic}
              style={{
                color: activeDropDown ? '#62AAC2' : '#555'
              }}
            >
              <span className={classes.iconFigureControls} />
            </Button>

            <ul
              className={`dropdown-menu open ${classes.dropdown}`}
              style={{
                display: activeDropDown ? 'block' : 'none'
              }}
            >
              <MenuItem
                onSelect={this.handleSelectFigureSearch}
                active={selection}
                title={titles.searchSelection}
              >
                <svg
                  xmlns={`http://www.w3.org/2000/svg`}
                  width={16}
                  height={16}
                  viewBox={`0 0 16 16`}
                  className={classes.searchSelection}
                >
                  <path
                    d={`
                      M0 0h3v1h-3ZM5 0h6v1h-6ZM13 0h3v1h-3ZM0 15h3v1h-3ZM5 15h6v1h-6ZM13 15h3v1h-3ZM0 1v2h1v-2ZM0 5v6h1v-6ZM0 13v2h1v-2ZM15 1v2h1v-2ZM15 5v6h1v-6ZM15 13v2h1v-2Z
                    `}
                    fill={selection === true ? `#fff` : `#555`}
                  />
                </svg>
              </MenuItem>
              <MenuItem
                onClick={this.handleRemoveAnnotation}
                active={Boolean(deleteMarker)}
                title={titles.deleteAnnotation}
              >
                <Glyphicon glyph='remove' />
              </MenuItem>
              {markers && markers.filter(marker => marker.status === 0).map((marker) => {
                return (
                  <MenuItem
                    key={marker.name}
                    className={classes.markers}
                    onClick={() => this.handleCreateMarker(marker.name)}
                    title={titles.insertAnnotation + marker.name}
                    style={marker.name === insert ? {
                      background: marker.bgcolor,
                      color: marker.color
                    } : {}}
                  >
                    {marker.name}
                  </MenuItem>
                );
              })}
            </ul>
          </ButtonGroup>
          <Button
            bsStyle={legend === true && !isMosaic ? 'primary' : 'default'}
            disabled={isMosaic}
            onClick={this.handleLegend}
            title={`Toggle annotations visibility (${getShortcutCombination('TOGGLE_ANNOTATIONS_VISIBILITY')})`}
          >
            <FontAwesome
              name='info'
              className={classes.awesomeIcons}
            />
          </Button>
          <Button
            disabled={isMosaic && full === 0}
            onClick={this.handleRotate}
            title={`Rotate drawing (${getShortcutCombination('ROTATE_FIGURE_CLOCKWISE')}, ${getShortcutCombination('ROTATE_FIGURE_ANTICLOCKWISE')} for anticlockwise rotation)`}
          >
            <FontAwesome
              name='repeat'
              className={classes.awesomeIcons}
            />
          </Button>
          <Button
            disabled={isMosaic && full === 0}
            onClick={this.handleZoomIn}
            title={`Zoom in on drawing (${getShortcutCombination('FIGURE_ZOOM_IN')})`}
          >
            <FontAwesome
              name='search-plus'
              className={classes.awesomeIcons}
            />
          </Button>
          <Button
            disabled={isMosaic && full === 0}
            onClick={this.handleZoomOut}
            title={`Zoom out on drawing (${getShortcutCombination('FIGURE_ZOOM_OUT')})`}
          >
            <FontAwesome
              name='search-minus'
              className={classes.awesomeIcons}
            />
          </Button>
          <Button
            disabled={isMosaic && full === 0}
            onClick={this.handleZoomReset}
            title={`Original zoom level (${getShortcutCombination('FIGURE_ZOOM_MAX')})`}
          >
            <FontAwesome
              name='arrows-alt'
              className={classes.awesomeIcons}
            />
          </Button>
          <AutoFlipSpeedSlider store={this.props.store} />
          <button
            type='button'
            className={`btn btn-${autoFlipping === false && autoFlippingContinue === false ? 'primary' : 'default'} autoFlip-event-allowed`}
            title={`Pause/resume the figures autoflip (${getShortcutCombination('PAUSE_RESUME_AUTOFLIP')})`}
            onClick={this.handleAutoFlipPause}
          >
            <FontAwesome
              name='pause'
              className={classes.awesomeIcons}
            />
          </button>
          <button
            type='button'
            className={`btn btn-${(autoFlipping === true || autoFlippingContinue === true) && denominator !== denominatorMinValue ? 'primary' : 'default'} autoFlip-event-allowed`}
            onClick={this.updateDenominator}
            title={`Increase the autoflip speed multiplier setting(2x, 4x, 8x), (${getShortcutCombination('INCREASE_AUTOFLIP_MULTIPLIER')})`}
          >
            <FontAwesome
              name='forward'
              className={classes.awesomeIcons}
            />
            <span className={classes.denominator}>
              {denominator !== denominatorMinValue ? denominator : ''}
            </span>
          </button>

        </ButtonGroup>
        <div className={classes.modesWrap}>
          <Button
            onClick={() => this.changeMode(1)}
            className={classes.modeButtonsLeft}
          >
            <svg
              xmlns={`http://www.w3.org/2000/svg`}
              width={15}
              height={15}
              viewBox={`0 0 15 15`}
            >
              <path
                d={`M0,0h15v1h-15ZM0,14h15v1h-15ZM0,1v13h1v-13ZM14,1v13h1v-13Z`}
                fill={`#555`}
              />
            </svg>
          </Button>
          <div
            className={classes.mode}
            title={`Increase (${getShortcutCombination('FIGURES_MOSAIC_BACKWARD')}) or decrease (${getShortcutCombination('FIGURES_MOSAIC_FORWARD')}) the mosaic view slider by one step`}>
            <ReactBootstrapSlider
              change={this.handleChangeMode}
              max={5}
              min={1}
              step={1}
              ticks={[1, 2, 3, 4, 5]}
              tooltip={`hide`}
              value={mode}
            />
          </div>
          <Button
            onClick={() => this.changeMode(5)}
            className={classes.modeButtonsRight}
          >
            <svg
              xmlns={`http://www.w3.org/2000/svg`}
              width={15}
              height={15}
              viewBox={`0 0 15 15`}
            >
              <path
                d={`M0,0h15v1h-15ZM0,14h15v1h-15ZM1,7h13v1h-13ZM0,1v13h1v-13ZM14,1v13h1v-13ZM7,1v6h1v-6ZM7,8v6h1v-6Z`}
                fill={`#555`}
              />
            </svg>
          </Button>
        </div>
      </div>
    );
  }
}

FiguresControls.propTypes = {
  store: PropTypes.object,
  markers: PropTypes.array,
  getShortcutCombination: PropTypes.func.isRequired
};

export default FiguresControls;
