import {action, observable, transaction, when} from 'mobx';
import {Annotation} from './index';
import unique from 'lodash.uniq';

/**
 * Figure Item
 *
 * @export
 * @class Figure
 */
export default class FigureModel {
  @observable active = false;
  @observable filtered = false;
  /* when applied external filters */
  lang;
  /* figure language - not used */
  @observable source;
  /* image url*/
  facsimileId;
  /* fascimileId of the figure */
  sprite;
  /* array of [facsimileId, page]*/
  spriteSource;
  /* image sprite url*/
  @observable offset = {
    x: 0, /* left shift when image was moved from default position */
    y: 0 /* top shift when image was moved from default position */
  };
  figure;
  orientation;
  /* ORC orientation data: 0 - not rotated, 1 - rotated on pi/2 */
  reliability;
  sheet;
  @observable angle;
  /* 0 - 359 degree */
  @observable mosaicAngle;
  /* 0 - 359 degree */
  @observable zoom = 1;
  /* figure current zoom level (limited in this.zoomMin and this.zoomMax )*/
  boxes;
  @observable annotations = [];
  /* OCR annotations */
  @observable annLength = 0;
  /* size of the annotations array */
  @observable stamps = [];
  /* user annotations */
  @observable markers = [];
  /* markers which was highlighted for current figure*/
  zoomMax = 100;
  /* max possible image zoom */
  zoomMin = 1;
  /* min possible image zoom */
  zoomInitial = 1;
  /* default zoom level*/
  zoomStep = 0.25;
  /* zooming step */
  maxHeight;
  /* max image height when you not apply height attribute in source*/
  maxWidth;
  /* max image width when you not apply height attribute in source*/
  maxOriginalHeight;
  /* original image height before applying cropping - needed to normalize cropping and annotations positions */
  maxOriginalWidth;
  /* original image width before applying cropping - needed to normalize cropping and annotations positions */
  @observable cropped = {
    x: 0, /* left cropping */
    y: 0, /* top cropping */
    fetched: 0 /* 0 - not fetched, 1- fetched, -1 - was an error fetching cropping data*/
  };
  fetchingCropping = false;
  /* when in process of fetching cropping data */
  type;
  callbacks = [];

  /* type of image - biblio, description etc. */

  /**
   * Creates an instance of Figure.
   *
   * @param {string} lang - figure language
   * @param {string} source - figure link to image
   * @param {string} figure - figure order in publication
   * @param {number} orientation - figure orientation (0 - turn to 90 deg, 1 - do nothing)
   * @param {number} reliability - figure reliability
   * @param {number} sheet - figure sheet number
   * @param {number} angle - figure angle (by default based on @param orientation)
   * @param {number} zoom - figure zoom level
   * @param {Object} max - figure maximal height and height
   * @param {number} max.width - figure maximal width
   * @param {number} max.height - figure maximal height
   * @param {Array} [annotations=[]] - figure annotations array
   * @param {string} [type] - figure type
   * @param {Object} api - a model's API
   *
   * @memberOf Figure
   */
  constructor (lang, source, figure, orientation, reliability, sheet, angle, zoom, max, annotations = [], type, api) {
    this.active = false;
    this.lang = lang;
    this.source = source;
    this.figure = figure;
    this.orientation = orientation;
    this.reliability = reliability;
    this.sheet = sheet;
    this.angle = angle;
    this.zoom = zoom;
    this.type = type;
    this.api = api;
    this.annotationsMap = {};
    let isSwapped = false;
    if (this.orientation === 0 && this.angle === 0) {
      this.angle = 90;
      isSwapped = true;
    }
    this.mosaicAngle = this.angle;
    if (annotations.length > 0) {
      transaction(() => {
        annotations.forEach((annotation) => {
          this.addAnnotation({
            key: annotation.key,
            text: annotation.text,
            x: annotation.x,
            y: annotation.y,
            width: !isSwapped ? annotation.width : annotation.height,
            height: !isSwapped ? annotation.height : annotation.width
          });
        });
      });
      this.annLength = this.annotations.length;
    }
  }

  /**
   * Add annotation to Figure
   * @param key{string} - annotation key
   * @param text{string} - annotation text
   * @param x{number} - annotation x coordinate
   * @param y{number} - annotation y coordinate
   * @param width{number} - annotation width
   * @param height{number} - annotation height
   */
  addAnnotation = ({key, text, x, y, width, height}) => {
    let annotationsMapKey = `${key}-${x}-${y}`;
    if (!this.annotationsMap[annotationsMapKey]) {
      this.annotations.push(new Annotation(key, text, x, y, width, height));
      this.annotationsMap[annotationsMapKey] = true;
    }
  };

  /**
   * Update Annotations Text by refs
   *
   * @param {object} refs - reference phrases
   * @param {object} markers - annotation markers
   * @memberOf Figure
   */
  updateAnnotationsText = (refs, markers) => {
    let markersList = [];
    transaction(() => {
      this.annotations.forEach((annotation) => {
        let annotationMarkers = annotation.updateText(refs, markers);
        annotationMarkers.forEach((marker) => {
          markersList.push(marker);
        });
      });
    });

    this.markers = unique(markersList);
  };

  /**
   * Zoom in figure by zoomStep value
   *
   * @memberOf Figure
   */
  zoomIn = () => {
    let zoom = this.zoom + this.zoomStep;
    this.zoom = (zoom < this.zoomMax ? zoom : this.zoomMax);
  };

  /**
   * Zoom out figure by zoomStep value
   *
   * @memberOf Figure
   */
  zoomOut = () => {
    let zoom = this.zoom - this.zoomStep;
    this.zoom = (zoom > this.zoomMin ? zoom : this.zoomMin);
  };

  /**
   * Reset figure zoom to zoomInitial value
   *
   * @memberOf Figure
   */
  zoomReset = () => {
    this.zoom = this.zoomInitial;
    this.offset = {x: 0, y: 0};
  };

  /**
   * Update Figure zoom by value
   *
   * @param {number} value - zoom value
   *
   * @memberOf Figure
   */
  @action setZoomValue = (value) => {
    if (!isNaN(parseFloat(String(value))) && isFinite(value)) {
      if (value < this.zoomMin) value = this.zoomMin;
      else if (value > this.zoomMax) value = this.zoomMax;
      this.zoom = value;
    }
  };

  /**
   * Update figure zoom
   * @param {string} action - Action to zoom in, zoom out or reset figure zoom
   *
   * @memberOf Figure
   */
  updateZoom = (action) => {
    if (action === 'in') {
      this.zoomIn();
    } else if (action === 'out') {
      this.zoomOut();
    } else if (action === 'reset') {
      this.zoomReset();
    }
  };

  /**
   * Update figure angle
   * @param {number} deg - angle to turn figure
   *
   * @memberOf Figure
   */
  updateAngle = (deg) => {
    let angle = this.angle + deg;
    if (angle < 0) angle = 360 + deg;
    if (angle > 360) angle = Math.abs(angle % 360);
    this.angle = angle;
  };

  getMarkers () {
    let markers = [];
    this.annotations.forEach((annotation) => {
      annotation.markers.forEach((marker) => {
        if (markers.indexOf(marker) === -1) {
          markers.push(marker);
        }
      });
    });
    return markers;
  }

  getCropping () {
    if (this.cropped.fetched === 0 && this.fetchingCropping === false) {
      this.fetchingCropping = true;
      return this.api.fetchFigureHead(this.source)
        .then(response => {
          let crop = response.headers.get('X-Image-Meta-Crop-Rectangle');
          let originalWidth = response.headers.get('X-Image-Meta-Original-Width');
          let originalHeight = response.headers.get('X-Image-Meta-Original-Height');

          if (crop) {
            let [x, y, width, height] = crop.split(',');
            transaction(() => {
              this.maxWidth = Number(width);
              this.maxHeight = Number(height);
              this.updateOriginals(originalWidth, originalHeight);
              this.cropped.x = Number(x);
              this.cropped.y = Number(y);
              this.cropped.fetched = 1;
              this.updateAnnotations();
            });
          } else if (originalWidth && originalHeight) {
            transaction(() => {
              this.updateOriginals(originalWidth, originalHeight);
            });
            this.cropped.fetched = -1;
          } else {
            this.cropped.fetched = -1;
          }

          this.fetchingCropping = false;
        })
        .catch(err => {
          this.cropped.fetched = -1;
          console.warn('FiguresModel.getCropping', err);
          this.fetchingCropping = false;
        });
    } else if (this.cropped.ann_fetched === 0 && this.cropped.fetched === 1) {
      transaction(() => {
        this.updateAnnotations();
      });
      return new Promise((resolve) => {
        resolve(this.cropped.fetched === 1);
      });
    } else if (this.fetchingCropping === true) {
      return new Promise((resolve) => {
        when(() => this.cropped.fetched !== 0, () => {
          resolve(this.cropped.fetched === 1);
        });
      });
    } else {
      return new Promise((resolve) => {
        resolve();
      });
    }
  }

  /**
   * Method updates model originals
   * @param originalWidth
   * @param originalHeight
   */
  updateOriginals (originalWidth, originalHeight) {
    this.maxOriginalWidth = Number(originalWidth);
    this.maxOriginalHeight = Number(originalHeight);
  }

  updateAnnotations () {
    this.cropped.ann_fetched = !this.annotations.length ? 0 : 1;
    this.annotations = this.annotations.map(ann => {
      ann.x -= this.cropped.x;
      ann.cx -= this.cropped.x;
      ann.y -= this.cropped.y;
      ann.cy -= this.cropped.y;
      return ann;
    });
  }
}
