import {observer, inject} from 'mobx-react';
import {transaction} from 'mobx';
import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {
  getImageKey,
  extendSpriteSource,
  getSpriteBody,
  getSpriteWidth,
  getSpriteHeight,
  getSpriteZoom,
  getMaxZoom,
  getMaxFigureZoom
} from 'Figures.Helper';

import Figure from './Figure';

import {
  FIGURE_DEFAULT_ZOOM,
  SPRITE_LIMIT,
  RLIST_ITEM_SMALL_HEIGHT,
  RLIST_ITEM_SMALL_WIDTH,
  RLIST_ITEM_SMALL_CORRECTION
} from '@epo/web-common';

@inject((stores) => stores.figuresStore ? {
  figuresStore: stores.figuresStore
} : {})
@observer
class SpriteFigure extends Component {
  static defaultProps = {
    skipOrientation: false,
    zoom: FIGURE_DEFAULT_ZOOM,
    figureZoom: FIGURE_DEFAULT_ZOOM,
    angle: 0,
    mode: 0,
    annotationsLength: 0
  };

  state = {
    loaded: false,
    error: false
  };

  blobUrl = '';
  sizes = {};
  offset = 0;
  mounted = false;
  urlZoom = 1;
  urlFigureZooms = new Map();

  UNSAFE_componentWillMount () {
    this.sizes = {height: this.props.height, width: this.props.width};
  }

  componentDidMount () {
    this.mounted = true;
    const {
      figure,
      width,
      height,
      zoom,
      figureZoom,
      angle,
      isStatic,
      isSprite,
      crop,
      figuresStore
    } = this.props;
    figuresStore.cache.addImage(this._getImageKey(this.props), this.getRequestSource(this.props), this.getRotation(this.props), this.getRequestBody(this.props), {
      callback: this.addImageHandler,
      key: this.getRequestKey(figure.facsimileId, figure.sheet, width, height, zoom, figureZoom, angle, isStatic, isSprite, crop)
    });
  }

  componentWillUnmount () {
    this.mounted = false;
  }

  UNSAFE_componentWillReceiveProps (nextProps) {
    const {
      isSprite,
      angle,
      figure: {source, facsimileId, sheet},
      figuresStore
    } = this.props;

    if (this.isNewImageRequest(nextProps)) {
      figuresStore.cache.addImage(this._getImageKey(nextProps), this.getRequestSource(nextProps), this.getRotation(nextProps), this.getRequestBody(nextProps));
    }
    if (nextProps.isSprite !== isSprite || nextProps.angle !== angle || nextProps.figure.source !== source || nextProps.figure.facsimileId !== facsimileId || nextProps.figure.sheet !== sheet) {
      this.blobUrl = '';
      this.urlZoom = 1;
      if (this.mounted === true) {
        this.setState({
          loaded: false,
          error: false
        });
      }
    }
  }

  componentDidUpdate (prevProps) {
    const {
      figure,
      width,
      height,
      zoom,
      figureZoom,
      angle,
      isStatic,
      isSprite,
      crop,
      figuresStore
    } = this.props;

    if (this.isNewImageRequest(prevProps)) {
      figuresStore.cache.addImage(this._getImageKey(this.props), this.getRequestSource(this.props), this.getRotation(this.props), this.getRequestBody(this.props), {
        callback: this.addImageHandler,
        key: this.getRequestKey(figure.facsimileId, figure.sheet, width, height, zoom, figureZoom, angle, isStatic, isSprite, crop)
      });
    } else {
      this.applyAnnotations(this.props.figure);
    }
  }

  isNewImageRequest (props) {
    const {
      width,
      height,
      isSprite,
      crop,
      isStatic,
      zoom,
      angle,
      figure: {source, facsimileId, sheet},
      skipOrientation,
      figureZoom
    } = this.props;

    return props.width !== width || props.height !== height || props.isSprite !== isSprite ||
      props.crop !== crop || props.isStatic !== isStatic || props.zoom !== zoom ||
      props.angle !== angle || props.figureZoom !== figureZoom || props.skipOrientation !== skipOrientation ||
      props.figure.source !== source || props.figure.facsimileId !== facsimileId || props.figure.sheet !== sheet;
  }

  getRequestKey (facsimileId, sheet, width, height, zoom, figureZoom, angle, isStatic, isSprite, crop) {
    return facsimileId + '_' + sheet + '_' + width + '_' +
      height + '_' + zoom + '_' + figureZoom + '_' + angle + '_' +
      isStatic + '_' + isSprite + '_' + crop;
  }

  _angle (props) {
    let {angle, skipOrientation} = props;
    return skipOrientation === true ? 0 : angle;
  }

  _getImageKey = (props) => {
    return !props.isSprite ? getImageKey(0, SPRITE_LIMIT, this.getRequestSource(props), [[props.figure.facsimileId, props.figure.sheet]], this.getRotation(props))
      : getImageKey(props.index, SPRITE_LIMIT, this.getRequestSource(props), props.figure.sprite, this.getRotation(props));
  }

  applyAnnotations = (figure) => {
    if (figure && figure.cropped && figure.cropped.fetched === 1) {
      transaction(() => {
        figure.annotations = figure.annotations.map(ann => {
          if (!ann.cropped) {
            ann.cropped = true;
            ann.x -= figure.cropped.x;
            ann.cx -= figure.cropped.x;
            ann.y -= figure.cropped.y;
            ann.cy -= figure.cropped.y;
          }
          return ann;
        });
      });
    }
  }

  isNotRelevantKey = (key) => {
    const keys = key.split('_');
    const {
      figure,
      width,
      height,
      zoom,
      figureZoom,
      angle
    } = this.props;
    return keys[0] != figure.facsimileId || keys[1] != figure.sheet || keys[2] != width || keys[3] != height || keys[4] != zoom || keys[5] != figureZoom || keys[6] != angle;
  }

  getKeyValue = (key, i) => {
    const keys = key.split('_');
    return keys[i];
  }

  addImageHandler = (result) => {
    if (this.mounted) {
      if (result.loaded === true && result.key) {
        if (this.isNotRelevantKey(result.key)) {
          return;
        }
        this.blobUrl = result.url;
        let index = this.props.index < SPRITE_LIMIT ? this.props.index : this.props.index % SPRITE_LIMIT;
        let maxHeight = 0;
        let maxWidth = result.sizes.width.reduce((x, y) => parseInt(x) + parseInt(y));
        result.sizes.height.forEach(height => {
          maxHeight = parseInt(height) > maxHeight ? height : maxHeight;
        });
        const i = result.sizes.height.length === 1 ? 0 : (result.sizes.height.length > index ? index : -1);
        if (i !== -1) {
          const height = parseInt(result.sizes.height[i].trim());
          const width = parseInt(result.sizes.width[i].trim());
          const orWidth = parseInt(result.sizes.orWidth[i].trim());
          const orHeight = parseInt(result.sizes.orHeight[i].trim());
          this.props.figure.maxOriginalWidth = orWidth;
          this.props.figure.maxOriginalHeight = orHeight;
          let crop;
          if (result.sizes.crop.length) {
            crop = result.sizes.crop[i].split(',');
          }
          this.sizes = {height: height, width: width, orWidth: orWidth, orHeight: orHeight, maxHeight: maxHeight, maxWidth: maxWidth};
          if (crop) {
            this.sizes.cropX = Number(crop[0]);
            this.sizes.cropY = Number(crop[1]);
            this.sizes.cropWidth = Number(crop[2]);
            this.sizes.cropHeight = Number(crop[3]);

            this.props.figure.maxWidth = this.sizes.cropWidth;
            this.props.figure.maxHeight = this.sizes.cropHeight;

            this.props.figure.cropped = {};
            this.props.figure.cropped.fetched = 1;
            this.props.figure.cropped.x = this.sizes.cropX;
            this.props.figure.cropped.y = this.sizes.cropY;
            this.applyAnnotations(this.props.figure);
          }
          this.offset = 0;
          result.sizes.width.forEach((width, j) => {
            if (j < i) {
              this.offset += parseInt(result.sizes.width[j].trim());
            }
          });
        }
        const urlFigureZoom = this.urlFigureZooms.get(this.getRequestKey(this.getKeyValue(result.key, 0), this.getKeyValue(result.key, 1), parseInt(this.getKeyValue(result.key, 2)),
          parseInt(this.getKeyValue(result.key, 3)), parseFloat(this.getKeyValue(result.key, 4)), 0, parseInt(this.getKeyValue(result.key, 6)),
          this.getKeyValue(result.key, 7) === 'true', this.getKeyValue(result.key, 8) === 'true', this.getKeyValue(result.key, 9) === 'true'));
        const figureZoom = parseFloat(this.getKeyValue(result.key, 5));
        if (urlFigureZoom && urlFigureZoom <= figureZoom) {
          this.urlZoom = urlFigureZoom;
        } else {
          this.urlZoom = figureZoom;
        }
        if (this.mounted === true) {
          this.setState({
            loaded: true,
            error: false
          });
        }
      } else {
        if (result.key) {
          if (this.isNotRelevantKey(result.key)) {
            return;
          }
          if (typeof this.props.onError === 'function') {
            this.props.onError(this.props.figure.source);
          }
          if (this.mounted === true) {
            this.setState({
              loaded: true,
              error: true
            });
          }
        }
      }
    }
  };

  _getMaxFigureZoom (originalSize, size, spriteZoom, isStatic, isSprite, crop) {
    const maxZoom = getMaxZoom(originalSize, size, crop);
    let maxFigureZoom = getMaxFigureZoom(maxZoom, spriteZoom, isStatic, isSprite);
    const fraction = maxFigureZoom - ~~maxFigureZoom;
    return ~~maxFigureZoom + (fraction < 0.25 ? 0.25 : (fraction < 0.5 ? 0.5 : (fraction < 0.75 ? 0.75 : 1)));
  }

  /**
   * Method return source to image file with height based on zoom
   * @returns {Object} props
   */
  getRequestSource (props) {
    let {figure: {spriteSource, maxOriginalWidth, maxOriginalHeight, facsimileId, sheet}, height: figHeight, width: figWidth, crop, zoom: spriteZoom, isStatic, isSprite, figureZoom} = props;
    const rotation = this.getRotation(props);
    const key = this.getRequestKey(facsimileId, sheet, figWidth, figHeight, spriteZoom, 0, props.angle, isStatic, isSprite, crop);
    const urlFigureZoom = this.urlFigureZooms.get(key);
    figureZoom = urlFigureZoom && figureZoom > urlFigureZoom ? urlFigureZoom : figureZoom;
    let zoom = getSpriteZoom(isStatic, isSprite, figureZoom, spriteZoom);
    let width = getSpriteWidth(this.getWidth(figWidth, figHeight), crop, zoom);
    let height = getSpriteHeight(this.getHeight(figWidth, figHeight), crop, zoom);
    if (maxOriginalWidth && maxOriginalHeight) {
      if (maxOriginalWidth && maxOriginalHeight) {
        if (width > maxOriginalWidth && !rotation) {
          const maxFigureZoom = this._getMaxFigureZoom(maxOriginalWidth, figWidth, spriteZoom, isStatic, isSprite, crop);
          this.urlFigureZooms.set(key, maxFigureZoom);
          width = maxOriginalWidth;
          height = getSpriteHeight(figHeight, crop, getSpriteZoom(isStatic, isSprite, maxFigureZoom, spriteZoom));
        } else if (height > maxOriginalHeight && rotation) {
          const maxFigureZoom = this._getMaxFigureZoom(maxOriginalHeight, figHeight, spriteZoom, isStatic, isSprite, crop);
          this.urlFigureZooms.set(key, maxFigureZoom);
          height = maxOriginalHeight;
          width = getSpriteWidth(figWidth, crop, getSpriteZoom(isStatic, isSprite, maxFigureZoom, spriteZoom));
        }
      }
    }
    return extendSpriteSource(!rotation ? width : height, !rotation ? height : width, crop, this._angle(props), spriteSource);
  }

  /**
   * Returns false if a sprite has portrait orientation, otherwise it is false
   * @param {Object} props
   * @returns {boolean}
   */
  getRotation (props) {
    let {figure: {mosaicAngle}, isSprite} = props;
    return !isSprite ? !!(this._angle(props) % 180) : !!(mosaicAngle % 180);
  }

  /**
   * Returns figure height to be requested
   * If figure height of small RListItemFigure is to be calculated, correction is needed
   * because backend returns smaller image than we need, so we have to ask larger image
   * @param figWidth - initial figure width
   * @param figHeight - initial figure height
   * @returns {number} - figure height to be requested
   */
  getHeight (figWidth, figHeight) {
    return (figWidth === RLIST_ITEM_SMALL_WIDTH && figHeight === RLIST_ITEM_SMALL_HEIGHT)
      ? RLIST_ITEM_SMALL_HEIGHT / RLIST_ITEM_SMALL_CORRECTION
      : figHeight;
  }

  /**
   * Returns figure width to be requested
   * If figure width of small RListItemFigure is to be calculated, correction is needed
   * because backend returns smaller image than we need, so we have to ask larger image
   * @param figWidth - initial figure width
   * @param figHeight - initial figure height
   * @returns {number} - figure width to be requested
   */
  getWidth (figWidth, figHeight) {
    return (figWidth === RLIST_ITEM_SMALL_WIDTH && figHeight === RLIST_ITEM_SMALL_HEIGHT)
      ? RLIST_ITEM_SMALL_WIDTH / RLIST_ITEM_SMALL_CORRECTION
      : figWidth;
  }

  /**
   * Returns sprite post request payload
   * @param props
   */
  getRequestBody (props) {
    let {figure: {facsimileId, sheet, sprite}, isSprite, index} = props;
    return getSpriteBody(isSprite ? index : 0, isSprite ? SPRITE_LIMIT : 1, isSprite ? sprite : [[facsimileId, sheet]]);
  }

  updateBackgroundTranslate = (x, y) => {
    this.refs.figure.updateBackgroundTranslate(x, y);
  };

  render () {
    const {
      figure,
      insert,
      height,
      hideToolTip,
      markers,
      searchSelection,
      trackMouse,
      isStatic,
      finIn,
      deleteMarker,
      skipOrientation,
      hideError,
      zoom,
      onMousePosition,
      width,
      prefix,
      solid,
      crop,
      selection,
      disableSpinner,
      onLoad,
      onError,
      isSprite,
      isThumb,
      figureZoom
    } = this.props;
    return (
      <Figure
        ref='figure'
        figure={figure}
        insert={insert}
        height={height}
        hideToolTip={hideToolTip}
        markers={markers}
        searchSelection={searchSelection}
        trackMouse={trackMouse}
        isStatic={isStatic}
        finIn={finIn}
        deleteMarker={deleteMarker}
        skipOrientation={skipOrientation}
        hideError={hideError}
        zoom={zoom}
        onMousePosition={onMousePosition}
        width={width}
        prefix={prefix}
        solid={solid}
        crop={crop}
        selection={selection}
        disableSpinner={disableSpinner}
        onLoad={onLoad}
        onError={onError}
        isSprite={isSprite}
        isThumb={isThumb}
        size={this.sizes}
        offset={this.offset}
        loadState={this.state}
        url={this.blobUrl}
        angle={figure.angle}
        figureZoom={isSprite ? 1 : figureZoom}
        urlZoom={this.urlZoom}
      />
    );
  }
}

SpriteFigure.propTypes = {
  figure: PropTypes.object,
  insert: PropTypes.any,
  height: PropTypes.number,
  hideToolTip: PropTypes.bool,
  markers: PropTypes.array,
  searchSelection: PropTypes.bool,
  trackMouse: PropTypes.bool,
  isStatic: PropTypes.bool,
  finIn: PropTypes.bool,
  deleteMarker: PropTypes.bool,
  skipOrientation: PropTypes.bool,
  hideError: PropTypes.bool,
  zoom: PropTypes.number,
  onMousePosition: PropTypes.func,
  width: PropTypes.number,
  prefix: PropTypes.string,
  solid: PropTypes.bool,
  crop: PropTypes.bool,
  selection: PropTypes.object,
  disableSpinner: PropTypes.bool,
  onLoad: PropTypes.func,
  onError: PropTypes.func,
  isSprite: PropTypes.bool,
  isThumb: PropTypes.bool,
  index: PropTypes.number,
  angle: PropTypes.number,
  figureZoom: PropTypes.number,
  annotationsLength: PropTypes.number,
  figuresStore: PropTypes.object.isRequired
};

export default SpriteFigure;
