import PropTypes from 'prop-types';
import React, { Component } from 'react';

class MarkerSVG extends Component {
  render () {
    let {name, width, height, bgcolor, color, deletion, userAdded, prefix, solid} = this.props;
    let suffix = '';
    let strokeProps = {};
    if (userAdded === true) {
      suffix += '-user';
      strokeProps = {
        stroke: `black`,
        strokeWidth: `2px`,
        strokeDasharray: `4px`
      };
    }
    if (deletion === true) suffix += '-deletion';

    return (<g
      id={`${prefix}-marker-m${name}${suffix}`}
      key={`marker-m${name}${suffix}`}
      className={`svg-marker${suffix} ${deletion === true ? 'deletion-group' : ''}`}
      style={{'cursor': deletion === true ? 'pointer' : 'move'}}
    >
      <rect
        width={width}
        height={height}
        fill={`${bgcolor}`}
        fillOpacity={solid === true ? 1 : 0.65}
        y={height / -2}
        x={width / -2}
        {...strokeProps}
      />
      <text
        fontSize={height}
        y={height / 2}
        x={0}
        width={width}
        height={height}
        dy='-.15em'
        transform={`rotate(-${0})`}
        fill={`${color}`}
        textAnchor={`middle`}
      >
        {name}
      </text>
      {deletion === false ? null
        : (
          <path
            className={`deletion`}
            y={height / -2}
            x={width / -2}
            d={`
              M ${(width / 2) - 16},${(height / -2) - 16} L ${(width / 2) + 16},${(height / -2) + 16}
              M ${(width / 2) + 16},${(height / -2) - 16} L ${(width / 2) - 16},${(height / -2) + 16}
            `}
            stroke={`red`}
            fill={`red`}
            strokeLinecap={`round`}
            strokeWidth={`8px`}
          />
        )
      }
    </g>);
  }
}

MarkerSVG.propTypes = {
  name: PropTypes.string,
  prefix: PropTypes.string,
  width: PropTypes.number,
  height: PropTypes.number,
  bgcolor: PropTypes.string,
  deletion: PropTypes.bool,
  userAdded: PropTypes.bool,
  solid: PropTypes.bool,
  color: PropTypes.string
};

export default MarkerSVG;
