import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {inject, observer} from 'mobx-react';
import classes from './FiguresContainer.scss';
import MosaicControls from '../MosaicControls/index';
import FiguresControls from '../FiguresControls/index';
import FigureView from '../FigureView/index';
import MosaicView from '../MosaicView/index';
import FigureAutoFlip from '../FigureAutoFlip/index';

import isEqual from 'lodash.isequal';

import {
  Spinner,
  TextSwitch,
  SIDEBAR_WIDTH,
  CONTROL_PANEL_HEIGHT
} from '@epo/web-common';
import { DelayRender } from 'common/dist';

import {
  getMosaicViewSize
} from 'Figures.Helper';

@inject(stores => ({
  enableThumbnailBar: stores.enableThumbnailBar,
  applySynonyms: stores.applySynonyms,
}))
@observer
class FiguresContainer extends Component {
  _markers = null;

  setAsMissedWhenNoFigures = () => {
    let {
      isMissed,
      store: {
        publication: {
          loaded
        }
      }
    } = this.props;
    if (isMissed) {
      isMissed.asMissed = loaded === -1;
      if (this.props.isBNS !== undefined) {
        this.props.isBNS.asMissed = loaded < 0;
      }
    }
  }

  highlightPublicationMarkers () {
    let {store, markers, applySynonyms} = this.props;
    let publication = store.publication;
    publication.refPhrases && publication.refPhrases.length && markers && markers.length &&
      publication.highlightMarkers(markers, applySynonyms);
  }

  get markers () {
    let {markers, store: {filterByMarkers}} = this.props;
    for (let i in markers) {
      if (markers.hasOwnProperty(i) === true) {
        let name = 'm' + markers[i]['name'];
        markers[i]['filtered'] = filterByMarkers.indexOf(name) !== -1;
      }
    }

    if (!isEqual(markers, this._markers)) {
      this._markers = markers;
    }

    return this._markers;
  }

  get visibleMarkers () {
    return this.markers.filter(marker => {
      return this.props.store.filterByMarkers.indexOf('m' + marker.name) === -1;
    }).map(marker => marker.name);
  }

  /**
   * Method sets next active image
   */
  scrollPrev = () => {
    let {store: {prevActiveFigure}, funnelSettings: {jumpMarker, funnel, markers}} = this.props;
    prevActiveFigure(jumpMarker, funnel === true, markers, 1, true);
  };

  /**
   * Method sets previous active image
   */
  scrollNext = () => {
    let {store: {nextActiveFigure}, funnelSettings: {jumpMarker, funnel, markers}} = this.props;
    nextActiveFigure(jumpMarker, funnel === true, markers, 1);
  };

  componentDidMount () {
    this.setAsMissedWhenNoFigures();
  }

  componentWillUnmount () {
    this.refs = {};
  }

  componentDidUpdate () {
    this.setAsMissedWhenNoFigures();
  }

  render () {
    let {
      activeTab,
      addSelectedTextToMarkerHandler,
      documentNavigator,
      getNextDocumentData,
      getShortcutCombination,
      height,
      persistency,
      store,
      markers,
      markersByColorUnfiltered,
      store: {
        publication,
        autoFlippingContinue
      },
      width,
      isBNS,
      documentWithError,
      funnelSettings,
      onFindSimilar,
      viewerType,
      openNextDocument,
      updateMarkersLegend,
      setMarkerToScroll,
      toggleMarker,
      toggleAllMarkers,
      toggleParagraphs,
      markersForSearch,
      isPanelActive,
    } = this.props;

    let { mosaicViewWidth, mosaicViewHeight } = getMosaicViewSize(store.mode, isBNS, height, width, documentWithError);

    if (publication.loaded < 0) {
      // Show error if server returns publication without figures
      mosaicViewHeight += CONTROL_PANEL_HEIGHT;
      return (
        <div
          ref='root'
          className={classes.root}
          style={{width, height}}
        >
          <div className={`${classes.notFound} missed-document-marker`}>
            {publication.loaded === -2
              ? <div className='alert alert-warning' role='alert'>
                <span className='glyphicon glyphicon-exclamation-sign' aria-hidden='true' style={{marginRight: '.5em'}} />
                <span>{(activeTab !== 'figures' ? 'No data found.' : 'No figures found.')}</span>
                {autoFlippingContinue === true ? <div style={{margin: '1rem 0rem 0rem 2.4rem'}}>Going to the next document in 2 seconds.</div> : null}
              </div>
              : null}
          </div>
          <MosaicView
            height={mosaicViewHeight}
            markers={this.markers}
            store={store}
            isBNS={isBNS}
            documentWithError={documentWithError}
            isEmpty={publication.loaded < 0}
            width={mosaicViewWidth}
            funnelSettings={funnelSettings}
            activeTab={activeTab}
            container={{width, height}}
            scrollPrev={this.scrollPrev}
            scrollNext={this.scrollNext}
            hotKeysActive={isPanelActive}
          />
          <div style={{
            width: `${SIDEBAR_WIDTH}px`,
            position: 'absolute',
            right: '0',
            bottom: '-2px'
          }}>
            {isBNS !== undefined && activeTab !== 'figures' && store.mode === 1 ? <TextSwitch
              isBNS={isBNS}
            /> : null}
          </div>
          {autoFlippingContinue === true ? <FigureAutoFlip
            store={store}
            funnelSettings={funnelSettings}
            documentNavigator={documentNavigator}
            getNextDocumentData={getNextDocumentData}
            persistency={persistency}
            viewerType={viewerType}
          /> : null}

        </div>
      );
      // return (<div className={classes.notFound}>No figures found. {autoFlippingContinue === true ? 'Go to next document in 2 seconds.' : null}</div>);
    } else if (publication.loaded === 0) {
      // Show spinner if publication is not loaded
      return (
        <div
          ref='root'
          className={classes.root}
          style={{width, height}}
        >
          <Spinner
            loading={publication.loaded === 0}
            height='50px'
            width='5px'
          />
        </div>
      );
    }

    this.highlightPublicationMarkers();

    let figureView = null;
    if (store.mode === 1) {
      const enableThumbnailBar = this.props.enableThumbnailBar;
      const figureViewWidth = enableThumbnailBar || activeTab !== 'figures' ? width - SIDEBAR_WIDTH : width;

      figureView = <div className='figureView'>
        <FigureView
          height={height}
          markers={this.markers}
          store={store}
          isBNS={isBNS}
          width={figureViewWidth}
          markersByColorUnfiltered={markersByColorUnfiltered}
          onFindSimilar={onFindSimilar}
          funnelSettings={funnelSettings}
          addSelectedTextToMarkerHandler={addSelectedTextToMarkerHandler}
        />
      </div>
    }

    return (
      <div
        ref='root'
        className={classes.root}
        style={{width, height}}
      >
        <DelayRender
          delay={100}
          placeholder={
            <div style={{height: 34, width: '100%', background: '#f9f9f9', borderBottom: '1px solid #ccc'}}/>
          }
        >
          <FiguresControls
            markers={this.markers}
            store={store}
            getShortcutCombination={getShortcutCombination}
          />
        </DelayRender>

        {figureView}
        {isBNS === undefined || !(isBNS.active || (documentWithError && !isBNS.active))
          ? <DelayRender delay={100}>
            <MosaicControls
              store={store}
              markers={markers}
              funnelSettings={funnelSettings}
              openNextDocument={openNextDocument}
              updateMarkersLegend={updateMarkersLegend}
              setMarkerToScroll={setMarkerToScroll}
              toggleMarker={toggleMarker}
              toggleAllMarkers={toggleAllMarkers}
              toggleParagraphs={toggleParagraphs}
              getShortcutCombination={getShortcutCombination}
              scrollPrev={this.scrollPrev}
              scrollNext={this.scrollNext}
              markersForSearch={markersForSearch}
            />
          </DelayRender> : null
        }
        <MosaicView
          height={mosaicViewHeight}
          markers={this.markers}
          store={store}
          isBNS={isBNS}
          documentWithError={documentWithError}
          funnelSettings={funnelSettings}
          width={mosaicViewWidth}
          activeTab={activeTab}
          container={{width, height}}
          scrollPrev={this.scrollPrev}
          scrollNext={this.scrollNext}
          hotKeysActive={isPanelActive}
        />
        <div style={{
          width: `${SIDEBAR_WIDTH}px`,
          position: 'absolute',
          right: '0',
          bottom: '-2px'
        }}>
          {isBNS !== undefined && store.mode === 1 && activeTab !== 'figures' ? <TextSwitch isBNS={isBNS} /> : null}
        </div>
        <FigureAutoFlip
          store={store}
          funnelSettings={funnelSettings}
          documentNavigator={documentNavigator}
          getNextDocumentData={getNextDocumentData}
          persistency={persistency}
          viewerType={viewerType}
        />
      </div>
    );
  }
}

FiguresContainer.propTypes = {
  activeTab: PropTypes.string.isRequired,
  store: PropTypes.any,
  viewerType: PropTypes.string,
  width: PropTypes.number,
  height: PropTypes.number,
  documentNavigator: PropTypes.object,
  markers: PropTypes.array,
  isBNS: PropTypes.object,
  isMissed: PropTypes.object,
  getNextDocumentData: PropTypes.func,
  funnelSettings: PropTypes.object.isRequired,
  markersByColorUnfiltered: PropTypes.object,
  markersForSearch: PropTypes.any,
  persistency: PropTypes.object,
  onFindSimilar: PropTypes.func,
  addSelectedTextToMarkerHandler: PropTypes.func,
  getShortcutCombination: PropTypes.func.isRequired,
  openNextDocument: PropTypes.func,
  updateMarkersLegend: PropTypes.func,
  setMarkerToScroll: PropTypes.func,
  toggleMarker: PropTypes.func,
  toggleAllMarkers: PropTypes.func,
  toggleParagraphs: PropTypes.func,
  isPanelActive: PropTypes.bool,
  enableThumbnailBar: PropTypes.bool,
  applySynonyms: PropTypes.func,
};

export default FiguresContainer;
