import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer, Provider} from 'mobx-react';
import {Debounce, Bind, Throttle} from 'lodash-decorators';
import get from 'lodash.get';
import isEqual from 'lodash.isequal';
import classes from './FiguresModule.scss';
import ReactResizeDetector from 'react-resize-detector';
import FiguresShortcuts from '../FiguresShortcuts';
import FiguresContainer from '../FiguresContainer';
import {
  uuid,
  SIDEBAR_WIDTH,
  FIGURES_MODULE_CLASS_NAME,
  CONTROL_PANEL_HEIGHT,
  FIGURE_PREFETCH_LIMIT_AFTER,
  SPRITE_LIMIT,
  SIDEBAR_MODE,
  FULL_MOSAIC_MODE,
  MOSAIC_CORRECTION_WIDTH,
  MOSAIC_CORRECTION_HEIGHT
} from '@epo/web-common';
import {
  getImageKey,
  calculateSideBar,
  extendSpriteSource,
  getFacsimileAttributes,
  getItemSize,
  getMosaicViewSize,
  getCrop,
  getModeCrop,
  setSprite,
  getSpriteBody,
  getFiguresInterval,
  getRotation,
  getSpriteZoom,
  getSpriteWidth,
  getSpriteHeight
} from 'Figures.Helper';
import figuresModuleCallbackProxy from './FiguresModuleCallbackProxy';

@observer
class FiguresModule extends Component {
  constructor (props) {
    super(props);
    this.state = {
      width: null,
      height: null,
      wait: false
    };
    this.wait = false;
    this._sidebar = 0;
    this.preloadQueue = [];
    this.interval = 0;
    this.uuid = uuid();
  }

  componentDidMount () {
    this.updateDimensions();
    if (typeof window !== 'undefined') {
      window.addEventListener('resize', this._handleResizeDebounce, false);
    }
  }

  /**
   * Set a sidebar width to later calculate of container width while image preloading.
   * @param value
   */
  set sidebar (value) {
    if (value) {
      this._sidebar = value;
    }
  }

  /**
   * Returns a sidebar width
   * @returns {number}
   */
  get sidebar () {
    return this._sidebar;
  }

  /**
   * Adds document figures to the queue to preload them later on
   * @param {Object} document
   */
  doPreload (document) {
    const {rid, limit} = document;
    if (rid && limit) {
      this.preloadQueue.push(document);
      if (!this.interval) {
        this.interval = setInterval(() => { this.preloadImages(); }, 30);
      }
    }
  }

  /**
   * Stops preloading of images
   * @param {Boolean} force
   */
  stopPreload (force = false) {
    if (force || !this.preloadQueue.length) {
      clearInterval(this.interval);
      this.interval = 0;
      this.preloadQueue = [];
    }
  }

  /**
   * Processes figures which should be loaded
   */
  preloadImages = () => {
    let {width, height} = this.state;
    if (width > 0 && height > 0 && !document.querySelector('.Spinner__spinner')) {
      const { figuresStore: { publications } } = this.props;
      this.preloadQueue.forEach(({rid, limit}) => {
        setTimeout(() => { this._processImages(publications.getById(rid), limit); }, 0);
      });
      this.preloadQueue = [];
      this.stopPreload();
    }
  }

  preLoadNextDocuments = () => {
    const {documentNavigator, viewerType, figuresStore} = this.props;
    if (documentNavigator !== undefined && documentNavigator !== null) {
      // const {sessionId, panelName} = this.props;
      // const store = figuresStore.stores[sessionId][viewerType][panelName];
      // if (store.autoFlipping === true || store.autoFlippingContinue) return;

      const actions = ['getNextDetails', 'getPreviousDetails'];
      actions.forEach((action) => {
        if (typeof documentNavigator[action] === 'function') {
          const data = documentNavigator[action]();
          if (data) {
            const onCreate = ({figures}) => {
                if (figures) {
                  this.doPreload({rid: data.rid || 'BNS-' + data.accessnum, limit: FIGURE_PREFETCH_LIMIT_AFTER, figures});
                }
            };
            const onCreateProxy = figuresModuleCallbackProxy.getCallbackProxy(this.uuid, onCreate);
            const dataSource = data.datasource || data.dataSource;
            const documentType = dataSource.type;
            figuresStore.publications.createPublication({
              familyId: data.family_id,
              publicationId: dataSource.id || 'BNS-' + data.accessnum,
              publicationNumber: data.publicationId || data.publicationidshort || 'BNS-' + data.accessnum,
              publicationIdShort: data.publicationId || data.publicationidshort || 'BNS-' + data.accessnum,
              documentType,
              onCreate: onCreateProxy,
              viewerType: viewerType
            });
          }
        }
      });
    }
  };

  UNSAFE_componentWillMount () {
    const {
      viewerType,
      panelName,
      sessionId,
      familyId,
      publicationId,
      publicationIdShort,
      figures,
      publicationNumber,
      documentType,
      activeTab,
      figuresStore
    } = this.props;
    figuresStore.createStore(sessionId, viewerType, panelName);
    if (publicationId.indexOf('BNS') !== -1 && figures === undefined) {
      return;
    }
    const onCreateProxy = figuresModuleCallbackProxy.getCallbackProxy(this.uuid, this._setActiveFigureByTypeCallback);
    figuresStore.setPublication(sessionId, viewerType, panelName, {
      familyId,
      figures,
      publicationId,
      publicationIdShort,
      publicationNumber: publicationNumber,
      documentType: documentType,
      onCreate: onCreateProxy
    });

    this._setActiveFigureByType(sessionId, viewerType, panelName, activeTab);
  }


  /**
   * Handles figures store operations like createStore and setPublication
   * @param nextProps
   */
  handleFiguresStore = (nextProps) => {
    const {
      viewerType,
      panelName,
      sessionId,
      familyId,
      publicationId,
      publicationIdShort,
      publicationNumber,
      figures,
      activeTab,
      documentType,
      figuresStore
    } = nextProps;

    figuresStore.createStore(sessionId, viewerType, panelName);
    const onCreateProxy = figuresModuleCallbackProxy.getCallbackProxy(this.uuid, this._setActiveFigureByTypeCallback);
    figuresStore.setPublication(sessionId, viewerType, panelName, {
      familyId: familyId,
      figures: figures,
      publicationId: publicationId,
      publicationIdShort: publicationIdShort,
      publicationNumber: publicationNumber,
      documentType: documentType,
      onCreate: onCreateProxy
    });
    this._setActiveFigureByType(sessionId, viewerType, panelName, activeTab);
  };

  UNSAFE_componentWillUpdate (nextProps) {
    const {
      activeTab,
      viewerType,
      panelName,
      sessionId,
      publicationId,
      figures,
      publicationNumber,
      figuresStore
    } = nextProps;

    if (nextProps.viewerType !== this.props.viewerType) {
      this.handleFiguresStore(nextProps);
    }
    if (
      (publicationId !== this.props.publicationId && activeTab !== this.props.activeTab) ||
      (publicationId !== this.props.publicationId && publicationNumber !== this.props.publicationNumber) ||
      this.wait === true ||
      (nextProps.isBNS !== undefined && nextProps.isBNS.asMissed === true) ||
      (figures && this.props.figures && !isEqual(figures, this.props.figures))
    ) {
      if (publicationId.indexOf('BNS') !== -1 && figures === undefined) {
        return;
      }
      if (figures !== undefined && (this.props.isBNS === undefined || this.props.isBNS.active === false)) {
        let [figure] = figures;
        if (figures.length && figure.source.startsWith('BNS-') && figure.source.indexOf(publicationId.replace('BNS-', '')) === -1) {
          this.wait = true;
          return;
        }
        this.wait = false;
      }
      this.handleFiguresStore(nextProps);
    } else if (activeTab !== this.props.activeTab) {
      this._setActiveFigureByType(sessionId, viewerType, panelName, activeTab);
    }
  }

  componentWillUnmount () {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this._handleResizeDebounce);
    }
    this.stopPreload(true);
    figuresModuleCallbackProxy.removeAllCallbacks(this.uuid);
    this.rootNode = undefined;
  }

  /**
   * Preload next/previous images
   * @param publication contains images to be preloaded
   * @param limit is a number of images to be loaded
   * @private
   */
  _processImages = (publication, limit) => {
    if (!publication) {
      return false;
    }
    const {
      sessionId,
      viewerType,
      panelName,
      isBNS,
      figuresStore
    } = this.props;
    let {figures: figs} = publication;
    let {width, height} = this.state;
    if (width > 0 && height > 0 && figs && figs.length > 0 && this.rootNode && this.rootNode.querySelector('.Spinner__spinner') === null) {
      if (figs[0].sprite === undefined) {
        setSprite(figs);
      }
      let figures = figs.slice(0, limit);
      if (figures.length > 0) {
        let store = figuresStore.stores[sessionId][viewerType][panelName];
        let {mode, full} = store;
        let {mosaicViewWidth, mosaicViewHeight} = getMosaicViewSize(mode, isBNS, height, width);

        if (mode === SIDEBAR_MODE && full !== FULL_MOSAIC_MODE) {
          //sidebar mode
          // preload sidebar
          let {itemWidth, itemHeight} = getItemSize(mode, figs, mosaicViewHeight, mosaicViewWidth, 0);
          let figZoom = getSpriteZoom(true, true);
          let crop = getModeCrop(this.props.isBNS, mode);
          let figWidth = getSpriteWidth(itemWidth - MOSAIC_CORRECTION_WIDTH, crop, figZoom);
          let figHeight = getSpriteHeight(itemHeight - MOSAIC_CORRECTION_HEIGHT, crop, figZoom);
          let {direct, rotate} = getRotation(0, SPRITE_LIMIT, figs);
          if (direct) {
            let sourceMosaic = extendSpriteSource(figWidth, figHeight, crop, 0, figures[0].spriteSource);
            figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, sourceMosaic, figures[0].sprite, false), sourceMosaic, false, getSpriteBody(0, SPRITE_LIMIT, figures[0].sprite));
          }
          if (rotate) {
            let sourceMosaic = extendSpriteSource(figHeight, figWidth, crop, 0, figures[0].spriteSource);
            figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, sourceMosaic, figures[0].sprite, true), sourceMosaic, true, getSpriteBody(0, SPRITE_LIMIT, figures[0].sprite));
          }

          // preload big images
          width = width - SIDEBAR_WIDTH;
          height = height - CONTROL_PANEL_HEIGHT;
          figZoom = getSpriteZoom(false, false);
          crop = getCrop(this.props.isBNS);
          figWidth = getSpriteWidth(width, crop, figZoom);
          figHeight = getSpriteHeight(height, crop, figZoom);
          let imageSource;
          figures.forEach(figure => {
            if (figure === undefined) return;
            imageSource = extendSpriteSource((figure.angle % 180) ? figHeight : figWidth, (figure.angle % 180) ? figWidth : figHeight, crop, 0, figures[0].spriteSource);
            let [facsimileId, sheet] = getFacsimileAttributes(figure);
            figuresStore.cache.addImage(getImageKey(0, 1, imageSource, [[facsimileId, sheet]], !!(figure.angle % 180)), imageSource, !!(figure.angle % 180), getSpriteBody(0, 1, [[facsimileId, sheet]]));
          });
        } else if (mode !== SIDEBAR_MODE && full !== FULL_MOSAIC_MODE) {
          //mosaic mode
          let mosaic;
          if (this.rootNode !== undefined) {
            mosaic = this.rootNode.querySelector('.MosaicView__mosaic');
            if (mosaic) {
              this.sidebar = calculateSideBar(mosaic, mode);
            }
          }

          let {itemWidth, itemHeight} = getItemSize(mode, figs, mosaicViewHeight, mosaicViewWidth, figs.length >= 2 ? this.sidebar : 0);
          if (itemWidth === Infinity) return;
          const figZoom = getSpriteZoom(true, true);
          const crop = getModeCrop(this.props.isBNS, mode);
          const figWidth = getSpriteWidth(itemWidth - MOSAIC_CORRECTION_WIDTH, crop, figZoom);
          const figHeight = getSpriteHeight(itemHeight - MOSAIC_CORRECTION_HEIGHT, crop, figZoom);
          if (mode !== SIDEBAR_MODE && this.itemWidth === Infinity) return;

          let sourceMosaic = extendSpriteSource(figWidth, figHeight, crop, 0, figures[0].spriteSource);
          let sourceMosaicRotated = extendSpriteSource(figHeight, figWidth, crop, 0, figures[0].spriteSource);
          let iterations = (~~((width / itemWidth) * (height / itemHeight) / SPRITE_LIMIT) + 1) * 2;

          let figuresSprite = figs.slice(0, SPRITE_LIMIT * iterations);
          if (figuresSprite.length) {
            let {direct, rotate} = getRotation(0, SPRITE_LIMIT, figuresSprite);
            let index = -SPRITE_LIMIT;
            for (let i = 0; i < iterations; i++) {
              index = index + SPRITE_LIMIT;
              const attributes = getFiguresInterval(index, SPRITE_LIMIT, figures[0].sprite);
              if (attributes.length > 0) {
                if (direct) {
                  figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, sourceMosaic, attributes, false), sourceMosaic, false, getSpriteBody(0, SPRITE_LIMIT, attributes));
                }
                if (rotate) {
                  figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, sourceMosaic, attributes, true), sourceMosaicRotated, true, getSpriteBody(0, SPRITE_LIMIT, attributes));
                }
              }
            }
          }
        }
      }
    }
  };

  /**
   * On create callback method
   * @param {Array} figures
   */
  _setActiveFigureByTypeCallback = ({figures}) => {
    const {viewerType, panelName, sessionId, activeTab} = this.props;
    if (figures) {
      this._setActiveFigureByType(sessionId, viewerType, panelName, activeTab);
    }
  };

  _setActiveFigureByType = (sessionId, viewerType, panelName, activeTab) => {
    let publication = this.props.figuresStore.getActivePublication(sessionId, viewerType, panelName);
    if (publication) {
      publication.setActiveFigureByType(viewerType, panelName, activeTab);
      this.doPreload({rid: publication.publicationId, limit: FIGURE_PREFETCH_LIMIT_AFTER});
      this.preLoadNextDocuments();
    }
  };

  @Bind()
  getMarkers () {
    let {markers} = this.props;
    return markers.filter(marker => marker.isValid).map((marker) => {
      return {
        name: marker.name,
        content: marker.content,
        color: marker.color,
        bgcolor: marker.bgcolor,
        status: marker.status,
        filtered: false
      };
    });
  };

  @Bind()
  @Debounce(100)
  _handleResizeDebounce () {
    this.updateDimensions();
  }

  @Bind()
  cancelEditing () {
    const { figuresStore, sessionId, viewerType, panelName} = this.props;
    let store = figuresStore.stores[sessionId][viewerType][panelName];
    if (store.selection === true) {
      store.selection = false;
    }
  }

  updateDimensions = () => {
    if (!this.rootNode) return;

    const { clientWidth, clientHeight } = this.rootNode;
    let parent = this.rootNode.parentNode;
    let width;
    let height;

    width = parent.clientWidth || clientWidth;
    height = parent.clientHeight || clientHeight;

    if (width !== this.state.width || height !== this.state.height) {
      if (width > 0 && height > 0) {
        this.setState({
          width,
          height
        }, () => {
          const { figuresStore, sessionId, viewerType, panelName} = this.props;
          let publication = figuresStore.getActivePublication(sessionId, viewerType, panelName);
          this.cancelEditing();
          this._processImages(publication, publication.figures.slice(0, FIGURE_PREFETCH_LIMIT_AFTER));
        });
      }
    }
  };

  @Bind()
  openNextDocument (next = true) {
    let {documentNavigator, getNextDocumentData, sessionId, viewerType, panelName, figuresStore} = this.props;
    const store = figuresStore.stores[sessionId][viewerType][panelName];
    if (next === true && documentNavigator && documentNavigator.hasNext()) {
      documentNavigator.getNext();
      getNextDocumentData();
      store.activeFigure = 0;
    } else if (next === false && documentNavigator && documentNavigator.hasPrevious()) {
      documentNavigator.getPrevious();
      getNextDocumentData();
      store.activeFigure = store.publication.figures.length - 1;
    }
  };

  @Bind()
  @Throttle(300, {trailing: false})
  _handleOnWheel (deltaY) {
    const {
      documentNavigator, funnelSettings: {funnel, markers},
      sessionId, viewerType, panelName, persistency, figuresStore
    } = this.props;
    const store = figuresStore.stores[sessionId][viewerType][panelName];
    const {prevActiveFigure, nextActiveFigure, modeWidth, full, mode} = store;
    let crossFamilyBoundary = persistency.get({type: 'FNB', module: 'familyBoundarySetting'});
    if (deltaY > 0) {
      let hasNext = nextActiveFigure('*', funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : (modeWidth * mode));
      if (hasNext === false && crossFamilyBoundary && documentNavigator && documentNavigator.hasNext()) {
        this.openNextDocument(true);
      }
    } else if (deltaY < 0) {
      let hasPrev = prevActiveFigure('*', funnel === true, funnel === true ? markers : {}, full === 1 ? 1 : (modeWidth * mode), true);
      if (hasPrev === false && crossFamilyBoundary && documentNavigator && documentNavigator.hasPrevious()) {
        this.openNextDocument(false);
      }
    }
  }

  render () {
    const {width, height} = this.state;
    let {
      activeTab,
      documentNavigator,
      funnelSettings,
      getNextDocumentData,
      isBNS,
      isPanelActive,
      markersByColorUnfiltered,
      onFindSimilar,
      panelName,
      getShortcutCombination,
      sessionId,
      persistency,
      viewerType,
      isMissed,
      documentWithError,
      updateMarkersLegend,
      setMarkerToScroll,
      toggleMarker,
      toggleAllMarkers,
      toggleParagraphs,
      markersForSearch,
      figuresStore,
      enableThumbnailBar,
      applySynonyms,
      notify,
      figureSearch,
      prefetch,
    } = this.props;
    let store = get(figuresStore, ['stores', sessionId, viewerType, panelName]);
    if (!store) {
      return null;
    }
    return (
      <Provider
        figuresStore={figuresStore}
        enableThumbnailBar={enableThumbnailBar}
        applySynonyms={applySynonyms}
        notify={notify}
        figureSearch={figureSearch}
        prefetch={prefetch}
      >
        <div
          ref={(node) => {this.rootNode = node}}
          className={`${classes.root} ${FIGURES_MODULE_CLASS_NAME}`}
          onWheel={(e) => {
          this._handleOnWheel(e.deltaY);
        }}
        >
          {(width !== null && height !== null)
          ? <FiguresContainer
            addSelectedTextToMarkerHandler={this.props.addSelectedTextToMarkerHandler}
            documentNavigator={documentNavigator}
            funnelSettings={funnelSettings}
            getNextDocumentData={getNextDocumentData}
            height={height}
            isBNS={isBNS}
            isPanelActive={isPanelActive}
            markers={this.getMarkers()}
            markersByColorUnfiltered={markersByColorUnfiltered}
            markersForSearch={markersForSearch}
            onFindSimilar={onFindSimilar}
            openNextDocument={this.openNextDocument}
            panelName={panelName}
            store={store}
            viewerType={viewerType}
            width={width}
            isMissed={isMissed}
            documentWithError={documentWithError}
            persistency={persistency}
            getShortcutCombination={getShortcutCombination}
            activeTab={activeTab}
            updateMarkersLegend={updateMarkersLegend}
            setMarkerToScroll={setMarkerToScroll}
            toggleMarker={toggleMarker}
            toggleAllMarkers={toggleAllMarkers}
            toggleParagraphs={toggleParagraphs}
          />
          : null}
          <FiguresShortcuts
            isPanelActive={isPanelActive}
            funnelSettings={funnelSettings}
            openNextDocument={this.openNextDocument}
            store={store}
            persistency={persistency}
          />
          <ReactResizeDetector
            handleWidth={Boolean(true)}
            handleHeight={Boolean(false)}
            onResize={this._handleResizeDebounce}
          />
        </div>
      </Provider>
    );
  }
}

FiguresModule.propTypes = {
  activeTab: PropTypes.string,
  addSelectedTextToMarkerHandler: PropTypes.func,
  documentNavigator: PropTypes.object,
  familyId: PropTypes.string,
  figures: PropTypes.array,
  funnelSettings: PropTypes.object.isRequired,
  getNextDocumentData: PropTypes.func,
  getShortcutCombination: PropTypes.func.isRequired,
  isBNS: PropTypes.object,
  isMissed: PropTypes.object,
  documentWithError: PropTypes.bool,
  isPanelActive: PropTypes.bool.isRequired,
  markersByColorUnfiltered: PropTypes.object,
  markersForSearch: PropTypes.any,
  onFindSimilar: PropTypes.func,
  panelName: PropTypes.string.isRequired,
  persistency: PropTypes.object,
  publicationId: PropTypes.string,
  publicationIdShort: PropTypes.string,
  publicationNumber: PropTypes.string,
  sessionId: PropTypes.number,
  viewerType: PropTypes.string.isRequired,
  documentType: PropTypes.string,
  updateMarkersLegend: PropTypes.func,
  setMarkerToScroll: PropTypes.func,
  toggleMarker: PropTypes.func,
  toggleAllMarkers: PropTypes.func,
  toggleParagraphs: PropTypes.func,
  markers: PropTypes.object.isRequired,
  figuresStore: PropTypes.object.isRequired,
  enableThumbnailBar: PropTypes.bool,
  applySynonyms: PropTypes.func,
  notify: PropTypes.func,
  figureSearch: PropTypes.func
};

export default FiguresModule;
