import PropTypes from 'prop-types';
import React, { Component } from 'react';
import classes from './FigureAutoFlip.scss';
import { observer, inject } from 'mobx-react';
import { transaction, action } from 'mobx';
import { NEXT_PUBLICATION_TIMEOUT, nodeParentsWithClass } from '@epo/web-common';

@inject(stores => ({
  prefetch: stores.prefetch,
}))
@observer
class FigureAutoFlip extends Component {
  _interval = 0;
  _timeout = 0;
  _missed = false;
  _nextImageTime = 0;
  _reset = false;

  state = {
    reachedLimit: false,
    change: false,
  };

  /**
   * Method disable auto flipping
   * @private
   */
  _stopAutoFlip () {
    clearInterval(this._interval);
    clearTimeout(this._timeout);
  }

  /**
   * Method enables auto flipping
   * @private
   */
  _startAutoFlip () {
    let {
      documentNavigator,
      getNextDocumentData,
      store,
      persistency,
      viewerType,
      funnelSettings: { jumpMarker, funnel, markers },
      viewerStore,
      prefetch,
    } = this.props;
    let autoFlippingLimit = persistency.get({
      type: 'FNB',
      module: 'autoFlippingLimit',
    });
    let crossFamilyBoundary = persistency.get({
      type: 'FNB',
      module: 'familyBoundarySetting',
    });
    // Before each new auto flipping, we should stop previous
    this._stopAutoFlip();
    let isLoaded = !!this._getLoadedImage(store.mode);
    this._nextImageTime = this._getNextTime(store);
    this._interval = setInterval(() => {
      if (!isLoaded) {
        if (this._getLoadedImage(store.mode)) {
          //reset _nextImageTime if the image is just loaded
          this._nextImageTime = this._getNextTime(store);
          isLoaded = true;
        }
      }
      let missedDocument = this.rootNode.parentNode.querySelector(
        '.missed-document-marker',
      );
      // Check if previous image was loaded
      if (!isLoaded && missedDocument !== null) {
        // if not we are going to wait until it will be loaded
        // it works only for single view
        this._nextImageTime = 0;
      }

      let timeNow = new Date().getTime();
      if (isLoaded && this._nextImageTime === 0) {
        this._nextImageTime += store.speed / store.denominator;
        return;
      } else if (
        this._nextImageTime > timeNow ||
        (!isLoaded && this._nextImageTime < timeNow && missedDocument === null)
      ) {
        return;
      } else {
        this._nextImageTime = 0;
      }

      // trying to open next image
      isLoaded = false;
      let goNext = store.nextActiveFigure(
        jumpMarker,
        funnel === true,
        funnel === true ? markers : {},
        store.full === 1 ? 1 : store.modeWidth * store.mode,
      );
      this._nextImageTime = this._getNextTime(store);
      if (goNext === false) {
        // if not trying to open next document
        if (
          crossFamilyBoundary &&
          documentNavigator &&
          documentNavigator.hasNext()
        ) {
          this._stopAutoFlip();
          if (autoFlippingLimit <= store.autoFlipCounter + 1) {
            transaction(() => {
              store.updateAutoFlipping(false);
              store.updateAutoFlippingContinue(false);
            });
            if (autoFlippingLimit > 0) {
              this.setState({
                reachedLimit: true,
              });
            }
            return;
          }
          if (missedDocument !== null) {
            this._timeout = setTimeout(() => {
              if (!this.rootNode) return;
              let missedDocument = this.rootNode.parentNode.querySelector(
                '.missed-document-marker',
              );
              if (missedDocument !== null) {
                const nextPublications = documentNavigator ? documentNavigator.getNext() : null;
                prefetch && prefetch(viewerType, nextPublications);
                getNextDocumentData(viewerType);
              }
            }, NEXT_PUBLICATION_TIMEOUT);
          } else {
            const nextPublications = documentNavigator ? documentNavigator.getNext() : null;
            prefetch && prefetch(viewerType, nextPublications);
            getNextDocumentData(viewerType);
          }
          transaction(() => {
            store.autoFlipCounter += 1;
            store.updateAutoFlipping(false);
            store.updateAutoFlippingContinue(true);
          });
        } else {
          // if there is no other documents stop auto flipping
          transaction(() => {
            store.updateAutoFlipping(false);
            store.updateAutoFlippingContinue(false);
            store.autoFlipCounter = 0;
          });
          this._stopAutoFlip();
        }
      } else {
        transaction(() => {
          //if we loaded new document we should enable auto flipping
          if (store.autoFlippingContinue === true) {
            store.updateAutoFlipping(true);
          }
          store.updateAutoFlippingContinue(false);
        });
      }
    }, 50);
  }

  _getNextTime (store) {
    return new Date().getTime() + store.speed / store.denominator;
  }

  /**
   * Returns loaded images depends on mode
   * @param mode
   * @returns {*}
   * @private
   */
  _getLoadedImage (mode) {
    //It is assumed that autoflipping waits for all entities has been loaded, so it is tested if spinner is shown.
    //If autoflipping does not wait it, react is overloaded with rendering entities and does not respond correctly on autoflipping actions.
    //As result react skips rendering one ore two images in a row.
    //It usually happens when a new document is loaded, especially with splitted view mode. So first and second images are not shown often.
    if (!document.querySelector('.Spinner__spinner')) {
      const isFigureShown = !!(this.rootNode && this.rootNode.parentNode);
      if (mode === 1) {
        const image =
          isFigureShown &&
          this.rootNode.parentNode.querySelector(
            '.figureView img.loaded, .figureView .loadingError',
          );
        let loaded = true;
        // check if sidebar images have been loaded
        const sidebar =
          isFigureShown &&
          this.rootNode.parentNode.querySelectorAll(
            '.MosaicView__thumbs img.loading',
          );
        if (sidebar && sidebar.length) {
          sidebar.forEach((img) => {
            loaded = loaded && img.className.indexOf('loaded') !== -1;
          });
        }
        return loaded && image ? image : null;
      } else {
        let loaded = true;
        let loadedImages =
          isFigureShown &&
          this.rootNode.parentNode.querySelectorAll(
            '.MosaicView__mosaic img.loading',
          );
        if (loadedImages && loadedImages.length) {
          loadedImages.forEach((img) => {
            loaded = loaded && img.className.indexOf('loaded') !== -1;
          });
          return loaded && loadedImages ? loadedImages : null;
        } else {
          loadedImages =
            isFigureShown &&
            this.rootNode.parentNode.querySelectorAll(
              '.MosaicView__mosaic img.loaded',
            );
          return loadedImages && loadedImages.length ? loadedImages : null;
        }
      }
    }
    return null;
  }

  /**
   * Method checks if auto flipping was incorrectly set on previous document
   * Ex. when next document doesn't have images
   * @private
   */
  _checkAutoFlip = () => {
    let {
      store,
      store: { autoFlipping, autoFlippingContinue },
      persistency,
    } = this.props;
    // check if for some reason autoFlipping and autoFlippingContinue true
    let autoFlippingLimit = persistency.get({
      type: 'FNB',
      module: 'autoFlippingLimit',
    });

    if (
      autoFlippingLimit !== 0 &&
      (autoFlippingContinue === true || autoFlipping === true) &&
      autoFlippingLimit <= store.autoFlipCounter
    ) {
      transaction(() => {
        store.updateAutoFlipping(false);
        store.updateAutoFlippingContinue(false);
        store.autoFlipCounter = 0;
        this.setState({
          reachedLimit: true,
        });
      });
      this._stopAutoFlip();
    } else if (autoFlippingContinue === true && autoFlipping === true) {
      transaction(() => {
        store.updateAutoFlipping(false);
        store.updateAutoFlippingContinue(false);
        store.autoFlipCounter = 0;
      });
      this._stopAutoFlip();
    } else if (autoFlippingContinue === true || autoFlipping === true) {
      if (this.state.reachedLimit === true) {
        this.setState({
          reachedLimit: false,
        });
      }
      // start auto flipping if we open new document
      this._startAutoFlip();
    } else if (autoFlippingContinue === false && autoFlipping === false) {
      store.autoFlipCounter = 0;
    }
  };

  _onKeyPressHandler = (evt) => {
    // stop auto flipping after clicking on any part of the page
    if (!evt.keyCode || evt.keyCode !== 32) {
      let {
        store,
        store: { autoFlipping, autoFlippingContinue },
      } = this.props;
      if (autoFlipping === true || autoFlippingContinue === true) {
        let parents = nodeParentsWithClass(
          evt.target,
          `autoFlip-event-allowed`,
        );
        if (parents.length === 0 || (evt.keyCode && evt.keyCode !== 32)) {
          if (autoFlipping === true) {
            store.updateAutoFlipping(false);
            store.autoFlipCounter = 0;
          }
          if (autoFlippingContinue === true)
            store.updateAutoFlippingContinue(false);
        }
      }
    }
  };

  /**
   * Set first image active if a user activated autoflipping on current publication
   * @param store
   */
  @action
  _resetActiveFigureIndex (store) {
    if (!this._reset) {
      this._reset = true;
      store.activeFigure = 0;
    }
  }

  componentDidUpdate () {
    this._checkAutoFlip();
  }

  componentDidMount () {
    this._checkAutoFlip();
    // Apply listener for key down event
    document.addEventListener('keydown', this._onKeyPressHandler);
    document.addEventListener('mousedown', this._onKeyPressHandler);
  }

  componentWillUnmount () {
    this.props.store.updateAutoFlipping(false);
    if (this.props.store.autoFlippingContinue === false) {
      this.props.store.autoFlipCounter += 1;
    }
    this._stopAutoFlip();
    // Remove listener on after removing component
    document.removeEventListener('keydown', this._onKeyPressHandler);
    document.removeEventListener('mousedown', this._onKeyPressHandler);
  }

  hideLimitMessage = (cancel = true) => {
    let { store } = this.props;
    this.setState(
      {
        reachedLimit: false,
      },
      () => {
        if (cancel === false) {
          transaction(() => {
            store.autoFlipCounter = -1;
            store.updateAutoFlipping(true);
          });
          this._checkAutoFlip();
        }
      },
    );
  };

  updateLimit = () => {
    if (this.state.change === false) {
      this.setState({
        change: true,
      });
    } else {
      this.props.persistency.set({
        type: 'FNB',
        module: 'autoFlippingLimit',
        data: Number(this.limitValueNode.value),
      });
      this.setState({
        change: false,
      });
    }
  };

  renderLimitMessage = () => {
    let autoFlippingLimit = this.props.persistency.get({
      type: 'FNB',
      module: 'autoFlippingLimit',
    });
    let buttonText = this.state.change === false ? `Change settings` : `Change`;

    return (
      <div className={classes.limit}>
        <div className={'panel panel-default'}>
          <div className={'panel-body'}>
            <div>
              <p>Autoflip next {autoFlippingLimit} results?</p>
              <div className={classes.buttons}>
                <span
                  className={`glyphicon glyphicon-info-sign ${classes.info}`}
                />
                <button
                  type={`button`}
                  className={`btn btn-default`}
                  onClick={() => {
                    this.hideLimitMessage(false);
                  }}
                >
                  Yes
                </button>
                <span>or</span>
                <button
                  type={`button`}
                  className={`btn btn-default`}
                  onClick={() => {
                    this.hideLimitMessage(true);
                  }}
                >
                  No
                </button>
              </div>
              <div className={classes.settings}>
                {this.state.change === true ? (
                  <input
                    ref={(node) => {
                      this.limitValueNode = node;
                    }}
                    type={`text`}
                    className={`form-control ${classes.limitValue}`}
                    defaultValue={autoFlippingLimit}
                  />
                ) : null}
                <button
                  className={`btn btn-default`}
                  type={`submit ${classes.change}`}
                  onClick={this.updateLimit}
                >
                  {buttonText}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  render () {
    let {
      store: {
        autoFlipping,
        autoFlippingContinue,
        autoFlippingStarted,
        startAutoFlipping,
      },
    } = this.props;
    if (autoFlippingContinue === true || autoFlipping === true) {
      if (autoFlippingStarted === false) {
        //a user pressed on autoflipping before (on other document), reset active figure index
        this._resetActiveFigureIndex(this.props.store);
      } else {
        //a user pressed on autoflipping at the moment, keep active figure index
        startAutoFlipping(false);
      }
      this._startAutoFlip();
    } else {
      this._stopAutoFlip();
    }
    return (
      <div
        ref={(node) => {
          this.rootNode = node;
        }}
      >
        {this.state.reachedLimit === true ? this.renderLimitMessage() : <div />}
      </div>
    );
  }
}

FigureAutoFlip.propTypes = {
  store: PropTypes.object,
  funnelSettings: PropTypes.object.isRequired,
  viewerType: PropTypes.string.isRequired,
  documentNavigator: PropTypes.object,
  persistency: PropTypes.object,
  getNextDocumentData: PropTypes.func,
};

export default FigureAutoFlip;
