import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer, inject} from 'mobx-react';
import FontAwesome from 'react-fontawesome';
import ReactBootstrapSlider from 'react-bootstrap-slider';

import classes from './AutoFlipSpeedSlider.scss';

@inject((stores) => ({
  figuresStore: stores.figuresStore
}))
@observer
class AutoFlipSpeedSlider extends Component {
  constructor (props) {
    super(props);
    this.state = {
      active: false
    };
  }

  /**
   * Handle when auto-flipper is visible
   */
  handleMouseOver = () => {
    this.setState({
      active: true
    });
  };

  /**
   * Handle when auto-flipper is visible
   */
  handleMouseOut = () => {
    this.setState({
      active: false
    });
  };

  /**
   * Handle activate auto-flipper
   */
  handleAutoFlipStart = () => {
    let {store: {denominatorMinValue, updateAutoFlipping, updateDenominator, startAutoFlipping}} = this.props;
    updateDenominator(denominatorMinValue);
    updateAutoFlipping(true);
    startAutoFlipping(true);
  };

  /**
   * Change Auto Flipping speed
   * @param event{Event} - click event
   */
  handleChangeAutoFlipSpeed = (event) => {
    if (this.state.active) {
      this.props.store.updateSpeed(Math.ceil(event.target.value * 1000));
    }
  };

  render () {
    let {store: {autoFlipping, autoFlippingContinue, denominator, denominatorMinValue}, figuresStore} = this.props;
    let speed = figuresStore.globalSpeed;
    return (
      <div className='btn-group'>
        <div
          className={classes.speedRange}
          onMouseOver={this.handleMouseOver}
          onMouseOut={this.handleMouseOut}
        >
          <button
            type='button'
            className={`btn btn-${(autoFlipping === true || autoFlippingContinue === true) && denominator === denominatorMinValue ? 'primary' : 'default'} autoFlip-event-allowed`}
            onMouseDown={this.handleAutoFlipStart}
          >
            <FontAwesome
              name='play'
              className={classes.awesomeIcons}
            />
            <span className={classes.speedRangeValue}>
              {speed / 1000}
            </span>
          </button>
          <div>
            <span className={classes.labels}>fast</span>
            <ReactBootstrapSlider
              change={this.handleChangeAutoFlipSpeed}
              max={5}
              min={1}
              step={0.1}
              ticks={[1, 2, 3, 4, 5]}
              orientation='vertical'
              value={speed / 1000}
            />
            <span className={classes.labels}>slow</span>
          </div>
        </div>
      </div>
    );
  }
}

AutoFlipSpeedSlider.propTypes = {
  store: PropTypes.object
};

export default AutoFlipSpeedSlider;
