import {FIGURES_MODULE_CLASS_NAME} from '@epo/web-common';

/**
 * Method checks if figures part opened on both panels
 * @param {Boolean} isPanelActive - attribute indicates that panel selected as active
 * @returns {Boolean} result - if figures on both panels
 */
export const isFiguresOnBothPanels = (isPanelActive) => {
  return document.querySelectorAll(`.${FIGURES_MODULE_CLASS_NAME}`).length === 2 && isPanelActive === false;
};

export function onAnyPanel (target, name, descriptor) {
  const original = descriptor.value;
  if (typeof original === 'function') {
    descriptor.value = function (...args) {
      if (isFiguresOnBothPanels(this.props.isPanelActive) === false) {
        try {
          return original.apply(this, args);
        } catch (e) {
          throw e;
        }
      }
    };
  }
  return descriptor;
}
