import React, {PureComponent} from 'react';
import classNames from 'classnames/bind';

import classes from './FigureToolTip.scss';

const TOP = 'top';
const BOTTOM = 'bottom';
let cx = classNames.bind(classes);

export default class FigureToolTip extends PureComponent {

  state = {
    // X center
    left: null,
    // Y center
    bottom: null,
    // tooltip text
    text: undefined,
    // if tooltip hovered
    hover: false,
    rootHeight: null,
    height: null,
    direction: TOP
  };

  /**
   * Method enable tooltip if annotation is passed
   * @param {number} left - left center line of element to which the tooltip belongs
   * @param {number} bottom - top line of element to which the tooltip belongs
   * @param {Object} annotation - annotation
   * @param {string} annotation.key - annotation key
   * @param {string} annotation.text - annotation text
   * @param {number} rootHeight - figures view port
   * @param {number} height - target (annotation) height
   */
  show = (left, bottom, annotation, rootHeight, height) => {
    if (annotation !== undefined) {
      this.setState({
        left,
        rootHeight,
        height,
        direction: TOP,
        text: annotation.key + ': ' + annotation.text
      });
      // update bottom separately in timeout, as setState is async, and prev state will sip through otherwise
      setTimeout(() => {
        const clientHeight = this.rootNode.getBoundingClientRect().height;
        const direction = bottom > clientHeight ? TOP : BOTTOM;
        this.setState({
          bottom: direction === TOP ? bottom - clientHeight : bottom + 60,
          direction: direction
        });
      }, 0);
    }
  };

  /**
   * Method disable tooltip
   * @param {boolean} [force=false] - ignore if element is hovered
   */
  hide = (force = false) => {
    if (this.state.hover !== true || force === true) {
      this.setState({
        left: null,
        bottom: null,
        text: undefined
      });
    }
  };

  /**
   * Handle when user move mouse over tooltip
   * @private
   */
  _handleMouseEnter = () => {
    this.setState({
      hover: true
    });
  };

  /**
   * Handle when user move mouse out tooltip
   * @private
   */
  _handleMouseOut = () => {
    this.setState({
      hover: false
    }, () => {
      this.hide();
    });
  };

  /**
   * Method checks is there are enough space to locate tooltip on top of annotation
   * @private
   */
  _checkDirection = () => {
    let {height} = this.rootNode.getBoundingClientRect();
    let {bottom, rootHeight, direction} = this.state;
    if (bottom && rootHeight && direction !== BOTTOM) {
      if (rootHeight < bottom + height + 10) {
        this.setState({
          direction: BOTTOM,
          bottom: this.state.bottom - this.state.height - height
        });
      }
    }
  };

  componentDidUpdate () {
    //Keep for rollback to not overlaying tooltips, and we need to check tooltip direction
    // this._checkDirection();
  }

  render () {
    const {left, bottom, text, direction} = this.state;

    return (
      <div
        ref={(node) => {this.rootNode = node}}
        className={
          cx({
            root: true,
            hide: left === null || bottom === null,
            bottom: direction === BOTTOM
          })
        }
        style={{
          left: left,
          top: bottom //As now all tooltips overlay content -- it always go Up instead of down
        }}
        dangerouslySetInnerHTML={{__html: text}}
        onMouseEnter={this._handleMouseEnter}
        onMouseOut={this._handleMouseOut}
      />
    );
  }
}
