/**
 * NPLByBusinessKeyRaw Raw response
 * @typedef {Object} NPLByBusinessKeyRaw
 * @property {Object} _links
 * @property {Object} _links.ht:self
 * @property {String} _links.ht:self.href
 * @property {String} xpNumber
 * @property {String|null} accessionNumber
 * @property {String|null} docId
 * @property {String} type
 * @property {String} supplier
 * @property {String|null} availability
 * @property {Number} groupNumber
 * @property {String} completeness
 * @property {Number} creationDate
 * @property {String} creationDateISO8601
 * @property {Number} lastUpdate
 * @property {String} lastUpdateISO8601
 * @property {Number} deliveryDate
 * @property {String} deliveryDateISO8601
 * @property {Object} biblio
 * @property {Object} _embedded
 * @property {Object} _embedded.document
 * @property {Object} _embedded.document._links
 * @property {Object} _embedded.document._links.facsimile
 * @property {String} _embedded.document._links.facsimile.href
 * @property {Object} _embedded.document._links.section
 * @property {String} _embedded.document._links.section.href
 * @property {Object} _embedded.sections
 * @property {Object} _embedded.sections.biblioSearch
 * @property {String} _embedded.sections.biblioSearch.sectionId
 * @property {Number} _embedded.sections.biblioSearch.facsimileFirstPageNumber
 * @property {Number} _embedded.sections.biblioSearch.facsimileLastPageNumber
 * @property {Object} _embedded.sections.biblioCitation
 * @property {String} _embedded.sections.biblioCitation.sectionId
 * @property {Number} _embedded.sections.biblioCitation.facsimileFirstPageNumber
 * @property {Number} _embedded.sections.biblioCitation.facsimileLastPageNumber
 * @property {Object} _embedded.sections.text
 * @property {String} _embedded.sections.text.sectionId
 * @property {Number} _embedded.sections.text.facsimileFirstPageNumber
 * @property {Number} _embedded.sections.text.facsimileLastPageNumber
 * @property {Object} _embedded.tei
 * @property {Object} _embedded.tei._links
 * @property {Object} _embedded.tei._links.teiSearch
 * @property {String} _embedded.tei._links.teiSearch.href
 * @property {Object} _embedded.tei._links.teiCitation
 * @property {String} _embedded.tei._links.teiCitation.href
 * @property {Object} _embedded.tei._links.teiComplete
 * @property {String} _embedded.tei._links.teiComplete.href
 */

/**
 * Annotations response
 * @typedef {Object} AnnotationsRaw
 * @property {Array} refPhrases - Array of referral phrases
 * @property {Array} refs - Array of referral keys
 * @property {Array} figures - Array of figures
 */

/**
 * Application annotations response
 * @typedef {Object} ApplicationAnnotations
 * @property {Array} figs - Array of figures
 */

/**
 * Parsed annotations
 * @typedef {Array<{source: String}>} parsedAnnotations
 *
 */

import union from 'lodash.union';
import get from 'lodash.get';

import {
  ES,
  http, paths, getHttpHeaders,
  DataCache,
  CROPPING_X, CROPPING_Y,
  NON_PATENT, UNPUBLISHED,
  getTransformedOrientationValue,
  escapeHTMLTags
} from '@epo/web-common';

/**
 * Highlight Annotations cache
 * @type {DataCache}
 */
export const highlightAnnotationsCache = new DataCache();

/**
 * Method returns figures for specific rid
 * @param {String} rid - publication rid
 * @param {String} source - publication source
 * @param {String} documentType - publication type
 * @returns {String}
 * @memberOf FiguresAPI
 */
export const getFigurePath = (rid, source, documentType) => {
  source = source.replace(/^\//, '');
  return paths.getFacsimile.replace('%docType', documentType).replace('%rid', rid).replace('%facsimileId', source);
};

/**
 * Method returns figure head information
 * @param {String} path - link to figure
 * @param {String} publicationNumber - publication number for MOSES
 * @returns {Promise<Object>} - figure image header details
 * @memberOf FiguresAPI
 */
export const fetchFigureHead = function (path, publicationNumber = '') {
  return new Promise((resolve, reject) => {
    if (publicationNumber && path.indexOf('publicationNumber') === -1) {
      path += `${(path.indexOf('?') === -1 ? '?' : '&')}publicationNumber=${publicationNumber}`;
    }
    http(path).head({
      headers: new Headers({
        'Accept': 'image/tiff',
        'Content-Type': 'image/png'
      })
    }).then((data) => {
      resolve(data);
    }).catch((err) => {
      if (err) {
        reject(err);
      }
    });
  });
};

/**
 * Method returns facsimilies for publication
 * @param {Object} params
 * @param {String} params.type - facsimiles type (e. drawings)
 * @param {String} params.publicationId - publication id
 * @param {String} params.documentType - publication type (application, publication, npl)
 * @returns {Promise}
 * @memberOf FiguresAPI
 */
export const getPublicationFacsimilies = function ({type, publicationId, documentType}) {
  if (documentType === NON_PATENT) {
    return Promise.resolve([]);
  }

  const url = paths.getFacsimiles.replace('%docType', documentType).replace('%rid', publicationId);

  /**
   * Publication facsimilies response
   * @typedef {Object} data - response
   * @property {Object} _embedded - facsimiles raw data
   * @property {Object} _embedded.facsimiles - facsimiles block
   * @memberOf FiguresAPI
   */
  return http(url).get().then(data => {
    let figures = [];
    let facsimiles = data._embedded.facsimiles[type];
    if (facsimiles && facsimiles.length) {
      /**
       * Facsimiles block drawing
       * @typedef {Object} drawing
       * @property {Number} startPage - facsimiles first id
       * @property {Number} endPage - facsimiles last id
       */
      facsimiles.forEach((drawing) => {
        const startPage = drawing.startPage || 1;
        const endPage = drawing.endPage || drawing.pages;
        for (let i = startPage; i <= endPage; ++i) {
          let path = paths.getFacsimileItemCrop
            .replace('%docType', documentType)
            .replace('%rid', publicationId)
            .replace('%facsimileId', drawing.id)
            .replace('%pageId', i);
          figures.push({
            source: path,
            sheet: i
          });
        }
      });
    }
    return figures;
  });

};

/**
 * Method call backend to retrieve publication annotations
 * @param familyId- publication family id
 * @param publicationIdShort - publication family id (10 max)
 * @returns {Promise<Object>} - list of annotations
 * @memberOf FiguresAPI
 */
export const fetchAnnotations = function ({familyId, publicationIdShort}) {
  const es = new ES(paths.ssl, Function.prototype);

  return new Promise((resolve) => {
    const handler = (data, status) => {
      if (status === 200 && data) {
        resolve(data || {});
      } else {
        let data = {hits: []};
        resolve(data || {});
      }
    };

    es.getFamily(familyId, publicationIdShort, [
      'publications.datasource',
      'publications.all_pubs_datasource',
      'oprid_full.untouched',
      'publications.figs',
      'publications.refs',
      'publications.refphrases_en',
      'publications.refphrases_de',
      'publications.refphrases_fr'
    ], handler, null);
  });
};

/**
 * Method applies annotation to figures
 * @param {Array} figs - Array of figures
 * @param {Array} refphrases - Array of referral phrases
 * @param {Array} refs - Array of referral keys
 * @param {String} publicationId - publication id
 * @param {boolean} isApp - is true if application's annotations are built
 * @returns {parsedAnnotations} - list of figures with annotations
 * @memberOf FiguresAPI
 */
export const buildAnnotations = function ({figs, refphrases, refs, publicationId, isApp}) {
  let figures = {};
  let references = {};

  if (refs) {
    refphrases.forEach((phrase, index) => {
      if (references[refs[index]] === undefined) {
        references[refs[index]] = phrase;
      } else {
        references[refs[index]] += ', ' + phrase;
      }
    });
  }

  figs = figs.sort((a, b) => a.sheet > b.sheet ? 1 : -1);

  figs.forEach((fig) => {
    if (typeof fig === 'string') fig = JSON.parse(fig);
    if (figures[fig.sheet] === undefined) {
      figures[fig.sheet] = {
        figure: fig.figure,
        orientation: getTransformedOrientationValue(fig.orientation),
        reliability: fig.reliability,
        sheet: fig.sheet,
        angle: 0,
        zoom: 1,
        boxes: []
      };
    }
    figures[fig.sheet]['boxes'] = [...figures[fig.sheet]['boxes'], ...fig.boxes.map((box) => {
      let x = box[1] - CROPPING_X;
      let y = box[2] - CROPPING_Y;
      const figOrientation = getTransformedOrientationValue(fig.orientation);
      let width = figOrientation === 0 && !isApp ? box[4] : box[3];
      let height = figOrientation === 0 && !isApp ? box[3] : box[4];
      let cx = x + width / 2;
      let cy = y + height / 2;

      return {
        key: box[0],
        references: box[0],
        text: references[box[0]],
        x,
        y,
        width,
        height,
        cx,
        cy
      };
    })];
  });

  return figures;
};

/**
 * Method parse annotations
 * @param {Object} data - annotations raw data
 * @param {Array} data.hits - Array of annotations
 * @param {String} publicationId - publication id
 * @returns {AnnotationsRaw}
 * @memberOf FiguresAPI
 */
export const parseAnnotations = function ({data, publicationId}) {
  let figures = {};
  let refphrases = [];
  let refs = [];

  data.hits.forEach(arr => {
    arr.hits.forEach(hit => {
      let [datasource] = hit.fields['publications.datasource'];
      if (datasource) {
        if (datasource.id !== publicationId) {
          return;
        }
      }

      let figs = hit.fields['publications.figs'];
      if (!figs) return;
      let refphrasesEN = hit.fields['publications.refphrases_en'];
      let refphrasesDE = hit.fields['publications.refphrases_de'];
      let refphrasesFR = hit.fields['publications.refphrases_fr'];
      let _refs = hit.fields['publications.refs'] || [];
      let _refphrases = [];

      if (refphrasesEN !== undefined) {
        _refphrases = refphrasesEN;
      } else if (refphrasesDE !== undefined) {
        _refphrases = refphrasesDE;
      } else if (refphrasesFR !== undefined) {
        _refphrases = refphrasesFR;
      }

      _refs.forEach((ref, i) => {
        ref.forEach(r => {
          refs.push(r);
          refphrases.push(_refphrases[i]);
        });
      });

      figures = buildAnnotations({
        figs,
        refphrases,
        refs,
        publicationId,
        isApp: false
      });
    });
  });

  return {
    figures, refPhrases: refphrases, refs
  };
};

