/**
 * Method call backend to get highlighting for passed reference phrases and markers
 *
 * @param {Array} ref_phrases - reference phrases for annotations
 * @param {Object} markers - Object of active markers
 * @returns {Promise<Array<String>>} - Array of highlighted markers
 * @memberOf FiguresAPI
 */
export const highlightAnnotations = function ({id, ref_phrases, markers}) {
  const params = {markers: markers, id: id};
  return highlightAnnotationsCache.getDocumentFromCache(params).then((response) => {
    return Promise.resolve(response);
  }).catch(() => {
    return getHighlightAnnotations({params, ref_phrases});
  });
};

function getHighlightAnnotations ({params, ref_phrases}) {
  // adding Pending object into cache
  highlightAnnotationsCache.add(params);
  const {markers} = params;
  const url = paths.highlightComposite;
  return new Promise((resolve, reject) => {
    if (!ref_phrases || !ref_phrases.length || !markers) {
      // if error resolve empty array that means markers are not going to be updated
      resolve([]);
    } else {
      const args = {
        'field': 'publications.refphrases_en',
        'default_pql_fields': ['FIG'],
        'strict': true,
        'text': ref_phrases.map(phrase => escapeHTMLTags(phrase.replace(/<\/?span[^>]*>/g, ''))),
        'markers': markers,
        'key': 1
      };
      const headers = getHttpHeaders({
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      });
      http(url).post({
        body: JSON.stringify({'text-items': [args]}),
        headers: headers
      }).then((data) => {
        highlightAnnotationsCache.set(params, data[0].response);
        resolve(data[0].response);
      }).catch((err) => {
        highlightAnnotationsCache.remove(params);
        if (err) {
          reject(err);
        }
      });
    }
  });
};

/**
 *
 * @param {NPLByBusinessKeyRaw} data
 * @returns {Array}
 */
const parseFiguresFROMNPL = (data) => {
  let figures = [];
  const figuresPath = get(data, '_embedded.document._links.facsimile.href', '');
  if (data && data._embedded && data._embedded.sections && figuresPath) {
    const sections = data._embedded.sections;
    for (let i in sections) {
      if (sections.hasOwnProperty(i) === true) {
        const section = sections[i];
        if (section.facsimileFirstPageNumber && section.facsimileLastPageNumber) {
          const pagesCount = section.facsimileLastPageNumber - section.facsimileFirstPageNumber + 1;
          const sectionFigures = Array.apply(null, Array(pagesCount)).map((ignore, index) => {
            let pageNumber = section.facsimileFirstPageNumber + index;
            return figuresPath.replace('{pageNumber}', pageNumber).replace('{format}', 'png');
          });
          figures = union(figures, sectionFigures);
        }
      }
    }
  }
  return figures.map((figure, index) => {
    return {
      angle: 0,
      boxes: [],
      figuresMax: {
        width: 2592,
        height: 3508
      },
      sheet: index,
      source: figure,
      type: 'biblio',
      zoom: 1
    };
  });
};

/**
 *
 * @param XPBusinessKey
 * @returns {Promise<Array<FigureModel>>}
 */
export const getNPLFiguresByBusinessKey = (XPBusinessKey) => {
  const url = paths.getNPLByBusinessKey.replace('%xpBusinessKey', XPBusinessKey);
  return new Promise((resolve, reject) => {
    http(url)
      .get()
      .then(data => {
        const figures = parseFiguresFROMNPL(data);
        if (figures.length) {
          resolve(figures);
        } else {
          reject(figures);
        }
      })
      .catch(err => {
        console.warn(`Error when loading ${XPBusinessKey} figures: `, err);
        reject(err);
      });
  });
};

/**
 * Executes post request (Sprites)
 * Returns a sprite url and image dimensions
 * @param url
 * @param body contains facsimileIds of images
 * @returns {Promise}
 */
export const getFigureUrl = function (url, body) {
  return new Promise((resolve, reject) => {
    if (!url || !body) {
      // if error resolve empty string
      Promise.reject('getFigureUrl error');
    } else {
      http(url).post({
        headers: new Headers({'Accept': 'image/png', 'Content-Type': 'text/plain'}),
        body: body
      }).then(({blob, headers}) => {
        const blobUrl = URL.createObjectURL(blob);
        const height = headers.get('X-Image-Meta-Height').split(',');
        const width = headers.get('X-Image-Meta-Width').split(',');
        const orWidth = headers.get('X-Image-Meta-Original-Width').split(',');
        const orHeight = headers.get('X-Image-Meta-Original-Height').split(',');
        const crops = headers.get('X-Image-Meta-Crop-Rectangle');
        const crop = crops ? crops.split(' ') : [];
        resolve({blobUrl, sizes: {height, width, orWidth, orHeight, crop}});
      }).catch((err) => reject(err));
    }
  });
};
