import {action, computed, observable, transaction} from 'mobx';
import findIndex from 'lodash.findindex';
import { MOSES_ALLOWED, PATENT, NON_PATENT, APPLICATION } from '@epo/web-common';
import {setSprite} from '../../Figures.Helper';
import {Figure} from './index';


// Publication model loading statuses
const CREATED = 0; // model was created, but didn't received any data
const FULFILLED = 1; // model received data.
const REJECTED = -1; // model failed to receive figures data
const CANCELED = -2; // model failed to receive bns data

/**
 * Publication
 *
 * @export
 * @class Publication
 */
export default class PublicationModel {
  familyId;
  publicationId;
  publicationIdShort;
  activeFigure = observable.map();
  documentType;
  @observable markers = [];
  @observable _figures = [];
  @observable loaded = CREATED;
  @observable refPhrases = [];
  @observable refs = [];

  /**
   * Creates an instance of Publication.
   *
   * @param {string} familyId - publication family id @example 045798243
   * @param {string} publicationId - publication id @example 352990458
   * @param {string} publicationIdShort - publication Short ID @example CN202162391U
   * @param {Array} [markers=[]] - Publication markers
   * @param {Array} [figures=[]] - Publication figures
   * @param {function} onCreate - current active tab
   * @param {boolean} preLoaded - is images was pre loaded
   * @param {Array|undefined} annotations - Publication annotations
   * @param {Array} refs - array of referral keys
   * @param {Array} refPhrases - array of referral phrases
   * @param {String|undefined} publicationNumber - Publication publicationNumber
   * @param {Object} api - a model's API
   *
   * @param documentType
   * @memberOf Publication
   */

  constructor (familyId, publicationId, publicationIdShort, figures, markers = [],
               onCreate = () => {}, preLoaded = false, annotations, refs, refPhrases,
               publicationNumber = undefined, documentType, viewerType, api) {
    this.familyId = familyId;
    this.publicationId = publicationId;
    this.publicationIdShort = publicationIdShort;
    this.markers = markers;
    this._figures = [];
    this.publicationNumber = publicationNumber;
    this.documentType = documentType;
    this.api = api;

    const isBNS = publicationId && publicationId.indexOf('BNS') !== -1;

    // Empty publication (unknown document)
    if (!publicationId) {
      this.loaded = CANCELED;
    } else if (isBNS && documentType === NON_PATENT) {
      let number = publicationNumber || publicationId.replace(/(bns-)(xp.*)xp(.*)/ig, '$2');
      if (number.indexOf('BNS') !== -1) {
        number = number.replace(/(bns-)(xp.*)/ig, '$2');
      }
      this.api.getNPLFiguresByBusinessKey(number)
        .then((figures) => {
          this.insertFigures(figures);
          setSprite(this.figures);
          this.loaded = FULFILLED;
        })
        .catch(err => {
          console.warn(`Error while loading figures data`, err);
          this.loaded = CANCELED;
        });
    } else if (isBNS && figures && figures.length === 0 && this._figures.length === 0 && this.loaded === REJECTED) {
      this.loaded = CANCELED;
    } else {
      // Publication enter
      new Promise((resolve, reject) => {
        if (figures === undefined) {
          this.fetchData().then(figures => {
            resolve(figures);
          }).catch(err => {
            err.missed = true;
            reject(err);
          });
        } else if (figures.length === 0) {
          let err = new Error('No figures found');
          err.missed = true;
          reject(err);
        } else if (preLoaded === true) {
          figures.map((figure) => {
            figure.source = this.api.getFigurePath(this.publicationId, figure.source, this.documentType);
            return figure;
          });
          resolve(figures);
        } else if (documentType === PATENT && viewerType === APPLICATION) {
          return this.fetchData()
            .then(figures => resolve(figures))
            .catch(err => {
              err.missed = true;
              reject(err)
            });
        } else {
          resolve(figures);
        }
      })
        .then((figures) => {
          this.insertFigures(figures);
        })
        .then(() => {
          return new Promise((resolve, reject) => {
            if (preLoaded === true) {
              this.refPhrases = refPhrases;
              this.refs = refs;
              resolve();
            } else if (isBNS) {
              resolve();
            } else {
              return this.api.fetchAnnotations({
                familyId: this.familyId,
                publicationIdShort: this.publicationIdShort
              })
                .then((data) => {
                  return this.api.parseAnnotations({
                    data,
                    publicationId: this.publicationId
                  });
                })
                .then((data) => {
                  this.refPhrases = data.refPhrases;
                  this.refs = data.refs;
                  for (let i in data.figures) {
                    if (data.figures.hasOwnProperty(i) === true) {
                      this.updateFigure(data.figures[i]);
                    }
                  }
                })
                .then(data => resolve(data))
                .catch(err => {
                  err.missed = true;
                  reject(err);
                });
            }
          });
        })
        .then(() => {
          setSprite(this.figures);
          this.loaded = FULFILLED;
          onCreate({figures: this.figures});
        })
        .catch((err) => {
          this.loaded = REJECTED;
          if (err && err.missed === true) {
            this.loaded = isBNS === true ? CANCELED : REJECTED;
          }
          onCreate({figures: []});
        });
    }
  }

  @computed
  get figures () {
    return this._figures;
  }

  /**
   * Add figures to publication
   * @param {string|undefined} lang - figure language
   * @param {string} source - figure link to image
   * @param {string|undefined} figure - figure order in publication
   * @param {number} orientation - figure orientation (1 - turn to 90 deg, 0 - do nothing)
   * @param {number} reliability - figure reliability
   * @param {number} sheet - figure sheet number
   * @param {number} angle - figure angle (by default based on @param orientation)
   * @param {number} zoom - figure zoom level
   * @param {Object} max - figure maximal height and height
   * @param {number} max.width - figure maximal width
   * @param {number} max.height - figure maximal height
   * @param {Array} annotations - figure annotations array
   * @param {string} type - figure type (ex. biblio)
   * @memberOf Publication
   */
  addFigures = (lang, source, figure, orientation, reliability, sheet, angle, zoom, max, annotations = [], type = '') => {
    this._figures.push(
      new Figure(lang, source, figure, orientation, reliability, sheet, angle, zoom, max, annotations, type, this.api));
    if (this.activeFigure === null && this._figures.length > 0) {
      this.setActiveFigure(0);
    }
  };

  updateFigure = (params) => {
    const index = findIndex(this._figures, {'sheet': params.sheet});
    if (index === -1) return;

    this._figures[index]['lang'] = params.lang;
    this._figures[index]['figure'] = params.figure;
    this._figures[index]['orientation'] = params.orientation;
    this._figures[index]['reliability'] = params.reliability;
    this._figures[index]['sheet'] = params.sheet;
    this._figures[index]['angle'] = params.angle;
    this._figures[index]['zoom'] = params.zoom;

    let isSwapped = false;
    if (params.orientation === 0 && this._figures[index]['angle'] === 0) {
      isSwapped = true;
      this._figures[index]['angle'] = 90;
    }
    this._figures[index]['mosaicAngle'] = this._figures[index]['angle'];
    transaction(() => {
      params.boxes.forEach((annotation) => {
        let {
          key,
          text,
          x,
          y,
          width,
          height
        } = annotation;
        [width, height] = !isSwapped ? [width, height] : [height, width];
        this._figures[index].addAnnotation({
          key, text, x, y, width, height
        });
      });
    });
    this._figures[index].annLength = this._figures[index].annotations.length;
  };

  /**
   * Set publication active figure by index
   *
   * @param {string} viewerType - viewer type
   * @param {string} panelName - viewer panel
   * @param {number} index - figure index
   * @param {boolean} skipMissed - skip -1
   *
   * @memberOf Publication
   */
  setActiveFigure = (viewerType, panelName, index, skipMissed = false) => {
    let name = viewerType + panelName;
    if (((index >= -1 && skipMissed === false) || index >= 0) && index < this._figures.length) {
      return this.activeFigure.set(name, index);
    } else {
      return false;
    }
  };

  setActiveFigureByType = (viewerType, panelName, type) => {
    if (type !== undefined) {
      let found = false;
      this._figures.forEach((figure, index) => {
        if (found === false && figure.type === type) {
          this.setActiveFigure(viewerType, panelName, index);
          found = true;
        }
      });
      if (found === false && this.getActiveFigure(viewerType, panelName) === -1) {
        this.firstActiveFigure(viewerType, panelName, '*');
      }
    }
  };

  getActiveFigure = (type, panel) => {
    let name = type + panel;
    return this.activeFigure.get(name) || 0;
  };

  _markersObjectToArray = (markersObj) => {
    let markers = [];
    for (let i in markersObj) {
      if (markersObj.hasOwnProperty(i) === true) {
        if (markersObj[i] === true) {
          markers.push(i);
        }
      }
    }
    return markers;
  };

  nextActiveFigure = (type, panel, funnel, marker, markersObj, step) => {
    let steps = 0;
    if (marker === '*') {
      let markers = this._markersObjectToArray(markersObj);
      if (markers.length > 0 || funnel === true) {
        for (let i = this.getActiveFigure(type, panel) + 1; i < this.figures.length; i += 1) {
          for (let j = 0; j < markers.length; j += 1) {
            if (this.figures[i]['markers'].indexOf(markers[j]) !== -1) {
              steps += 1;
              if (steps === step) {
                return this.setActiveFigure(type, panel, i);
              }
            }
          }
        }
      } else {
        const index = this.getActiveFigure(type, panel) + step;
        if (step === 0 || step === 1 || index < this._figures.length) {
          return this.setActiveFigure(type, panel, index);
        } else {
          //check whether the last figure is shown
          const el = document.querySelector(`.MosaicView__mosaic > div > section[data-id="${this._figures.length - 1}"]`);
          const mosaic = document.querySelector(`.MosaicView__mosaic`);
          if (mosaic && el) {
            const elRect = el.getBoundingClientRect();
            const mosaicRect = mosaic.getBoundingClientRect();
            if ((2 * elRect.height / 3 + elRect.top) < mosaicRect.bottom) {
              return this.setActiveFigure(type, panel, index);
            }
          }
          return this.setActiveFigure(type, panel, this._figures.length - 1);
        }
      }
    } else {
      for (let i = this.getActiveFigure(type, panel) + 1; i < this.figures.length; i += 1) {
        if (this.figures[i]['markers'].indexOf(marker) !== -1) {
          steps += 1;
          if (steps === step) {
            return this.setActiveFigure(type, panel, i);
          }
        }
      }
    }
    return false;
  };

  prevActiveFigure = (type, panel, funnel, marker, markersObj, step, skipMissed = false) => {
    let steps = 0;
    if (marker === '*') {
      let markers = this._markersObjectToArray(markersObj);
      if (markers.length > 0 || funnel === true) {
        for (let i = this.getActiveFigure(type, panel) - 1; i >= 0; i -= 1) {
          for (let j = 0; j < markers.length; j += 1) {
            if (this.figures[i]['markers'].indexOf(markers[j]) !== -1) {
              steps += 1;
              if (steps === step) {
                return this.setActiveFigure(type, panel, i, skipMissed);
              }
            }
          }
        }
      } else {
        const index = this.getActiveFigure(type, panel) - step;
        if (step === 0 || step === 1 || index >= 0) {
          return this.setActiveFigure(type, panel, index, skipMissed);
        } else {
          //check whether the first figure is shown
          const el = document.querySelector(`.MosaicView__mosaic > div > section[data-id="0"]`);
          if (el && el.getBoundingClientRect().top >=0) {
            return this.setActiveFigure(type, panel, index, skipMissed);
          }
          return this.setActiveFigure(type, panel, 0, skipMissed);
        }
      }
    } else {
      for (let i = this.getActiveFigure(type, panel) - 1; i >= 0; i -= 1) {
        if (this.figures[i]['markers'].indexOf(marker) !== -1) {
          steps += 1;
          if (steps === step) {
            return this.setActiveFigure(type, panel, i, skipMissed);
          }
        }
      }
    }
    return false;
  };

  firstActiveFigure = (type, panel, marker) => {
    if (marker === '*') {
      this.setActiveFigure(type, panel, 0);
    } else {
      for (let i = this.getActiveFigure(type, panel) - 1; i >= 0; i -= 1) {
        if (this.figures[i]['markers'].indexOf(marker) !== -1) {
          return this.setActiveFigure(type, panel, i);
        }
      }
    }
  };

  lastActiveFigure = (type, panel, marker) => {
    if (marker === '*') {
      this.setActiveFigure(type, panel, this.figures.length - 1);
    } else {
      for (let i = this.getActiveFigure(type, panel) - 1; i >= 0; i -= 1) {
        if (this.figures[i]['markers'].indexOf(marker) !== -1) {
          return this.setActiveFigure(type, panel, i);
        }
      }
    }
  };

  /**
   * Call publication active figure zoom action @example in, out, reset
   * @param {string} type - viewer type
   * @param {string} panel - viewer panel
   * @param {string} action - zooming action
   *
   * @memberOf Publication
   */
  updateActiveFigureZoom = (type, panel, action) => {
    let activeFigure = this.getActiveFigure(type, panel);
    this._figures[activeFigure].updateZoom(action);
  };

  /**
   * Change publication active figure angle on @param deg
   * @param {string} type - viewer type
   * @param {string} panel - viewer panel
   * @param {number} deg - angle which should be added to the current value
   *
   * @memberOf Publication
   */
  updateActiveFigureAngle = (type, panel, deg) => {
    let activeFigure = this.getActiveFigure(type, panel);
    this._figures[activeFigure].updateAngle(deg);
  };

  // /**
  //  * Update publication markers
  //  *
  //  * @param {markers} [markers=[]] - angle which should be added to the current value
  //  *
  //  * @memberOf Publication
  //  */
  // updateMarkers = (markers = []) => {
  //   this.markers = markers;
  // };

  insertFigures = (figures) => {
    transaction(() => {
      figures.forEach((figure) => {
          this.addFigures(
            figure.lang,
            figure.source + (this.publicationNumber !== undefined && MOSES_ALLOWED === true ? `&publicationNumber=${this.publicationNumber}` : ``),
            undefined,
            figure.orientation !== undefined ? figure.orientation : 1,
            figure.reliability || 0,
            figure.sheet,
            0,
            1,
            undefined,
            figure.annotations || [],
            figure.type
          );
        }
      );
    });
  };

  /**
   * Fetch publication data
   *
   * @memberOf Publication
   */
  @action
  fetchData = () => {
    return this.api.getPublicationFacsimilies({
      type: 'drawings',
      publicationId: this.publicationId,
      publicationIdShort: this.publicationIdShort,
      documentType: this.documentType
    })
      .then((figures) => {
        if (!figures || !figures.length) {
          return Promise.reject(new Error({
            missed: true
          }));
        }
        return figures;
      });
  };

  /**
   * Hightlight markers for publication
   *
   * @memberOf Publication
   */
  highlightMarkers = (markers, applySynonyms) => {
    let markersObj = {};
    const modifiedMarkers = {};
    markers.forEach((marker) => {
      markersObj['m' + marker.name] = {
        bgcolor: marker.bgcolor,
        color: marker.color
      };
    });
    markers
      .filter(marker => marker.status === 0)
      .forEach(marker => {
        if (marker.content && marker.content.trim()) {
          modifiedMarkers[`m${marker.name}`] = applySynonyms ? applySynonyms(marker.content) : marker.content;
        }
      });
    this.api.highlightAnnotations({
      id: this.publicationId,
      ref_phrases: this.refPhrases,
      markers: modifiedMarkers //markers
    }).then((data) => {
      if (!data || data.length === 0) return;
      let refs = {};
      if (this.refs !== undefined) {
        this.refs.forEach((key, index) => {
          if (refs[key] === undefined) {
            refs[key] = data[index];
          } else {
            refs[key] += ', ' + data[index];
          }
        });
        transaction(() => {
          this._figures.forEach((figure) => {
            figure.updateAnnotationsText(refs, markersObj);
          });
        });
      }
    }).catch((err) => {
      console.warn('FiguresStore.highlightMarkers.warning', err);
    });
  };

  getMarkers () {
    let markers = [];
    this.figures.forEach((figure) => {
      let figureMarkers = figure.getMarkers();
      figureMarkers.forEach((marker) => {
        if (markers.indexOf(marker) === -1) {
          markers.push(marker);
        }
      });
    });
    return markers;
  }
}
