import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';
import FontAwesome from 'react-fontawesome';

import SpriteFigure from '../Figure/SpriteFigure';

import classes from './MosaicFullScreen.scss';

export default class MosaicFullScreen extends PureComponent {
  onNext = () => {
    if (this.props.next === true) this.props.onNext();
  };

  onPrev = () => {
    if (this.props.prev === true) this.props.onPrev();
  };

  onCloseMode = () => {
    this.props.onCloseMode();
  };

  render () {
    let {figure, width, height, markers, prev, next, crop} = this.props;
    return (
      <div
        className={classes.root}
        style={{width, height}}
      >
        <div
          onDoubleClick={this.onCloseMode}
        >
          <SpriteFigure
            figure={figure}
            width={width}
            height={height}
            markers={markers}
            isStatic={Boolean(false)}
            insert={Boolean(false)}
            searchSelection={Boolean(false)}
            crop={Boolean(crop)}
            isSprite={Boolean(false)}
            index={0}
            angle={figure.angle}
            figureZoom={figure.zoom}
          />
        </div>
        <FontAwesome
          name='chevron-right'
          style={{opacity: next === true ? 1 : 0.25}}
          className={classes.next}
          onClick={this.onNext}
        />
        <FontAwesome
          name='chevron-left'
          style={{opacity: prev === true ? 1 : 0.25}}
          className={classes.prev}
          onClick={this.onPrev}
        />
        <FontAwesome
          name='times'
          className={classes.close}
          onClick={this.onCloseMode}
        />
      </div>
    );
  }
}

MosaicFullScreen.propTypes = {
  figure: PropTypes.object,
  markers: PropTypes.array,
  height: PropTypes.number,
  width: PropTypes.number,
  onCloseMode: PropTypes.func,
  onNext: PropTypes.func,
  onPrev: PropTypes.func,
  prev: PropTypes.bool,
  crop: PropTypes.bool,
  next: PropTypes.bool
};
