import { FIGURES_CACHE_LIMIT, CACHE_TIMEOUT, CACHE_LOADING } from '@epo/web-common';

/**
 * The class orginizes a cache store for figures
 */
export default class FiguresCacheStore {
  /**
   * Map of cache images links
   * @type {Map<String, Object>}
   * @private
   */
  _cache = new Map();
  _figuresAPI;
  _capacity;

  /**
   * Create a store
   * @param {Integer} capacity - a store capacity
   */
  constructor (capacity = FIGURES_CACHE_LIMIT, figuresAPI) {
    this._capacity = capacity;
    this._figuresAPI = figuresAPI;
  }

  /**
   * Adds sprite request to a cache
   * Does sprite post request
   * Runs callbacks to spread response
   *
   * @param {String} name - unique name for sprite request
   * @param {String} url - request url
   * @param {Boolean} rotation - true if a sprite is horizontal oriented
   * @param {String} attributes - request payoad
   * @param {Function|null} callback - function to process a response
   */
  addImage (name, url, rotation, attributes, callback = null) {
    const {width, height, publicationNumber} = this.getSearchParams(url);
    const figureCache = this._cache.get(name);
    if (figureCache) {
      const { loaded, queue } = figureCache;
      if (loaded) {
        const figureKey = this.getFigureKey(figureCache, [width, height]);
        const cachedFigure = figureCache.cache[figureKey];
        if (cachedFigure) {
          const { loaded, listeners } = cachedFigure;
          if (loaded) {
            let { source: url, sizes } = cachedFigure;
            if (url === undefined && cachedFigure[figureKey] && cachedFigure[figureKey].loaded) {
              url = cachedFigure[figureKey].source;
              sizes = cachedFigure[figureKey].sizes;
            }
            const { originSpriteFigure: {originSpriteWidths: orWidth, originSpriteHeights: orHeight} } = figureCache;
            callback && callback.callback({key: callback.key, loaded, url, sizes: {orWidth, orHeight, ...sizes}});
          } else {
            callback && listeners.push(callback);
          }
        } else {
          if (figureCache.cache[CACHE_LOADING]) {
            figureCache.cache[CACHE_LOADING].queue.push([name, url, attributes, callback, width, height]);
          } else {
            figureCache.cache[CACHE_LOADING] = {loaded: null, listeners: callback ? [callback] : [], queue: []};
            this.getImage(url, attributes, figureCache.cache[CACHE_LOADING], figureCache.cache, figureCache, width, height, rotation).catch(err => {
              console.warn(`Error when loading image in FiguresCacheStore`, err)
            });
          }
        }
      } else if (loaded === false) {
        // it means error happened
        callback && callback.callback({key: callback.key, loaded, url, sizes: {}});
      } else {
        //new callback
        queue.push([name, url, attributes, callback, width, height, rotation]);
      }
    } else {
      // Remove old images from cache when it overcrowded
      if (this._cache.size >= this._capacity) {
        this._cache.delete(this.getRemovingItem(publicationNumber));
      }

      // Set pending status
      const cache = {};
      const figureCache = {loaded: null, cache, listeners: callback ? [callback] : [], queue: []};
      this._cache.set(name, figureCache);

      this.getImage(url, attributes, figureCache, cache, cache, width, height, rotation).catch(err => {
        console.warn(`Error when loading image in FiguresCacheStore`, err)
      });
    }
  }

  /**
   * Extracts parameters from an url
   * @param url
   * @returns {{width: string, height: string, publicationNumber: string}}
   */
  getSearchParams (url) {
    const uRL = new URL(url);
    const { searchParams } = uRL;
    return {width: searchParams.get('width'), height: searchParams.get('height'), publicationNumber: searchParams.get('publicationNumber')};
  }

  /**
   * Gets randomly any item key, which is loaded and does not belong to requested publication, to delete it from cache
   * @param publicationNumber
   * @returns {String} key of the item to be deleted
   */
  getRemovingItem (publicationNumber) {
    const cache = Array.from(this._cache);
    let index = 0;
    //Repeat attempts to find a proper item maximum [this._capacity] times
    for (let i = 0; i < this._capacity; i++) {
      index = Math.floor(Math.random() * this._capacity);
      if (cache[index][0].indexOf(publicationNumber) === -1 && cache[index][1].loaded === true) {
        return cache[index][0];
      }
    }
    return cache[index][0];
  }

  /**
   * Get a figure image from a server
   * @param {String} url - request url
   * @param {Array} attributes - request payload
   * @param {Object} figureCacheMeta
   * @param {Object} figureCache
   * @param {Object} cache
   * @param {Number} width
   * @param {Number} height
   * @param {Boolean} rotation
   * @returns {Promise<any | never>}
   */
  getImage (url, attributes, figureCacheMeta, figureCache, cache, width, height, rotation) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const {listeners, queue, originSpriteFigure, figureRatio} = figureCacheMeta;
        if (queue && queue.length) {
          let sameOrientation = queue.filter(el => el[6] === rotation);
          if (sameOrientation.length) {
            const lastWidth = sameOrientation[sameOrientation.length - 1][4];
            const lastHeight = sameOrientation[sameOrientation.length - 1][5];
            if (width !== lastWidth || height !== lastHeight) {
              url = sameOrientation[sameOrientation.length - 1][1];
              [width, height] = [lastWidth, lastHeight];

              //test if there is an image in the cache for [lastWidth, lastHeight]
              if (cache.hasOwnProperty('originSpriteFigure')) {
                const figureKey = this.getFigureKey(cache, [lastWidth, lastHeight]);
                let cachedFigure = figureCache[figureKey];
                if (cachedFigure) {
                  // there is an image in the cache

                  listeners.forEach(listener => listener.callback({
                    key: listener.key,
                    loaded: true,
                    url: cachedFigure.source,
                    sizes: cachedFigure.sizes
                  }));

                  sameOrientation.forEach(call => call[3] && (call[3].callback({
                    key: call[3].key,
                    loaded: true,
                    url: cachedFigure.source,
                    sizes: cachedFigure.sizes
                  })));

                  this.clearCallbacks(figureCacheMeta, sameOrientation);

                  if (figureCache.hasOwnProperty(CACHE_LOADING)) {
                    delete figureCache[CACHE_LOADING];
                  }
                  resolve();
                }
              }
            }
          }
        }

        this._figuresAPI.getFigureUrl(url, attributes).then(({blobUrl, sizes}) => {
          figureCacheMeta.loaded = true;

          const {orWidth: originSpriteWidths, orHeight: originSpriteHeights, ...restSizes} = sizes;
          !originSpriteFigure && (figureCacheMeta.originSpriteFigure = {originSpriteWidths, originSpriteHeights});
          !figureRatio && (figureCacheMeta.figureRatio = {widthRatio: restSizes.width, heightRatio: restSizes.height});

          const cacheKey = this.getFigureKey(figureCacheMeta, [width, height]);
          let cachedFigure = figureCache[cacheKey];
          !cachedFigure && (figureCache[cacheKey] = cachedFigure = {loaded: true});

          cachedFigure.source = blobUrl;
          cachedFigure.sizes = restSizes;

          listeners.forEach(listener => listener.callback({key: listener.key, loaded: true, url: blobUrl, sizes}));

          const queueCallbacks = queue.map(el => {
            return el[6] === rotation && cacheKey === this.getFigureKey(figureCacheMeta, [el[4], el[5]]) ? el : null;
          }).filter(el => el).map(call => {
            call[3] && (call[3].callback({
              key: call[3].key,
              loaded: true,
              url: blobUrl,
              sizes
            }));
            return call;
          });

          this.clearCallbacks(figureCacheMeta, queueCallbacks);

          if (figureCache.hasOwnProperty(CACHE_LOADING)) {
            delete figureCache[CACHE_LOADING];
          }
          resolve();
        }).then(() => {
          const cacheKey = this.getFigureKey(figureCacheMeta, [width, height]);
          queue.map(el => { return el[6] !== rotation || cacheKey !== this.getFigureKey(figureCacheMeta, [el[4], el[5]]) ? el : null; })
          .filter(el => el).forEach(call => {
            this.addImage(call[0], call[1], call[6], call[2], call[3]);
          });
        }).catch((err) => {
          const {listeners, queue} = figureCacheMeta;
          figureCacheMeta.loaded = false;
          listeners.forEach(listener => listener.callback({key: listener.key, loaded: false}));
          queue && queue.forEach(call => call[3] && (call[3].callback({key: call[3].key, loaded: false})));
          this.clearCallbacks(figureCacheMeta);
          reject(err);
        });
      }, CACHE_TIMEOUT);
    });
  }

  /**
   * Removes callback references that were registered by SpriteFigure
   * @param {Object} figureCacheMeta - figure cache
   * @param {Array} queueCallbacks - queue callbacks to be cleared, if not defined, all queue will be cleared
   */
  clearCallbacks (figureCacheMeta, queueCallbacks = null) {
    figureCacheMeta.listeners = [];
    if (queueCallbacks) {
      figureCacheMeta.queue = figureCacheMeta.queue.filter(queueCall => !queueCallbacks.includes(queueCall));
    } else {
      figureCacheMeta.queue = [];
    }
  }

  /**
   * Get a figure key
   * @param {Object} figureCache - a figure cache
   * @param {Array} dimensions - a container dimensions
   */
  getFigureKey (figureCache, dimensions) {
    const { figureRatio: {widthRatio, heightRatio} } = figureCache;
    const [width, height] = dimensions;
    return widthRatio.map((spriteWidth, index) => {
      const figureWidth = spriteWidth;
      const figureHeight = heightRatio[index];
      if (!figureHeight || !figureWidth) {
        return 'null';
      }
      return (width * figureHeight < height * figureWidth ? `${width}w` : `${height}h`);
    }).join('_');
  }
}
