import {observable} from 'mobx';

/**
 * Figure Store Item
 *
 * @export
 * @class FigureStoreItem
 */
export default class FigureStoreItemModel {
  @observable mode = 1;
  @observable full = 0;
  @observable autoFlipCounter = 0;
  @observable modeWidth = 1;
  @observable denominator = 1;
  @observable autoFlipping = false;
  @observable autoFlippingContinue = false;
  @observable legend = false;
  @observable selection = false;
  @observable magnifier = false;
  @observable deleteMarker = false;
  @observable insert = false;
  @observable filter = false;
  @observable markerToScroll = '*';
  @observable filterByMarkers = [];
  @observable publicationId;
  @observable publication = {};
  @observable speed = 1000;
  type;
  panel;
  modeMinValue = 1;
  modeMaxValue = 5;
  modeStep = 1;
  denominatorMinValue = 1;
  denominatorMaxValue = 8;
  speedMinValue = 1000;
  speedMaxValue = 5000;
  speedStep = 100;
  persistency;
  /* autoFlippingStarted is a flag to show that a user pressed on autoflip on current publication */
  autoFlippingStarted = false;
  updateGlobalSpeed = function () {};

  constructor (type, panel, persistency, updateGlobalSpeed) {
    this.type = type;
    this.panel = panel;
    this.persistency = persistency;
    this.updateGlobalSpeed = updateGlobalSpeed;
    const resotrePersistency = persistency.get({
      module: `figures_settings_${this.type}${this.panel}`
    });
    if (resotrePersistency) {
      Object.keys(resotrePersistency).forEach((key) => {
        this[key] = resotrePersistency[key];
      });
    }
    const globalSettings = persistency.get({
      type: 'FNB',
      module: 'figures_settings'
    });
    if (globalSettings) {
      Object.keys(globalSettings).forEach((key) => {
        this[key] = globalSettings[key];
      });
    }
  }

  /**
   * Update Figure search selection
   *
   * @param {boolean} value - new value
   *
   * @memberOf FigureStoreItem
   */
  updateSelection = (value) => {
    if (typeof value === 'boolean') {
      if (value !== false) {
        this.disableDelete();
        this.disableInsert();
      }
      this.selection = value;
    }
  };

  /**
   * Update Figure Legend visibility
   *
   * @param {boolean} value - new value
   *
   * @memberOf FigureStoreItem
   */
  setLegend = (value) => {
    if (typeof value === 'boolean') {
      this.legend = value;
    }
  };

  /**
   * Enable magnifier
   *
   * @memberOf FigureStoreItem
   */
  enableMagnifier = () => {
    this.magnifier = true;
  };

  /**
   * Disable magnifier
   *
   * @memberOf FigureStoreItem
   */
  disableMagnifier = () => {
    this.magnifier = false;
  };

  /**
   * AUpdate Auto flipping status
   *
   * @param {boolean} value - Auto flipping status
   *
   * @memberOf FigureStoreItem
   */
  updateAutoFlipping = (value) => {
    if (typeof value === 'boolean' && this.autoFlipping !== value) {
      this.autoFlipping = value;
    }
  };

  updateAutoFlippingContinue = (value) => {
    if (typeof value === 'boolean' && this.autoFlippingContinue !== value) {
      this.autoFlippingContinue = value;
    }
  };

  startAutoFlipping = (value) => {
    if (typeof value === 'boolean' && this.autoFlippingStarted !== value) {
      this.autoFlippingStarted = value;
    }
  };

  /**
   * Update Figure view mode (example: from 1 to 5)
   * This function will round you value by modeStep
   *
   * @param {number} value - mode value
   *
   * @memberOf FigureStoreItem
   */
  setMode = (value) => {
    if (typeof value === 'number') {
      value = Math.floor(value / this.modeStep) * this.modeStep;
      if (value > this.modeMaxValue) value = this.modeMaxValue;
      else if (value < this.modeMinValue) value = this.modeMinValue;
      this.mode = value;
      this.persistency.set({
        module: `figures_settings_${this.type}${this.panel}`,
        data: { mode: value }
      });
      this.updateAutoFlipping(false);
      this.autoFlipCounter = 0;
    }
  };

  /**
   * Update Auto Flip denominator
   *
   * @param {number} value - denominator value
   *
   * @memberOf FigureStoreItem
   */
  updateDenominator = (value) => {
    if (typeof value === 'number' && value === Math.floor(value)) {
      if (value > this.denominatorMaxValue) value = this.denominatorMinValue;
      else if (value < this.denominatorMinValue) value = this.denominatorMinValue;
      this.denominator = value;
    }
  };

  /**
   * Update Auto Flip speed
   * This function will round you value by speedStep
   *
   * @param {number} value - auto flip speed
   *
   * @memberOf FigureStoreItem
   */
  updateSpeed = (value) => {
    value = Math.floor(value / this.speedStep) * this.speedStep;
    if (value > this.speedMaxValue) value = this.speedMaxValue;
    else if (value < this.speedMinValue) value = this.speedMinValue;
    this.speed = value;
    this.updateGlobalSpeed(value);
  };

  /**
   * Enable markers insertion
   *
   * @param {string} value - marker
   *
   * @memberOf FigureStoreItem
   */
  enableInsert = (value) => {
    if (typeof value === 'string') {
      this.disableDelete();
      this.updateSelection(false);
      this.insert = value;
    }
  };

  disableInsert = () => {
    this.insert = false;
  };

  /**
   * Reset store values to default
   *
   * @memberOf FigureStoreItem
   */
  resetStore = () => {
    this.full = 0;
    this.updateSelection(false);
  };

  /**
   * Toogle filtering figures by marker
   *
   * @memberOf FigureStoreItem
   */
  toggleFilter = () => {
    this.filter = Boolean(!this.filter);
  };

  /**
   * Toogle markers to filter
   *
   * @memberOf FigureStoreItem
   */
  toggleFilterMarker = (marker) => {
    if (this.filterByMarkers.indexOf(marker) === -1) {
      this.addFilterMarker(marker);
    } else {
      this.removeFilterMarker(marker);
    }
  };

  /**
   * Add filtering markers
   *
   * @memberOf FigureStoreItem
   */
  addFilterMarker = (marker) => {
    this.filterByMarkers.push(marker);
  };

  /**
   * Remove filtering markers
   *
   * @memberOf FigureStoreItem
   */
  removeFilterMarker = (marker) => {
    let {filterByMarkers} = this;
    filterByMarkers.remove(marker);
  };

  toggleDelete () {
    this.deleteMarker = !this.deleteMarker;
    this.disableInsert();
    this.updateSelection(false);
  }

  disableDelete () {
    this.deleteMarker = false;
  }

  set activeFigure (index) {
    return this.publication.setActiveFigure(this.type, this.panel, index);
  }

  get activeFigure () {
    return this.publication.getActiveFigure(this.type, this.panel);
  }

  nextActiveFigure = (marker, funnel = false, markers = {}, step = 1) => {
    return this.publication.nextActiveFigure(this.type, this.panel, funnel, marker, markers, step);
  };

  prevActiveFigure = (marker, funnel = false, markers = {}, step = 1, skipMissed = false) => {
    return this.publication.prevActiveFigure(this.type, this.panel, funnel, marker, markers, step, skipMissed);
  };

  firstActiveFigure = (marker) => {
    this.publication.firstActiveFigure(this.type, this.panel, marker);
  };

  lastActiveFigure = (marker) => {
    this.publication.lastActiveFigure(this.type, this.panel, marker);
  };

  updateActiveFigureZoom = (action) => {
    this.publication.updateActiveFigureZoom(this.type, this.panel, action);
    this.updateSelection(false);
  };

  updateActiveFigureAngle = (deg) => {
    this.publication.updateActiveFigureAngle(this.type, this.panel, deg);
  };
}
