import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {observer} from 'mobx-react';
import { observable } from 'mobx';
import { HighlightDropdown, ScrollJumpDropdown } from '@epo/web-common';
import FontAwesome from 'react-fontawesome';

import classes from './MosaicControls.scss';

@observer
class MosaicControls extends Component {
  constructor (props) {
    super(props);
    this.markersHit = observable.map();
    this.visibleMarkers = [];
    this.updateMarkers(props.markers, props.store);
  }

  /**
   * Method updates set marker (as active or inactive) to funnel settings
   * @param {String} marker - marker name with prefix m (mA)
   */
  toggleMarker = (marker) => {
    let {store} = this.props;
    store.toggleFilterMarker(marker);
    this.props.toggleMarker(marker);
  };

  /**
   * Method toggle filtering by markers
   */
  toggleParagraphs = () => {
    let {store} = this.props;
    store.toggleFilter();
    this.props.toggleParagraphs();
  };

  /**
   * Method sets all markers as active or as inactive
   * @param {Array<String>} markers - list of markers
   */
  toggleAllMarkers = (markers) => {
    this.props.toggleAllMarkers(markers);
  };

  /**
   * Method sets marker as active to navigation
   * @param {String} marker - marker name with prefix m (mA)
   */
  setMarkerToScroll = (marker) => {
    this.props.setMarkerToScroll(marker);
  };

  /**
   * Method returns all active (status = !) markers
   * @returns {Object} markers - list of markers
   */
  get markers () {
    let markers = {};
    this.props.markers.forEach((marker) => {
      if (marker.status !== 0 || marker.content === '') return;
      markers['m' + marker.name] = {
        bgcolor: marker.bgcolor,
        color: marker.color
      };
    });
    return markers;
  }

  /**
   * Method creates visibleMarkers variable
   * @param {Array<Object>} markers - array of markers
   * @param {Object} store - figures store
   */
  updateMarkers = (markers, store) => {
    this.markersHit.clear();
    this.visibleMarkers = [];
    let availableMarkers = store.publication.getMarkers();
    markers.forEach((marker) => {
      let markerName = 'm' + marker.name;
      if (store.filterByMarkers.indexOf(markerName) === -1 && availableMarkers.indexOf(marker.name) !== -1) {
        if (marker.status === 0) {
          this.markersHit.set(markerName, {
            bgcolor: marker.bgcolor,
            color: marker.color
          });
          this.visibleMarkers.push(markerName);
        }
      }
    });
    this._updateMarkersLegend();
  };

  /**
   * Method highlight markers in figures legend
   * @private
   */
  _updateMarkersLegend = () => {
    let {
      store: {
        publication
      },
      updateMarkersLegend
    } = this.props;

    if (!updateMarkersLegend) {
      return;
    }

    let markers = this.markers;
    let availableMarkers = publication.getMarkers().map((marker) => 'm' + marker);

    const markerHits = {};
    availableMarkers.forEach(marker => {
      if (markers[marker]) {
        markerHits[marker] = {
          bgcolor: markers[marker].bgcolor
        };
      }
    });

    updateMarkersLegend(markerHits);
  };

  /**
   * Method calls before updating props
   * @param {Object} nextProps - next properties
   */
  UNSAFE_componentWillUpdate (nextProps) {
    this.updateMarkers(nextProps.markers, nextProps.store);
  }

  render () {
    let {
      store: {
        mode,
        publication
      },
      funnelSettings,
      getShortcutCombination,
      scrollPrev,
      scrollNext,
      markersForSearch
    } = this.props;
    let markers = this.markers;
    let availableMarkers = publication.getMarkers();
    availableMarkers = availableMarkers.map((marker) => 'm' + marker);
    return (
      <div
        className={
          `${classes.root} ${mode !== 1 ? classes.mosaic : classes.thumb}`
        }
      >
        <div
          className={classes.legend}
        />
          <HighlightDropdown
            markersByColor={markers}
            markersByContent={markers}
            visibleMarkers={funnelSettings.markers}
            isParagraphsCollapsed={funnelSettings.funnel}
            toggleMarker={this.toggleMarker}
            toggleAllMarkers={this.toggleAllMarkers}
            toggleParagraphs={this.toggleParagraphs}
            funnelSettings={funnelSettings}
          />
        {mode === 1
          ? <button title={`Jump to previous relevant figure (${getShortcutCombination('SIDEBAR_PREV_MARKER')})`}
                    onClick={scrollPrev}
                    className={classes.navButton}>
            <FontAwesome name='chevron-up'/>
          </button>
          : null}
        {mode === 1
          ? <ScrollJumpDropdown
              markersByColor={markers}
              markersForSearch={markersForSearch}
              visibleMarkers={availableMarkers}
              markersHit={this.markersHit}
              setMarkerToScroll={this.setMarkerToScroll}
              funnelSettings={funnelSettings}
              jumpMarker={funnelSettings.jumpMarker !== '*' ? 'm' + funnelSettings.jumpMarker : '*'}
            />
          : null}
        {mode === 1
          ? <button title={`Jump to next relevant figure (${getShortcutCombination('SIDEBAR_NEXT_MARKER')})`}
                    onClick={scrollNext}
                    className={classes.navButton}>
            <FontAwesome name='chevron-down'/>
          </button> : null}
      </div>
    );
  }
}

MosaicControls.propTypes = {
  store: PropTypes.object,
  markers: PropTypes.array,
  funnelSettings: PropTypes.object.isRequired,
  // openNextDocument: PropTypes.func, // Disabled in fav. to P0693-1201
  updateMarkersLegend: PropTypes.func,
  setMarkerToScroll: PropTypes.func,
  toggleMarker: PropTypes.func,
  toggleAllMarkers: PropTypes.func,
  toggleParagraphs: PropTypes.func,
  getShortcutCombination: PropTypes.func.isRequired,
  scrollNext: PropTypes.func.isRequired,
  scrollPrev: PropTypes.func.isRequired,
  markersForSearch: PropTypes.any
};

export default MosaicControls;
