import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {inject, observer} from 'mobx-react';
import FontAwesome from 'react-fontawesome';
import classNames from 'classnames/bind';
import {action} from 'mobx';

import Magnifier from '../Magnifier';
import SpriteFigure from '../Figure/SpriteFigure';
import MosaicFullScreen from '../MosaicFullScreen';

import classes from './MosaicView.scss';
import {
  hotkeys,
  epoARMService,
  ARM_LOAD_IMAGE,
  SIDEBAR_WIDTH,
  CONTROL_PANEL_HEIGHT,
  SPRITE_LIMIT,
  FIGURE_PREFETCH_LIMIT_AFTER,
  SIDEBAR_MODE,
  FULL_MOSAIC_MODE,
  MOSAIC_CORRECTION_WIDTH,
  MOSAIC_CORRECTION_HEIGHT
} from '@epo/web-common';
import { LazyLoad } from 'common/dist';

import {
  extendSpriteSource,
  getFacsimileAttributes,
  getFiguresInterval,
  getRotation,
  getImageKey,
  getItemSize,
  getCrop,
  getModeCrop,
  calculateSideBar,
  getSpriteBody,
  getSpriteZoom,
  getSpriteWidth,
  getSpriteHeight
} from 'Figures.Helper';

import {
  Bind,
  Throttle,
  Debounce
} from 'lodash-decorators';

let cx = classNames.bind(classes);

const THUMB_OFFSET = 6;

@inject(stores => ({
  enableThumbnailBar: stores.enableThumbnailBar,
  figuresStore: stores.figuresStore,
}))
@observer
class MosaicView extends Component {
  state = {
    selected: 0,
    scrollbarWidth: 0
  };

  active = {
    offset: 0,
    height: 0
  };

  triggeredScroll = true;
  armActionId;
  trackARMID = '';

  itemWidth;
  itemHeight;
  fit;

  mosaicScrollTop = -1;
  singleScrollTop = -1;
  sidebarScrollTop = 0;

  activeIndexHidden = false;
  firstVisibleIndex = 0;
  activeFigureIndex = 0;

  handleClick = (event, index, itemWidth, itemHeight) => {
    event.preventDefault();
    event.persist();
    event.stopPropagation();
    this.dispatchEvent(event, index, itemWidth, itemHeight);
  };

  @Bind()
  @Debounce(100)
  dispatchEvent = (event, index, itemWidth, itemHeight) => {
    this.dispatcher(event, index, itemWidth, itemHeight);
  }

  dispatcher = (event, index, itemWidth, itemHeight) => {
    if (event && event.type !== 'dblclick') {
      this.handleChangeClick(event, index, itemWidth, itemHeight);
    } else {
      this.handleDoubleClick(index);
    }
  }

  handleChangeClick = (evt, index, itemWidth, itemHeight) => {
    let {store} = this.props;
    store.activeFigure = index;
    if (store.mode === 1) {
      store.updateActiveFigureZoom('reset');
    } else {
      if (store.magnifier === true) {
        this.triggeredScroll = true;
        store.disableMagnifier();
      } else {
        this.triggeredScroll = false;
        store.enableMagnifier();
        let target = evt.target.getBoundingClientRect();
        evt.preventDefault();
        let event = {
          x: evt.clientX - target.left,
          y: evt.clientY - target.top
        };
        setTimeout(() => {
          this._updateMagnifier(event, index, itemWidth, itemHeight);
        }, 0);
      }
    }
  };

  handleMouseMove = (evt, index, itemWidth, itemHeight) => {
    this._updateMagnifier(evt, index, itemWidth, itemHeight);
  };

  _updateMagnifier = (evt, index, itemWidth, itemHeight) => {
    let {store: {mode, magnifier}, width, height} = this.props;
    if (magnifier === true && mode !== 1 && this.refs.magnifier) {
      this.refs.magnifier.mouseMove(evt, index, itemWidth, itemHeight, {width, height});
    }
  };

  handleDoubleClick = (index) => {
    let {store} = this.props;
    this.triggeredScroll = false;
    store.activeFigure = index;
    store.disableMagnifier();
    this.props.store.full = 1;
  };

  onCloseFullMode = () => {
    this.triggeredScroll = true;
    this.mosaicScrollTop = -1;
    this.props.store.full = 0;
  };

  onNext = () => {
    this.props.store.activeFigure += 1;
  };

  onPrev = () => {
    this.props.store.activeFigure -= 1;
  };

  _calculateModeWidth = () => {
    let {store} = this.props;
    let modeWidth = Math.round(this.props.width / this.itemWidth);
    if (Number(modeWidth) !== Number(store.modeWidth)) {
      store.modeWidth = Math.round(this.props.width / this.itemWidth);
    }
  };

  _calculateSideBar = () => {
    if (this.refs.root !== undefined && this.props.store.mode !== 1) {
      let scrollbarWidth = calculateSideBar(this.refs.root, this.props.store.mode);
      if (scrollbarWidth !== this.state.scrollbarWidth) {
        this.setState({
          scrollbarWidth
        });
      }
    }
  };

  UNSAFE_componentWillMount () {
    this.trackARMID = '';
  }

  componentWillUnmount () {
    this.refs = {};
  }

  componentDidMount () {
    const {
      store: {
      mode,
      publication
      }} = this.props;

    if (publication) {
      this.activeFigureIndex = -1;
    }
    this._calculateModeWidth();
    if (mode === 1) {
      this._scrollSidebarItems();
    } else {
      this._calculateSideBar();
    }
    // forceCheck();
  };

  componentDidUpdate () {
    this._calculateModeWidth();
    const {
      store,
      store: {
      mode,
      magnifier,
      publication,
      panel,
      type
    }} = this.props;

    const activeFigure = publication.getActiveFigure(type, panel);

    if (this.activeIndexHidden === true) {
      this.activeIndexHidden = false;
      if (Number(this.firstVisibleIndex) !== Number(store.activeFigure)) {
        this.props.store.activeFigure = this.firstVisibleIndex;
      }
    }
    if (this.refs.root !== undefined) {
      if (mode === 1) {
        activeFigure !== this.activeFigureIndex && this._scrollSidebarItems();
        this.mosaicScrollTop = -1;
      } else {
        if (this.triggeredScroll) {
          activeFigure !== this.activeFigureIndex && this._scrollMosaicView();
        }
        if (magnifier !== false) {
          this._updateMagnifier({
            x: this.itemWidth / 2,
            y: this.itemHeight / 2
          }, this.props.store.activeFigure, this.itemWidth, this.itemHeight);
        }
        this.singleScrollTop = -1;
        this._calculateSideBar();
      }
      if (this.refs.mosaic) {
        this._checkImageLoaded(this.props);
      }
      // forceCheck();
    }
    this.activeFigureIndex = activeFigure;
  }

  _scrollSidebarItems = () => {
    let {height} = this.props;
    if (this.refs.root !== undefined && this.singleScrollTop === -1) {
      if (height + this.sidebarScrollTop < this.active.offset || (this.active.offset - this.active.height) < this.sidebarScrollTop) {
        this.refs.root.scrollTop = this.active.offset - this.active.height;
      }
    }
  };


  _scrollToView (row, mode) {
    const line = `line-${row}-${mode}`;
    return this.refs[line] && this.refs[line].scrollIntoView({behavior: 'auto', block: 'start'})
  }

  _scrollMosaicView () {
    if (this.mosaicScrollTop === -1) {
      let {store: {mode, modeWidth, activeFigure}} = this.props;
      let row = Math.floor(activeFigure / modeWidth);
      this.refs.root.scrollTop = row * (this.itemHeight + THUMB_OFFSET);
      this._scrollToView(row, mode);
    }
  };

  @Bind()
  @Debounce(100)
  _handleScroll = (e) => {
    // forceCheck();

    // cache scrollTop position to avoid layout recalculation later on component render
    this.sidebarScrollTop = e.target.scrollTop;
  };

  runFetchNextImages (index, figures, width, height, crop, zoom) {
    const attributes = getFiguresInterval(index, SPRITE_LIMIT, figures[0].sprite);
    if (attributes.length > 0) {
      const { figuresStore } = this.props;
      let {direct, rotate} = getRotation(index, SPRITE_LIMIT, figures);
      const figZoom = getSpriteZoom(true, true, zoom, 1);
      const figWidth = getSpriteWidth(width, crop, figZoom);
      const figHeight = getSpriteHeight(height, crop, figZoom);

      if (direct === true) {
        let source = extendSpriteSource(figWidth, figHeight, crop, 0, figures[0].spriteSource);
        figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, source, attributes, false), source, false, getSpriteBody(0, SPRITE_LIMIT, attributes));
      }
      if (rotate === true) {
        let source = extendSpriteSource(figHeight, figWidth, crop, 0, figures[0].spriteSource);
        figuresStore.cache.addImage(getImageKey(0, SPRITE_LIMIT, source, attributes, true), source, true, getSpriteBody(0, SPRITE_LIMIT, attributes));
      }
    }
  }

  runFetchNextImage (figures, width, height, crop, zoom) {
    const { figuresStore } = this.props;
    const figZoom = getSpriteZoom(false, false, zoom, 1);
    const figWidth = getSpriteWidth(width, crop, figZoom);
    const figHeight = getSpriteHeight(height, crop, figZoom);
    figures.forEach(figure => {
      if (figure === undefined) return;
      let [facsimileId, sheet] = getFacsimileAttributes(figure);
      let source = extendSpriteSource((figure.angle % 180) ? figHeight : figWidth, (figure.angle % 180) ? figWidth : figHeight, crop, 0, figure.spriteSource);
      figuresStore.cache.addImage(getImageKey(0, 1, source, [[facsimileId, sheet]], !!(figure.angle % 180)), source, !!(figure.angle % 180), getSpriteBody(0, 1, [[facsimileId, sheet]]));
    });
  }

  preFetchNextImages () {
    let {
      store:
        {
          activeFigure,
          publication: {figures},
          mode,
          full
        },
      container:
        {
          width: containerWidth,
          height: containerHeight
        },
      isBNS,
      width,
      height
    } = this.props;
    if (figures.length === 0 || containerWidth === 0 || containerHeight === 0) {
      return;
    }
    if (mode !== SIDEBAR_MODE && full === FULL_MOSAIC_MODE) {
      //mosaic full screen
      //do down prefetch
      let figs = figures.slice(activeFigure, activeFigure + FIGURE_PREFETCH_LIMIT_AFTER);
      this.runFetchNextImage(figs, width, height, getModeCrop(isBNS, mode), 1);
      //do up prefetch
      figs = figures.slice((activeFigure - FIGURE_PREFETCH_LIMIT_AFTER) < 0 ? 0 : (activeFigure - FIGURE_PREFETCH_LIMIT_AFTER), activeFigure);
      this.runFetchNextImage(figs, width, height, getModeCrop(isBNS, mode), 1);
    } else {
      if (mode === SIDEBAR_MODE && full !== FULL_MOSAIC_MODE) {
        //large image if it is sidebar mode
        //do down prefetch
        let figs = figures.slice(activeFigure + 1, activeFigure + FIGURE_PREFETCH_LIMIT_AFTER);
        this.runFetchNextImage(figs, containerWidth - SIDEBAR_WIDTH, containerHeight - CONTROL_PANEL_HEIGHT, getCrop(isBNS), 1);
        //do up prefetch
        if (activeFigure) {
          figs = figures.slice((activeFigure - FIGURE_PREFETCH_LIMIT_AFTER) < 0 ? 0 : (activeFigure - FIGURE_PREFETCH_LIMIT_AFTER), activeFigure - 1);
          this.runFetchNextImage(figs, containerWidth - SIDEBAR_WIDTH, containerHeight - CONTROL_PANEL_HEIGHT, getCrop(isBNS), 1);
        }
      }
      if (mode !== 1 && this.itemWidth === Infinity) return;

      let iterations = 3;
      if (mode !== 1) {
        iterations = (~~((containerWidth / this.itemWidth) * (containerHeight / this.itemHeight) / SPRITE_LIMIT) + 1) * 3;
      }
      let startIndex = ~~(activeFigure / SPRITE_LIMIT);
      let index = startIndex * SPRITE_LIMIT - SPRITE_LIMIT;
      //do down prefetch
      for (let i = 0; i < iterations; i++) {
        index = index + SPRITE_LIMIT;
        this.runFetchNextImages(index, figures, this.itemWidth - MOSAIC_CORRECTION_WIDTH, this.itemHeight - MOSAIC_CORRECTION_HEIGHT, getModeCrop(isBNS, mode), 1);
      }
      //do up prefetch
      index = startIndex * SPRITE_LIMIT;
      for (let i = 0; i < iterations; i++) {
        index = index - SPRITE_LIMIT;
        if (index > 0) {
          this.runFetchNextImages(index, figures, this.itemWidth - MOSAIC_CORRECTION_WIDTH, this.itemHeight - MOSAIC_CORRECTION_HEIGHT, getModeCrop(isBNS, mode), 1);
        }
      }
    }
  }

  @action
  _checkImageLoaded = (props) => {
    let {store, store: {publication: {figures}}} = props;
    if (figures.length === 0) {
      return;
    }
    let activeFigure = figures[store.activeFigure];
    if (typeof activeFigure === 'object') {
      this.preFetchNextImages();
    }
  };

  _handleOnLoad = (source) => {
    if (this.trackARMID === source && this.armActionId) {
      epoARMService.commitAction(this.armActionId);
      this.armActionId = '';
    }
  };

  _handleOnError = (source) => {
    if (this.trackARMID === source && this.armActionId) {
      epoARMService.failAction(this.armActionId);
      this.armActionId = '';
    }
  };

  _handleWheel = (e) => {
    if (this.triggeredScroll || (this.props.store.magnifier === false && this.triggeredScroll === false)) {
      this.triggeredScroll = true;
    } else {
      e.stopPropagation();
    }
  };

  _getItemSize (props) {
    let {
      store: {
        mode,
        publication
      },
      height,
      width
    } = props;

    let size = getItemSize(mode, publication.figures, height, width, this.state.scrollbarWidth);
    this.itemWidth = size.itemWidth;
    this.itemHeight = size.itemHeight;
    this.fit = size.fit;
  }

  renderSidebarNavigationShortcuts () {
    const {
      funnelSettings,
      store: {
        mode,
        publication
      },
      scrollPrev,
      scrollNext,
      hotKeysActive,
    } = this.props;
    if (!hotKeysActive) return null;
    let markerHitNames = publication._markersObjectToArray(funnelSettings.markers);
    let alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
    let hotKeysArray = [
      {
        name: 'SIDEBAR_NEXT_MARKER',
        callback: () => {
          scrollNext();
        }
      },
      {
        name: 'SIDEBAR_PREV_MARKER',
        callback: () => {
          scrollPrev();
        }
      },
      {
        name: 'SIDEBAR_NEXT_MARKER_*',
        callback: () => {
          scrollNext();
        }
      },
      {
        name: 'SIDEBAR_PREV_MARKER_*',
        callback: () => {
          scrollPrev();
        }
      }
    ];
    alphabet.split('').map((letter) => {
      const markerName = 'm' + letter;
      hotKeysArray.push(
        {
          name: 'SIDEBAR_NEXT_MARKER_' + letter,
          callback: () => {
            if (markerHitNames.indexOf(markerName) === -1) return;
            scrollNext();
          }
        },
        {
          name: 'SIDEBAR_PREV_MARKER_' + letter,
          callback: () => {
            if (markerHitNames.indexOf(markerName) === -1) return;
            scrollPrev();
          }
        }
      );
    });
    return mode === 1 ? hotkeys(hotKeysArray) : null;
  };

  render () {
    let {
      activeTab,
      store,
      store: {
        mode,
        full,
        panel,
        magnifier,
        publication
      },
      markers,
      height,
      width,
      isEmpty,
      isBNS,
      documentWithError,
      funnelSettings
    } = this.props;
    let {figures} = publication;
    this._getItemSize(this.props);

    if (mode !== 1 && full === 1) {
      return <MosaicFullScreen
        ref='mosaic'
        figure={figures[store.activeFigure]}
        markers={markers}
        height={height}
        width={width}
        crop={Boolean(getModeCrop(isBNS, mode))}
        onCloseMode={this.onCloseFullMode}
        prev={Boolean(store.activeFigure > 0)}
        onNext={this.onNext}
        next={Boolean((store.activeFigure + 1) < figures.length)}
        onPrev={this.onPrev}
        figureZoom={figures[store.activeFigure].zoom}
        angle={figures[store.activeFigure].angle}
      />;
    }

    if (isBNS.active === true && activeTab === 'figures' && mode === 1) {
      height += 18;
    }

    let sections;
    let activeOffset = 0;
    let visibleMarkers = [];
    let activeMarkers = [];

    markers.forEach(m => {
      let marker = funnelSettings.markers[m.name];
      if (marker !== undefined && marker === true) {
        activeMarkers.push(m);
        visibleMarkers.push(m);
      } else if (funnelSettings.funnel === false) {
        visibleMarkers.push(m);
      }
    });
    if (mode !== 1) {
      if (this.itemWidth === Infinity) return null;
    }
    let visibleMarkersNames = visibleMarkers.map(marker => marker.name);
    let firstVisibleIndex = -1;
    this.activeIndexHidden = store.activeFigure === -1;

    sections = figures.map((figure, index) => {
      if (funnelSettings.funnel === true && isBNS.active === false && isBNS.asMissed !== true) {
        let hideFigure = true;
        figure.markers.forEach((marker) => {
          if (hideFigure === false || visibleMarkersNames.indexOf(marker) !== -1) {
            hideFigure = false;
          }
        });
        if (hideFigure === true) {
          if (index === store.activeFigure) {
            this.activeIndexHidden = true;
          }
          return null;
        } else {
          if (firstVisibleIndex === -1) {
            firstVisibleIndex = index;
          }
        }
      } else if (firstVisibleIndex === -1) {
        firstVisibleIndex = 0;
      }

      activeOffset += this.itemHeight + THUMB_OFFSET;

      if (index === store.activeFigure) {
        this.active = {
          offset: activeOffset,
          height: this.itemHeight + THUMB_OFFSET
        };
      }

      if (this.trackARMID === '') {
        this.trackARMID = figure.source;
        this.armActionId = epoARMService.beginAction(ARM_LOAD_IMAGE);
      }

      return (
        <section
          key={figure.sheet + index}
          data-id={index}
          onClick={(evt) => {
            this.handleClick(evt, index, this.itemWidth, this.itemHeight);
          }}
          onDoubleClick={(evt) => {
            this.handleClick(evt, index, this.itemWidth, this.itemHeight);
          }}
          onMouseLeave={() => {
            if (this.refs.magnifier !== undefined) {
              this.refs.magnifier.hide();
            }
          }}
          onMouseEnter={() => {
            if (this.refs.magnifier !== undefined) {
              this.refs.magnifier.show();
            }
          }}
          className={index === store.activeFigure ? 'active' : ''}
          style={{
            width: this.itemWidth - 2,
            height: this.itemHeight - 4
          }}
        >
          <SpriteFigure
            ref='mosaic'
            prefix={`mosaic-${panel}`}
            figure={figure}
            width={this.itemWidth - MOSAIC_CORRECTION_WIDTH}
            height={this.itemHeight - MOSAIC_CORRECTION_HEIGHT}
            markers={activeMarkers}
            isStatic={Boolean(true)}
            finIn={Boolean(mode !== 1)}
            hideToolTip={Boolean(mode === 1)}
            disableSpinner={Boolean(mode === 1)}
            trackMouse={magnifier === true}
            crop={Boolean(getModeCrop(isBNS, mode))}
            solid={Boolean(mode === 1)}
            onMousePosition={(evt) => {
              return this.handleMouseMove(evt, index, this.itemWidth, this.itemHeight);
            }}
            onLoad={this._handleOnLoad}
            onError={this._handleOnError}
            isSprite={Boolean(true)}
            isThumb={true}
            index={index}
            angle={mode !== 1 ? figure.angle : figure.mosaicAngle}
            mode={mode}
          />
          {mode !== 1 ? <FontAwesome
            name='arrows-alt'
            className={classes.fullScreen}
            onClick={(e) => {
              this.handleDoubleClick(index);
              e.stopPropagation();
            }}
          /> : null}
          <span className={classes.thumbNumber}>{(index + 1)}</span>
        </section>
      );
    });

    let lines = [];

    sections.filter(el => el !== null).forEach((section, index) => {
      if (index % this.fit === 0) {
        lines.push([]);
      }
      lines[lines.length - 1].push(section);
    });

    if (this.activeIndexHidden === true) {
      this.firstVisibleIndex = firstVisibleIndex;
    }

    if (mode === 1 && activeTab === 'figures' && !this.props.enableThumbnailBar) {
      return null;
    }

    return (
      <div
        className={
          cx(
            {
              thumbs: mode === 1,
              mosaic: mode !== 1,
              isEmpty,
              magnifier: magnifier === true,
              asMissed: isBNS.active === true && activeTab === 'figures',
              bns: isBNS.active || (documentWithError && !isBNS.active)
            }
          )}
        ref='root'
        style={{width, height}}
        onScroll={this._handleScroll}
        onWheel={this._handleWheel}
      >
        <input type='hidden' value={figures.length} />
        {magnifier === true && mode !== 1
          ? <Magnifier
            ref='magnifier'
            store={store}
            markers={markers}
            width={this.itemWidth}
            height={this.itemHeight}
            container={{width, height}}
            parent={this.refs.root}
            isBNS={isBNS}
            finIn={Boolean(true)}
          /> : null}
        {lines.map((line, index) => {
          const lineConst = `line-${index}-${mode}`;
          // 8 in LazyLoad it's not a magic number - it's negative offset from thumbnail check in .thumbs > div
          return (
            <LazyLoad
              height={(this.itemHeight + THUMB_OFFSET + 8)}
              key={lineConst}
              triggerOnce
              // debounce={0}
              // overflow
            >
              <div
                ref={lineConst}
                style={{height: (this.itemHeight + 5)}}
              >
                {line}
              </div>
            </LazyLoad>
          );
        })}
        <div>
          {this.renderSidebarNavigationShortcuts()}
        </div>
      </div>
    );
  }
}

MosaicView.defaultProps = {
  isEmpty: false,
  isBNS: {
    active: false
  }
};

MosaicView.propTypes = {
  activeTab: PropTypes.string,
  height: PropTypes.number.isRequired,
  markers: PropTypes.array,
  store: PropTypes.object.isRequired,
  isBNS: PropTypes.object,
  documentWithError: PropTypes.bool,
  isEmpty: PropTypes.bool,
  funnelSettings: PropTypes.object.isRequired,
  width: PropTypes.number.isRequired,
  container: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number
  }),
  scrollNext: PropTypes.func.isRequired,
  scrollPrev: PropTypes.func.isRequired,
  hotKeysActive: PropTypes.bool,
  enableThumbnailBar: PropTypes.bool
};

export default MosaicView;
